<!--12栅格布局
  xs: <768px;
  sm: ≥768px;
  md: ≥992px;
  lg: ≥1200px;
  xl: ≥1920px
-->
<template>
  <div class="ap-col" :class="[!!apSm?aa:'', !!apMd?bb:'', !!apLg?cc:'', !!apXl?dd:'', !!isShadow?'box-shadow':'']">
    <slot></slot>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        isShadow: false
      }
    },
    props: {
      apSm: {
        type: Number,
        default: null
      },
      apMd: {
        type: Number,
        default: null
      },
      apLg: {
        type: Number,
        default: null
      },
      apXl: {
        type: Number,
        default: null
      },
      // 效果
      shadow: {
        type: String,
        default: 'never'
      }
    },
    computed: {
      aa() {
        return `ap-sm-${this.apSm}`
      },
      bb() {
        return `ap-md-${this.apMd}`
      },
      cc() {
        return `ap-lg-${this.apLg}`
      },
      dd() {
        return `ap-xl-${this.apXl}`
      }
    },
    created() {
      if (this.shadow == 'always') {
        this.isShadow = true
      }
    }
  }
</script>

<style scoped lang="scss">
  @import './../../../css/common';

  .box-shadow {
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, .1);
  }
  .ap-col {
    padding: 10px 0;
    float: left;
    box-sizing: border-box;
    overflow: hidden;
  }
</style>


