<template>
  <div>
    <div class="wrap">
      <div class="title">
        <p><span>流程进度</span><span>（江安河建造水坝项目）</span></p>
      </div>
      <div class="steps">
        <div class="step" v-for="(item, index) in steps" :key="index">
          <h1 :class="{textE98: item.state == '完成', textRed: index == 2 && state == 2, textBlack: index == 2 && state == 3}">{{item.state}}</h1>
          <h2 v-if="item.name">{{item.name}}</h2>
          <span v-if="item.time">{{item.time}}</span>
          <el-button type="primary" v-if="item.state == '已拨款'">确认收款</el-button>
        </div>
      </div>
      <div class="explain" v-if="state == 2">
        <p>请补充完善2018年3月份的XX工程款项相关增值税发票附件。</p>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {}
    },
    props: {
      state: {
        type: Number,
        default: 2
      }
    },
    methods: {
      init() {
        if (this.state == 1) {
          this.steps = [{
              state: '发起申请',
              name: '代用名',
              time: '2016-12-12'
            },
            {
              state: '审批通过',
              name: '代用名',
              time: '2016-12-12'
            },
            {
              state: '申请被驳回',
              name: '代用名',
              time: '2016-12-12'
            },
            {
              state: '申请通过，待拨款',
              name: '代用名',
              time: '2016-12-12'
            },
            {
              state: '已拨款',
              name: null,
              time: null
            },
            {
              state: '完成',
              name: null,
              time: null
            }
          ]
        }
        if (this.state == 2) {
          this.steps = [{
              state: '发起申请',
              name: '代用名',
              time: '2016-12-12'
            },
            {
              state: '审批通过',
              name: '代用名',
              time: '2016-12-12'
            },
            {
              state: '申请被驳回',
              name: '代用名',
              time: '2016-12-12'
            },
            {}
          ]
        }
        if (this.state == 3) {
          this.steps = [{
              state: '发起申请',
              name: '代用名',
              time: '2016-12-12'
            },
            {
              state: '审批通过',
              name: '代用名',
              time: '2016-12-12'
            },
            {
              state: '申请待审批',
              name: '代用名',
              time: '催一下'
            },
            {}
          ]
        }
      }
    },
    created() {
      this.init()
    }
  }

</script>
<style scoped lang="scss">
  .wrap {
    position: relative;
    background-color: #fff;
    border-radius: 2px;
    margin: 20px;

    .title {
      padding: 20px 30px;
      font-size: 16px;
      color: rgba(0, 0, 0, 0.85);
      border-bottom: 1px solid #E9E9E9;
      font-weight: 600;

      span:last-child {
        font-size: 14px;
        color: rgba(0, 0, 0, 0.45);
      }
    }

    .steps {
      padding: 35px;
      position: relative;
      display: flex;

      .step {
        width: 160px;
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;

        h1 {
          padding: 25px 0;
          font-size: 16px;
          color: rgba(0, 0, 0, 0.65);
          padding-bottom: 15px
        }

        .textE98 {
          color: rgba(0, 0, 0, 0.45);
        }

        .textRed {
          color: rgba(245, 34, 45, 0.85);
        }

        .textBlack {
          color: rgba(0, 0, 0, 0.85);
        }

        h2 {
          font-size: 14px;
          color: rgba(0, 0, 0, 0.45);
        }

        span {
          padding: 13px 0;
          font-size: 14px;
          color: rgba(0, 0, 0, 0.45);
        }

        &::after {
          content: "";
          display: block;
          width: 8px;
          height: 8px;
          background-color: #1890FF;
          border: 3px solid #FFF;
          border-radius: 50%;
          position: absolute;
          top: -6px;
          z-index: 2;
        }

        &::before {
          content: "";
          display: block;
          width: 100%;
          height: 2px;
          background-color: #1890FF;
          position: absolute;
          right: 50%;
        }

        &:first-child::before {
          display: none;
        }

        &:last-child::before {
          background-color: #E9E9E9;
        }

        &:last-child::after {
          background-color: #E9E9E9;
        }
      }
    }

    .explain {
      width: 336px;
      height: 35px;
      padding: 20px;
      background: #FFFFFF;
      box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.15);
      border-radius: 4px;
      position: absolute;
      top: 0;
      left: 393px;

      p {
        font-size: 14px;
        color: rgba(0, 0, 0, 0.65);
        line-height: 22px;
      }

      &::after {
        content: "";
        width: 15px;
        height: 15px;
        background-color: #fff;
        // border-right: 10px solid transparent;
        // border-left: 10px solid transparent;
        // border-top: 10px solid #fff;
        box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.15);
        transform: rotate(317deg);
        position: absolute;
        // bottom: -10px;
        bottom: -7px;
        left: 30px;
      }
    }
  }

</style>



