export default {
    state:{
    	headShow:true,
    	sideShow:true,
    	height:document.body.offsetHeight,
    	width:document.body.offsetWidth
    },
    mutations:{//单一事件处理器 （同步）
    },
    actions:{//多事件处理机（异步）
    },
    getters:{//监听器
    	bodyWidth:function(state){
    		return state.width
    	},
    	bodyHeight:function(state){
    		return state.height
    	},
       	
       	
    },
}



