<!--vue组件
    回调函数： updataList（默认项被取消的， 默认项， 新增项）
-->
<template>
  <div id="cerManageSelect" v-if="isCerManageSelect">
    <div class="cerManageSelect_cant">
      <div class="cms_batch">
        <span v-if="isShowNO == 1">列表</span>
        <span v-if="isShowNO == 2">人员证书借用</span>
        <div class="cms_batch_right" v-if="isShowNO == 1">
          <input placeholder="搜索证书内容" v-model="value" size="small" style="width:200px;" @input="getInitData"  @click="search" />
        </div>
      </div>
      <div class="cms_table" v-if="isShowNO == 1">
        <div class="ap-table">
          <div class="table-header">
            <span class="check_box" :class="{checked: ifCheckAll}" @click="selectAllItem">
              <i class="iconfont icon-finish"></i>
            </span>
            <p style="width: 10%">序号</p>
            <p style="width: 20%">姓名</p>
            <p style="width: 20%">性别</p>
            <p style="width: 15%">年龄</p>
            <p style="width: 15%">班组</p>
            <p style="width: 10%">工种</p>
            <p style="width: 10%">单位名称</p>
          </div>
          <div class="table-list" v-for="(list, index) of tableData" :key="index">
            <span class="check_box" :class="{checked: list.ifCheck==1, ban: list.ifCheck == -2}" @click="selectItem(list, index, tableData)">
              <i class="iconfont icon-finish"></i>
            </span>
            <p style="width: 10%">{{index + 1}}</p>
            <p style="width: 20%">{{list.text}}</p>
            <p :title="list.companyCredentialContent" style="width: 20%">{{list.companyCredentialContent | substring10}}</p>
            <p style="width: 15%">{{list.expiryDate}}</p>
            <p style="width: 15%">{{list.credentialUnit}}</p>
            <p style="width: 10%">{{list.place}}</p>
            <p style="width: 10%">
              <span v-if="list.dispositon == 1">在册</span>
              <span v-if="list.dispositon == 2">借出</span>
              <span v-if="list.dispositon == 3">锁定</span>
            </p>
          </div>
        </div>
      </div>
      <div class="cms_table" v-if="isShowNO == 2">
        <div class="ap-table">
          <div class="table-header">
            <span class="check_box" :class="{checked: ifCheckAll2}" @click="selectAllItem2">
