            <p style="width: 17%">证书类型</p>
            <p style="width: 10%">有效期</p>
            <p style="width: 15%">身份证号</p>
            <p style="width: 10%">发证单位</p>
            <p style="width: 10%">社保缴纳月数</p>
            <p style="width: 10%">存放点</p>
            <p style="width: 5%">去向</p>
