      width: 14px;
      height: 14px;
      cursor: pointer;
      text-align: center;
      line-height: 12px;
      // transition: all 300ms;
      box-sizing: border-box;
      margin-left: 10px;
    }

    .check_box i {
      display: inline-block;
      font-size: 12px !important;
      color: #333;
      transform: scale(0);
    }

    .checked {
      background-color: #409eff;
    }

    .checked i {
      color: #fff;
      transform: scale(1);
      transition: all 0.3s;
    }

    .ban {
      background-color: #edf2fc;
      border-color: #dcdfe6;
    }

    .ban i {
      color: #c0c4cc;
      transform: scale(1);
    }
    p {
      width: 15%;
      font-size: 14px;
      color: #333;
      text-align: center;
      padding: 15px 6px;
      box-sizing: border-box;
      span {
        display: block;
        font-size: 14px;
        padding: 7px;
        border-bottom: 1px solid #333;
        &:last-child {
          border-bottom: 0
        }
      }
    }
  }
  .table-list:hover {
    background-color: rgba(245, 247, 250, 1)
  }
}
</style>




