<template>
  <div>
    <span>签字盖章</span>
    <span @click="clickfile">{{imgName}}</span>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        imgName: '',
        url: ''
      }
    },
    created() {
      this.getImage()
    },
    methods: {
      getImage() {
        setTimeout(() => {
          this.imgName = '签署意见图片'
          this.url = './qianzhiGZ/df.png'
        }, 2000)
      },
      clickfile() {
        this.$pdfFile.install(this.fileId, 1)
      }
    }
  }

</script>

<style>

</style>



