export default {
  apDirective(Vue) {
    Vue.directive('isRollShow', {
        bind: (el, binding) => {
          document.addEventListener('scroll', () => {
            let visibleTop = window.scrollY;
            let visibleBottom = window.scrollY + document.documentElement.clientHeight;
            let centerY = el.offsetTop + (el.offsetHeight / 2);
    
            if (centerY < visibleBottom && visibleTop < centerY) {
              el.classList.add("isShow")
            } else {
              el.classList.remove("isShow")
            }
          }, false)
        }
    });
  }
}



