<template>
  <div>
    <router-link to="/" tag="button" class="to-home">返回UI页</router-link>
    <h1>登录,注册页</h1>
    <button class="to-home" @click="toRouter">进入主页 定位到树形联动</button>
    <router-link to="/page1" tag="button" class="to-home">进入历史页面</router-link>
      <div id="print_box">
        <div class="yangs">
          <!-- <input type="text" v-model="saveName">
          <keep-alive>
            <p>姓名： {{saveName}}</p>
          </keep-alive> -->
          <p v-for="(k, i) in 2" :key="i" @click="currt(k)">{{k}}</p>
          <keep-alive include="xm2">
            <component :is="current"></component>
          </keep-alive>
        </div>
        <div>
          <span>电话：</span>
          <img src="" alt="">
          <input type="text">
        </div>
        <!-- <img src="./photo2.png" alt=""> -->
      </div>
      <button @click="toDoPrint('print_box')">iframe打印</button>
    <menua></menua>
    
    <button @click="tobiaoge">表格</button>
    <table2 ref="table2" @updataList="updataList"></table2>
  </div>
</template>

<script>
  import xm1 from './xm1'
  import xm2 from './xm2'
  import menua from './test/index'
  import { VueCrop } from '../../template/index'
  import Vue from 'vue'
  import print from './print'
  import table2 from '../../../xp/components/table2'
  Vue.use(VueCrop)
  export default {
    data() {
      return {
        current: 'xm1',
        saveName: ''
      }
    },
    
    created() {

    },
    components: {
      menua,
      print,
      table2,
      xm1,
      xm2
    },
    methods: {
      currt(k) {
        this.current = 'xm' + k
      },
      toRouter() {
        let path = 'index/projectsubject/administrative/projectDistribution'
        this.$store.state.page.HomeRoute=path // 控制台跳转路劲
        this.$router.push({
          name: 'projectDistribution'
        })
      },
      tobiaoge() {
        this.$refs.table2.init(1,1)
      },
      updataList(res1, res2, res3) {
        console.log(res1, res2, res3, '返回数据')
      },
      toDoPrint(id) {
        this.doPrint(id)
      },
      // =========================================================================
      doPrint(id_str){ //id-str 打印区域的id
        var el = document.getElementById(id_str);
        var iframe = document.createElement('IFRAME');
        var doc = null;
        iframe.setAttribute('style', 'position:absolute;width:0px;height:0px;left:-500px;top:-500px;');
        document.body.appendChild(iframe);
        doc = iframe.contentWindow.document;
        doc.head.innerHTML = ''
        doc.head.innerHTML += "<meta charset='utf-8'>"
        doc.head.innerHTML += "<meta name='viewport' content='width=device-width, initial-scale=1.0'>"
        doc.head.innerHTML += "<meta http-equiv='X-UA-Compatible' content='ie=edge'>"
        doc.head.innerHTML += "<title>Document</title>"
        doc.head.innerHTML += "<link rel='stylesheet' media='print' href='./print.css'>"
        doc.body.innerHTML = el.innerHTML
        
        // doc.close();
        iframe.contentWindow.focus();
        iframe.contentWindow.print();
        if (navigator.userAgent.indexOf("MSIE") > 0)
        {
            document.body.removeChild(iframe);
        }
      },
      
    }
  }

</script>

<style lang="scss">
  #print_box {
    display: flex;
    div {
      color: blue;
      input {
        outline: none;
        border: 1px solid #333
      }
    }
  }
  .yangs {
    color: red
  }
</style>



