<template>
    <ul>
    	<li v-for="k,i in data" class="li_padding" :key='k.privilegeName' :class="defaul(k)">
    		<div class="text_icon">
    			<i class="iconfont icon-xiangxia" :style="{opacity:k.nodes?'':'0'}"  
    				:class="k.open==true?'open_xiangxia':''"
    				@click="openTree(k)" >
    			</i>
	    		<b class="chenk_box" @click="chenck(k)" :class="[k.ifPrivilege==true?'pitch':'',k.privilegeId==5||k.privilegeId==6?'unableSelect':'']" 
	    			>
	    			<i class="iconfont icon-finish" v-if="(k.ifPrivilege||k.privilegeId==5||k.privilegeId==6)==true"></i>
	    		</b>
	    		<span class="chenck_name">
	    			{{k.privilegeName}}
	    		</span>
    		</div>
    		
    		
    		
    		<tree :data="k.nodes" v-if="k.open" @permissions="permissions" :upt="upt"></tree>
    	</li>
    </ul>
</template>
<script>
	
export default {
	name:'tree',
  data(){
    return{
    	child:[]
    	
    }
  },
	
  props:['data','upt'],
  created(){
  },
  methods:{
  	defaul(k){
  		if(this.upt.indexOf(k.privilegeId)!=-1){
  			k.ifPrivilege=true
  		}else{
  			k.ifPrivilege=false
  		}
  	},
  	openTree(k){//是否打开树
  		k.open=!k.open
  		this.$forceUpdate()
  	},
  	chenck(k){
  		this.child=[]
  		if(k.privilegeId!=5&&k.privilegeId!=6){
  			k.ifPrivilege=!k.ifPrivilege
	  		if(k.ifPrivilege==true){
	  			let arr=[];
	  				arr.push(k.privilegeId)
	  				
				this.$emit('permissions',arr,'add')
	  		}else{
	  			let arr=[];
	  				arr.push(k.privilegeId)
	  				
	  				this.removeArrData(k.nodes)
	  				
				this.$emit('permissions',arr,'remove',k.privilegeId,this.child)
	  		}
  		}
  	},
  	removeArrData(arr){
  		if(arr!=null){
			for (var i in arr) {
				this.child.push(arr[i].privilegeId)
				
				this.removeArrData(arr[i].nodes)
			}
  		}
	
  	},
  	permissions(arr,sta,rid,removeChild){
  		for (var i in this.data) {
  			for (var j in this.data[i].nodes) {
  				if(this.data[i].nodes[j].privilegeId==arr[arr.length-1]){
  					if(sta=='add'){
  						this.data[i].ifPrivilege=true
  					}
  					arr.push(this.data[i].privilegeId)
					this.$emit('permissions',arr,sta,rid,removeChild)
  				}
  			}
  		}
  	}
  }
}
</script>
<style >
ul {
    list-style: none;
}
	.adjustpowerdiv .text_icon{
		width: inherit;
		height: 30px;
		line-height: 28px;
	}
	.adjustpowerdiv .li_padding{
 		padding-left: 30px;
 		line-height: 30px;
 	}
	/*向下*/
 	.adjustpowerdiv .icon-xiangxia{
 		font-size: 14px;
 		transform: rotateZ(-90deg)translateX(0px);
 		display: inline-block;
 		cursor: pointer;
 		color: #666;
 		float: left;
 		transition: all 300ms;
 		margin-right: 5px;
 	}
 	.adjustpowerdiv .open_xiangxia{
 		transform: rotateZ(0deg)translateY(2px)translateX(-3px);
 	}
 	/*选中*/
 	.chenk_box{
 		border:1px solid #0092DD ;
 		display: inline-block;
 		width: 18px;
 		height: 18px;
 		cursor: pointer;
 		text-align: center;
 		line-height: 18px;
 		transition: all 300ms;
 		float: left;
 		box-sizing: border-box;
 		margin-top: 5px;
 		margin-right: 10px;
 	}
 	.chenk_box i{
 		font-size: 13px!important;
 	}
 	.pitch{
 		background: #0092DD;
 		color: #fff;
 	}
 	/*名字*/
 	.chenck_name{
 		font-size: 16px;
 		font-family: "微软雅黑";
 		float: left;
 	}
 	/*无法勾选*/
 	.unableSelect{
 		background: #999!important;
 		border: 1px solid #666;
 	}
 	.unableSelect i{
 		color: #eee!important;
 	}
</style>




