<!--备注：
当前板块索引数组：@currentHighlightArr: [父， 子]
返回节点信息：eleNodeArr：[]
eleNodeArray: null // 注： 此时定义需为null，不能为 [], 赋值类型为HTMLCollection
距离顶部高度： topHeight：（Number）
裁剪数组： cutArr： [] 注：子项为一项时  需传1 隐藏
-->
<template>
  <div class="news-content" v-roll="userMethods">
    <slot></slot>
  </div>
</template>

<script>
import _ from 'lodash'
import _aU from '../../js/utils'
  export default {
    data() {
      return {
        currentHighlight: [0, 0],
        heightArr: [],
        visibleTop: 0
      }
    },
    props: {
      topHeight: {
        type: Number,
        default: 300
      },
      cutArr: {
        type: Array,
        default() {
          return [3,2]
        }
      }
    },
    methods: {
      userMethods() {
        this.visibleTop = window.scrollY;
        // if(visibleTop < 1157) {
        //   this.currentHighlight = [0, 0]
        // }
        // if(visibleTop >= 1157 && visibleTop < 1754) {
        //   this.currentHighlight = [0, 1]
        // }
        // if(visibleTop >= 1754 && visibleTop < 1924) {
        //   this.currentHighlight = [0, 2]
        // }
        // if(visibleTop >= 1924 && visibleTop < 2200) {
        //   this.currentHighlight = [1, 0]
        // }
        for(let k in this.heightArr) {
          for(let i=0; i < this.heightArr[k].length; i++) {
            let t = k,
                p = i + 1
            if(i == this.heightArr[k].length) {
              t = k + 1
              // 取后一板块的第一项
              p = 0
            }

            if(this.visibleTop >= this.heightArr[k][i] && this.visibleTop < (this.heightArr[t][p] || 100000)) { //最后一项时 取绝对大值100000
              this.currentHighlight = [k, i]
            }
            // 直接特殊处理第一项
            if(this.visibleTop < this.heightArr[0][1]) {
              this.currentHighlight = [0, 0]
            }
          }
        }
        // let aa = [[300, 1157, 1754], [1924, 2577]]
        this.$emit('currentHighlightArr', this.currentHighlight)
      }
    },
    mounted() {
      
      let ele = document.getElementsByClassName('news-content')[0].children
      for(let i=0; i < ele.length; i++) {
        ele[i].classList.add('sec')
      }
      this.$emit('eleNodeArr', ele)

      let itemH = document.getElementsByClassName('sec')
      for(let k= 0; k < itemH.length; k++) {
        this.heightArr.push(this.topHeight + itemH[k].offsetTop)
      }
      // this.heightArr = _.chunk(this.heightArr, 3)
      this.heightArr = _aU.chunkArr(this.heightArr, this.cutArr)
      console.log(this.heightArr, '拆分高度数组')
    }
  }
</script>

<style scoped lang="scss">
  .news-content {
    padding-left: 220px;
    .sec {
      width: 1200px;
      background-color: #6AD7E9;

      .news-item {
        height: 260px;
      }
    }
  }
</style>


