<template>
  <div>
    <span>树形联动</span>
    <div id="entPer">
      <!-- 左边架构 -->
      <div class="left">
        <div class="treesearch">
          <div>
            <p class="treesearch-p">
              <input type="text" placeholder="搜索人员电话或名称">
              <i class="iconfont icon-icon_search1 searchbtn"></i>
            </p>
          </div>
        </div>
        <div class="treeadjust">
          <tree :there="there" :num="1" @dataUpt="dataUpt"
            :checkdepartid="21"
            @selectId="selectId" @openFather="openFather"></tree>
        </div>
      </div>
      <div class="right">
        <!-- 部门详情 -->
        <p>部门名称</p>
        <div class="details-lowerdepartmentul-div">
          <ul class="details-lowerdepartmentul" v-if="department.departmentullist">
            <li class="details-lowerdepartmentul-li" v-for="(item,index) in department.departmentullist" :key="index" @click="looklowerdepartment(item)">
              <span>{{item.departmentName}}</span>
              <i class="iconfont icon-icon_fold"></i>
            </li>      
          </ul>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
  import {treeJson} from './tree/tree-json'
  import tree from './tree/tree'
  export default {
    data() {
      return {
        there: {},
        department: {
          departmentullist: null  
        }
      }
    },
    components: {
      tree
    },
    mounted() {
      this.there = treeJson
      console.log(this.there, '父级')
    },
    methods: {
      openFather() {

      },
      dataUpt() {

      },
      selectId() {

      }
    }
  }


</script>
<style lang="scss">
#entPer {
  display: flex;
  ul {
    padding: 0
  }
  .left {
    width: 48%
  }
  .right {

  }
}
</style>



