const CURRENT_TABS = 'current_tabs'
const BORROW_DETAILS = 'borrow_details'
const state = {
  // 变量缓存管理
  personName: '',
  tabsIndex: [0, 0, 0], // 多级标签返回当前记录,
  borrowDetails: {
    status: 1,
    table1: [],
    table2: []
  }
}
const getters = {
  tabsIndex: (state) => state.tabsIndex,
  borrowDetails: (state) => state.borrowDetails,
}
const mutations = {
  [CURRENT_TABS](state, parame) {
    state.tabsIndex = parame
  },
  saveName(state, personName) {
    this.state.personName = personName
  },
  [BORROW_DETAILS](state, parame) {
    if(parame instanceof Array) {
      if(parame[1] == 1) {
        for(let k=0;k<parame[0].length;k++) {
          state.borrowDetails.table1.push(parame[0][k])
        }
      } else if(parame[1] == 2){
        for(let k=0;k<parame[0].length;k++) {
          state.borrowDetails.table2.push(parame[0][k])
        }
      } else {
        state.borrowDetails.table1 = []
        state.borrowDetails.table2 = []
      }
    } else { // 直接进入 情况时 进行赋值操作
      if(!parame.hasOwnProperty('table1')) {
        parame.table1 = []
        parame.table2 = []
      }
      state.borrowDetails = parame
      
    }
  }
}
const actions = {
  currentTabs({commit}, parame) {
    commit(CURRENT_TABS, parame)
  },
  borrowDetailsMessage({commit}, parame) {
    commit(BORROW_DETAILS, parame)
  },
}

export default {
  state,
  getters,
  mutations,
  actions
}


