<template>
  <div>
    <!-- <ul>
      <transition-group name="flip-list" class="vegroup">
        <li class="menuli" v-for="(item, index) in menuArr" :key="item.onlyid" draggable="true"
          @dragstart="dragstart(index, item, $event)" 
          @dragenter="dragenter(index, item)" 
          @dragend="dragend(index, item)">
          <span class="liname">{{item.name}}</span>
          <tree :menuArr="item.children" v-if=" 'children' in item"></tree>
        </li>
      </transition-group>
    </ul> -->
    <div class="tab">
      <ul class="tab-header">
        <li class="check-box">
          <span></span>
        </li>
        <li v-for="(item, index) in tableTitle" :key="index">
          {{item}}
        </li>
      </ul>
      <ul class="tab-content">
        <li v-for="(item, index) in tableList" :key="item.id" draggable="true" 
        @dragstart="dragstart(index, item)" 
        @dragenter="dragenter(index, item)" 
        @dragend="dragend(index, item)">
          <span></span>
          <span>{{item.name}}</span>
          <span>{{item.age}}</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'tree',
    data() {
      return {
        oldNum: 0,
        newNum: 0,
        oldItem: {},
        tableList: [],
        tableTitle: ['职位名称', '人员数', '操作']
      }
    },
    mounted() {
      this.tableList = [{
        name: '姓名1',
        age: 18,
        id: 1
      }, {
        name: '姓名2',
        age: 19,
        id: 2
      }, {
        name: '姓名3',
        age: 20,
        id: 3
      }]
    },
    directives: {
      trak: {
        inserted(e) {
          el.draggable = "true"
          // el.ondragstart = () => {
          //     target = {
          //         innerHtml = _.cloneDeep(el.innerHtml),
          //         element: el
          //     }
          // }
          // el.ondragover = e => {
          //     if(e.prenent) {
          //         e.preventDefault()
          //     }
          // }
          // el.ondrop = () => {
          //     if(!_.isEmpty(target)) {
          //         target.element.innerHtml = el.innerHtml
          //         el.innerHtml = target.innerHtml
          //     }
          // }
        }
      }
    },
    props: {
      menuArr: {
        type: Array,
        default: () => []
      }
    },
    methods: {
      dragstart(index, item, e) {
        // console.log(index, '开始')
        this.oldNum = index
      },
      // 做最终操作
      dragend(onlyid) {
        if (this.oldNum != this.newNum) {
          this.oldItem = this.tableList[this.oldNum]; // 只需要获取所添加的新的数据
          this.tableList.splice(this.oldNum, 1);
          this.tableList.splice(this.newNum, 0, this.oldItem);
          // console.log(this.tableList, 'id结构')
          // this.handleTree(this.tableList)
        }
      },
      // 保存拖动操作
      // 记录移动过程中信息
      dragenter(index) {
        // console.log(index, '到达')
        this.newNum = index
      },
      handleTree(data) {
        for(let k in data) {
          if(data[k].onlyid == 322) {
            return data[k].name
          }
          if('children' in data[k]) {
            return this.handleTree(data.children)
          }
        }
      }
    }
  }

</script>

<style scoped lang="scss">
    .flip-list-move {
        transition: transform 1s;
    }
    .tab {
      ul {
        list-style-type: none;
        padding: 0;
        li {
          border-bottom: 1px solid #EBEEF5;
        }
      }
      .tab-header {
        display: flex;
        justify-content: space-between;
        height: 48px;
        line-height: 48px;
        background-color: rgb(250, 250, 250);
        .check-box {
          width: 120px;
          padding: 12px 0;
          span {
            display: inline-block;
            width: 22px;
            height: 22px;
            border: 1px solid red
          }
        }
        li {
          text-align: center;
          width: calc((100% - 120px) / 3);
          color: rgba(0, 0, 0, 0.85);
          font-size: 14px;
        }
      }
      .tab-content {
        li {
          text-align: center
        }
      }
    }
</style>



