<template>
  <ul >
    <transition-group name="flip-list">
      <li v-for="(k,i) in there" :key="k.departmentId" 
    :class="openF(k)" 
    draggable="true" 
    @dragstart="dragstart(i, k, k.departmentId, $event)" 
    @dragenter="dragenter(i, k, k.departmentId, $event)" 
    @dragend="dragend(i, k, k.departmentId, $event)">
      <div :style="{'padding-left':`${num * 10}px`,'padding-right':`${10}px`}"  @click="open(k)" v-if="k.ifJob==false" class="icontext" :class="{'backggg':checkdepartid==k.departmentId,'class2':fool(k.departmentId,k)}">
        <div class="icontextdiv">
          <i class="iconfont icon-icon_tree" v-if="num==1"></i><i class="iconfont icon-icon_folder" v-if="num>=2"></i><span :class="num==1?'text':''">{{k.departmentName}}（{{k.personNum || 0}}）人</span>
        </div>
        <i class="iconfont icon-icon_fold" v-if="k.nodes!=null" :style="k.unfold?{transform:'rotate(180deg)'}:''"></i>
      </div>
      <tree :there="k.nodes" :num="num+1" v-show="k.unfold==true" @dataUpt="dataUpt" @updataRight="updataRight" :checkdepartid="checkdepartid"     @openFather="openFather"></tree>
    </li>
    </transition-group>
  </ul>  
</template>

<script >
export default {
  name:'tree',
  data(){
    return{
      partId: null, 
      oldNum: 0,
      newNum: 0,
      oldItem: {},
      newSortArr: []
    }
  },
  props:['there','num','checkdepartid'],
  created(){
    if(this.num==1){
      for(let i in this.there){
        this.there[i].unfold=true;
      }
    }
    
  },
  computed: {
    indent() {
        return {
          padding: '10px',
          'padding-left': `${this.depth * 20}px`
        }
      },
  },
  methods:{
    // 拖拽
    cleanUpDefaultEvent(event) {
      event = event || window.event || arguments.callee.caller.arguments[0];
      if (event.preventDefault) {//IE not have
          event.preventDefault();
      }
      event.returnValue = false;
    },
    dragstart(index, item, id, e) {
      e.dataTransfer.setData('aa', null)
      // this.cleanUpDefaultEvent(e)
        this.oldNum = index
    },
    dragend(index, item, id, e) {
      // this.cleanUpDefaultEvent(e)
      this.newSortArr = []
      if (this.oldNum != this.newNum) {
        this.oldItem = this.there[this.oldNum];
        this.there.splice(this.oldNum, 1);
        this.there.splice(this.newNum, 0, this.oldItem);
        // let fatArr = [{
        //   departmentId: 1795,
        //   parentDepartmentId: 236,
        //   nodes: []
        // }]
        // this.there.forEach(item => {
        //   fatArr[0].nodes.push(item)
        // })
        // this.getSortData(fatArr)
        this.getSortData(this.there)
        let paramsArr = []
        this.newSortArr.filter(item => {
          if(item.parentDepartmentId == this.oldItem.parentDepartmentId) {
            paramsArr.push(item.departmentId)
          }
        })
        let paramsObj = {}
        paramsArr.forEach((item, index, arr) => {
          paramsObj[item] = index
        })
        console.log(paramsObj, 'paramsObj')
        // let token = this.$user.get_data('userData').token;
        // let params = new URLSearchParams();
        // params.append("Body", JSON.stringify(paramsObj));
        // this.axios({
        //   method: "POST",
        //   headers: { 'content-type': 'application/json;charset=utf-8' },
        //   url: '/api/organization/change/order?token=' + token,
        //   data: JSON.stringify(paramsObj),
        // })
        // .then((res) => {
        //   if(res.data.code==1){            
        //     this.$message.success('成功')
        //     this.$emit('updataRight');
        //   } else {
        //     this.$message.error(res.data.errorMessage)
        //   }
        // })
      }
    },
    dragenter(index, item, id, e) {
      // this.cleanUpDefaultEvent(e)
      this.newNum = index
    },
    getSortData(data) {
      for(var k = 0; k < data.length; k++) {
        if(!data[k].ifJob) {
          this.newSortArr.push({
            departmentId: data[k].departmentId,
            parentDepartmentId: data[k].parentDepartmentId
          })
        }
        // if(data[k].nodes) {
        //   this.getSortData(data[k].nodes)
        // }
      }
    },
    // ==========================================
    openF(k){
      
      if(k.departmentId==this.checkdepartid){
        this.$emit('openFather',k.departmentId,k)
      }
    },
    openFather(sId,item){
      for(var i in this.there){
        for(var j in this.there[i].nodes){
          if(this.there[i].nodes[j].departmentId==sId){
            this.there[i].unfold=true;
            this.$emit('openFather',this.there[i].departmentId,item)
            this.$forceUpdate()
          }
        }
      }      
    },
    updataRight() {
      this.$forceUpdate()
      this.$emit('updataRight');
    },
  	dataUpt(k){
      this.$emit('dataUpt',k);
  	},
    open(k){
      this.$emit('dataUpt',k);
      if(this.num>1){
        k.unfold=!k.unfold;
      }
    	this.$forceUpdate()
    },
    fool(id,k){
      if(id){

      }
      // if(id==this.ids){
      //    k.unfold=!k.unfold;
      //    this.$emit('dataUpt')
      //    return true
      // }
    },
  }
}
</script>

<style scoped>
ul>li{
  position: relative;
}
.icontext{  
  font-size: 14px;
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
  height: 40px;
  line-height: 40px;
  /* width: 100%; */
  cursor: pointer;
 
}
.icontext>div.icontextdiv{
  width: 100%;
   overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.icontext>div.icontextdiv>i:first-child{
  margin-right: 8px;
}
.text{
  font-size: 16px;
  /* color: rgba(0,0,0,0.85); */
}
.backggg{
  background: #1A8CFF;
  color: #fff;
}
.textcolor{
  color: #fff;
}
.class2{
  background: red;
}
.flip-list-move {
    transition: transform 1s;
  }
</style>




