'use strict'
//设置打包及端口配置
	const port=20;
	//[10:公网打包,11:测试打包,20:测试联调,21:向志锐联调,22:朴索联调,23:余佳音,24:李发涛,30:外网数据]
	
//设立数据导出对象
	var dataSet={
			isPack:false,//是不是打包（true为打包状态）
			webpack:'',
			main:'',
		};
	
//现有ip及接口地址整合
	var address={
		//打包
		pack_A:'http://jianyitong.com',//正式环境
		pack_B:'http://test.jianyitong.com',//测试环境
		
		//联调
		join_main:'/testfile',
		join_A:'http://test.jianyitong.com',//测试服务器
		join_B:'http://192.168.8.73:80',//向志锐
		join_C:'http://192.168.8.113',//朴索
		// join_C:'http://192.168.8.74:80',
		join_D:'http://192.168.50.83:8080',//余佳音
		join_E:'http://192.168.8.91',//李发涛
		
		//webpack配置
		HOST_A:'0.0.0.0',
		HOST_B:'http://192.168.8.72',
		HOST_C:'http://test.jianyitong.com', 
	}



//调制导出路径集合
	let num=port.toString();
	if(num.slice(0,1)==1){
		if(num.slice(1,2)==0){
			dataSet.webpack=address.HOST_A;
			dataSet.main=address.pack_A;
		}else if(num.slice(1,2)==1){
			dataSet.webpack=address.HOST_A;
			dataSet.main=address.pack_B;
		}
		dataSet.isPack=true 
	}
	if(num.slice(0,1)==2){
		if(num.slice(1,2)==0){
			dataSet.webpack=address.HOST_C
		}
		if(num.slice(1,2)==1){
			dataSet.webpack=address.join_B
		}
		if(num.slice(1,2)==2){
			dataSet.webpack=address.join_C
			
		}
		if(num.slice(1,2)==3){
			dataSet.webpack=address.join_D
			
		}
		if(num.slice(1,2)==4){
			dataSet.webpack=address.join_E
			
		}
		dataSet.main=address.join_main;//main.js接口配置
		dataSet.isPack=false;
	}


	module.exports=dataSet;


