<!--/* 
  调用组件是所需要的参数及方法案例
  <roster  ref="initRoster" @checkedVal="checkedVal"></roster>
  this.$refs.initRoster.apRoster(data)
 *data(对象) 参数 ：
  选择类型： typeChoose （String） radio: 单选， 默认复选
  禁用数组：disabledArr：[ueserId,ueserId] （Array）
  默认选中项：morenUserId： （Array）
  类型：type（企业：company, 项目：project, 项企：pAndC） （String）
  允许选择的最大项数值 ： allowMaxNum  （number） 默认： 不限
  点击确定的回调方法：  checkedVal(one, two) one选中项userName, two选中项userId
 */-->
<template>
  <div class="mask" v-if="isShow">
    <div class="wrap">
      <div class="search">
        <input type="text" placeholder="搜索" v-model="fuzzySearch" @input="toSearch">
      </div>
      <div class="nav">
        <span @click="current(1, null, $event)" :class="{'active': currentIndex == 1, 'isDisabled': type == 'company'}">全部</span>
        <span @click="current(2, null, $event)" :class="{'active': currentIndex == 2, 'isDisabled': type == 'project'}">企业</span>
        <span @click="current(4, 0, $event)" :class="{'active': currentIndex == 4, 'isDisabled': type == 'company' || userRole == -2 || userRole == 1 || userRole == 2 || userRole == 3 || userRole == 9}">建设</span>
        <span @click="current(3, 1, $event)" :class="{'active': currentIndex == 3, 'isDisabled': type == 'company' || userRole == -2 || userRole == 0 || userRole == 2 || userRole == 3 || userRole == 9}">设计</span>
        <span @click="current(5, 2, $event)" :class="{'active': currentIndex == 5, 'isDisabled': type == 'company' || userRole == -2 || userRole == 0 || userRole == 1 || userRole == 3 || userRole == 9}">施工</span>
        <span @click="current(6, 3, $event)" :class="{'active': currentIndex == 6, 'isDisabled': type == 'company' || userRole == -2 || userRole == 0 || userRole == 1 || userRole == 2 || userRole == 9}">监理</span>
        <span @click="current(7, 9, $event)" :class="{'active': currentIndex == 7, 'isDisabled': type == 'company' || userRole == -2 || userRole == 0 || userRole == 1 || userRole == 2 || userRole == 3}">行政</span>
      </div>
      <div class="main">
        <div class="item" v-for="(item, index) of user" :key="index">
          <h1>{{index}}</h1>
          <div v-for="(list, indexc) of item" :key="indexc">
            <span class="check_box" :class="{checked: list.ifCheck==1, ban: list.ifCheck == -2}" @click="checky(list, index, indexc)">
              <i class="iconfont icon-finish"></i>
            </span>
            <div class="details">
              <div class="text">
                <p>
                  <b>{{list.userName}}</b>
                  <span>{{list.mobile}}</span>
                </p>
                <p>
                  <i v-if="list.companyRoleType == 0">建</i>
                  <i v-else-if="list.companyRoleType == 2">施</i>
                  <i v-else-if="list.companyRoleType == 3">监</i>
                  <i v-else-if="list.companyRoleType == 9">行</i>
                  <i v-else-if="list.companyRoleType == 1">设</i>
                  <i v-else>企</i>
                  <!-- <span>
                      <span v-if="(isAll == 1 && type != 'company') || isAll == 2">{{list.companyDepartmentName || "无"}}</span>
                      <span style="padding: 0 7px" v-if="isAll == 1 && type != 'company'">/</span>
                      <span v-if="(isAll == 1 && type != 'company') || isAll == 3">{{list.projectDepartmentName || "无"}}</span>
                    </span> -->
                  <span v-if="isAll == 1" :name="item.userId">
                    <span>{{list.companyDepartmentName || "无"}}</span>&nbsp;&nbsp;/&nbsp;&nbsp;
                    <span>{{list.projectDepartmentName || "无"}}</span>
                  </span>
                  <span v-else-if="isAll == 2">
                    <span>{{list.companyDepartmentName || "无"}}</span>
                  </span>
                  <span v-else>
                    <span>{{list.projectDepartmentName || "无"}}</span>
                  </span>
                </p>
              </div>
            </div>
          </div>
        </div>
        <span v-if="!user">暂无人员信息</span>
      </div>
      <div class="footer">
        <div class="footer-cd1" :class="{'footer-cd-active': isShowMore, 'footer-cd': isShowMore1}">
          <span v-for="(item, index) in checkedAllMsg" :key="index">
            {{item.userName}}
            <i @click="deleteTag(item)">x</i>
          </span>
          <i @click="showMore" v-if="isShowMore2">{{caozuoText}}</i>
        </div>
        <div class="footer-cz">
          <p>已选中<span>{{choseNum}}</span>项</p>
          <div>
            <button @click="cancel">取 消</button>
            <button @click="sureChoice" type="primary">确 定</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      useraa: null,
      user: [],
      checked1: true,
      checkList: [],
      checkedId: [],
      data: null,
      num: 0,
      choseNum: 0,
      isDisable: false,
      currentIndex: 1,
      checkedName: [],
      checkedId: [],
      checkedAllMsg: [],
      fuzzySearch: '',
      chooseType: null,
      projectType: null,
      isShow: false,
      type: '',
      allowMaxNum: 999999,
      disabledArr: [],
      morenUserId: [],
      currentUserId: null,
      isDeleteCurrent: true,
      userRole: -3,
      // 效果
      isShowMore: false,
      isShowMore1: false,
      isShowMore2: false,
      caozuoText: '更多'
    }
  },
  computed: {
    //   isChecked() {
    //     return this.current == 1 ? true : false
    //   }
  },
  methods: {
    showMore() {
      this.isShowMore = !this.isShowMore
      if(this.caozuoText == '更多') {
        this.caozuoText =  'X'
      } else {
        this.caozuoText = '更多'
      }
    },
    checky(k, i, ic) {
      if (k.ifCheck == -2) {
        return
      }
      if (this.typeChoose == 'radio') {
        // 单选
        this.checkedName = []
        this.checkedId = []
        if (k.ifCheck == 1) {
          k.ifCheck = -1
        } else {
          k.ifCheck = 1
          // this.checkedName.push(k.userName)
          for (let t in this.user) {
            for (let k in this.user[t]) {
              if (
                this.user[t][k].ifCheck == 1 &&
                this.user[t][k] != this.user[i][ic]
              ) {
                this.user[t][k].ifCheck = -1
              }
              // 获取选中数据
              if (this.user[t][k] == this.user[i][ic]) {
                this.checkedName.push(this.user[t][k].userName)
                this.checkedId.push(this.user[t][k].userId)
              }
            }
          }
        }
        this.choseNum = this.checkedName.length
      } else {
        // 复选

        if (k.ifCheck == 1) {
          k.ifCheck = -1
          this.checkedName.splice(this.checkedName.indexOf(k.userName), 1)
          this.checkedId.splice(this.checkedId.indexOf(k.userId), 1)
          this.$emit('cbChecked', this.checkedName)

          for(let t in this.checkedAllMsg) {
            if(this.checkedAllMsg[t].userId == k.userId) {
              let t1 = Number(t)
              this.checkedAllMsg.splice(t1, 1)
            }
          }
        } else {
          k.ifCheck = 1
          this.checkedName.push(k.userName)
          this.checkedId.push(k.userId)
          // 全部信息
          if (this.checkedName.length <= this.allowMaxNum) {
            let all = {
              userName: k.userName,
              userId: k.userId
            }
            this.checkedAllMsg.push(all)
          }
          
        }

        if (this.checkedName.length > this.allowMaxNum) {
          k.ifCheck = -1
          this.checkedName.splice(this.checkedName.indexOf(k.userName), 1)
          this.checkedId.splice(this.checkedId.indexOf(k.userId), 1)

          this.$message.warning('最大允许选择' + this.allowMaxNum + '项')
        }
        this.choseNum = this.checkedName.length
        // 样式
        this.isShowMore1 = true
      }
      this.$forceUpdate()
    },
    deleteTag(item) {
      for(let t in this.checkedAllMsg) {
        if(this.checkedAllMsg[t].userId == item.userId) {
          let t1 = Number(t)
          this.checkedAllMsg.splice(t1, 1)
        }
      }
      // 同时取消勾选
      for(let k in this.user) {
        for(let d in this.user[k]) {
          if(this.user[k][d].userId == item.userId) {
            this.user[k][d].ifCheck = -1
          }
        }
      }
      // 同步删除名字， id
      this.checkedName.splice(this.checkedName.indexOf(item.userName), 1)
      this.checkedId.splice(this.checkedId.indexOf(item.userId), 1)
      // 设置选中数字
      this.choseNum = this.checkedAllMsg.length
      
    },
    checked() {
      this.checkedName = []
      this.checkedId = []
      // 选中项userId
      this.choseNum = this.checkList.length

      // 返回选中人员
      for (let k in this.user) {
        for (let t in this.user[k]) {
          for (let j in this.checkList) {
            if (this.user[k][t].userId == this.checkList[j]) {
              this.checkedName.push(this.user[k][t].userName)
              this.checkedId.push(this.user[k][t].userId)
            }
          }
        }
      }
      if (this.choseNum == this.allowMaxNum) {
        this.$message.warning('最大允许选择' + this.allowMaxNum + '项')
      }
    },
    sureChoice() {
      this.isShow = false
      this.$emit('checkedVal', this.checkedName, this.checkedId)
      this.clearCheck()
    },
    cancel() {
      // this.$emit('close')
      this.isShow = false
      this.clearCheck()
    },
    current(index, type, event) {
      // 判断是否禁选================
      if (event.target.className == 'isDisabled') {
        return
      }
      this.checkedName = []
      this.checkedId = []
      if (index == 1) {
        this.isAll = 1
      } else if (index == 2) {
        this.isAll = 2
      } else {
        this.isAll = 3
      }
      this.currentIndex = index
      this.checkList = []
      this.choseNum = 0
      this.projectType = type
      if (this.type == 'company') {
        // this.getCData()
        // return
      }
      if (this.type == 'project') {
        this.getPData(this.projectType)
        this.chooseType = this.projectType
      }
      if (this.type == 'pAndC') {
        this.getCPData(index, this.projectType)
      }
    },
    // 已选择回填勾选
    backfill() {
      // this.checkedAllMsg 已选择项数组
      for (let k in this.user) {
        for (let t in this.user[k]) {
          for (let i in this.checkedAllMsg) {
            if (this.user[k][t].userId == this.checkedAllMsg[i].userId) {
              this.user[k][t].ifCheck = 1
            }
          }
        }
      }
      // 设置选中数字
      this.choseNum = this.checkedAllMsg.length
    },
    toSearch() {
      if (this.type == 'company') {
        this.getCData()
      }
      if (this.type == 'project') {
        this.getPData(this.projectType)
        this.chooseType = this.projectType
      }
      if (this.type == 'pAndC') {
        this.getCPData(this.currentIndex, this.projectType)
      }
    },
    getPData(type) {
      let params = new URLSearchParams(),
        useData = this.$user.get_data('userData'),
        token = useData.token,
        projectId = this.$user.get_data('pro_id')
      params.append('token', token)
      this.fuzzySearch ? params.append('fuzzySearch', this.fuzzySearch) : ''
      if (type != undefined) {
        params.append('projectCompanyRoleType', type) //========
      }
      this.axios({
        method: 'POST',
        url: 'api/roster/' + projectId + '/queryProjectUserRoster',
        data: params
      }).then(res => {
        if (res.data.code == 1) {
          this.user = res.data.data

          for (let k in this.user) {
            for (let t in this.user[k]) {
              this.user[k][t].ifCheck = -1
              // 设置默认勾选
              for (let i in this.morenUserId) {
                if (this.user[k][t].userId == this.morenUserId[i]) {
                  this.user[k][t].ifCheck = 1
                  this.checkedName.push(this.user[k][t].userName)
                  this.checkedId.push(this.user[k][t].userId)
                }
              }
            }
          }
          this.disableFun()
          this.backfill()
          if (this.isDeleteCurrent) {
            this.deleteCurrent()
          }
        }
        if (res.data.code == 0) {
          this.$message.error(res.data.errorMessage)
        }
      })
    },
    getCData() {
      // this.currentIndex = 2
      // let params = new URLSearchParams(),
      //   useData = this.$user.get_data('userData'),
      //   token = useData.token
      // params.append('token', token)
      // this.fuzzySearch?params.append('fuzzySearch', this.fuzzySearch):''
      // this.axios({
      //   method: 'POST',
      //   url: '/api/roster/queryCompanyUserRoster',
      //   data: params
      // })
      //   .then(res => {
      //     if (res.data.code != 1)
      //       return this.$message.error(res.data.errorMessage)
      //     this.user = res.data.data

      //     for (let k in this.user) {
      //       for (let t in this.user[k]) {
      //         this.user[k][t].ifCheck = -1
      //         // 设置默认勾选
      //         for (let i in this.morenUserId) {
      //           if (this.user[k][t].userId == this.morenUserId[i]) {
      //             this.user[k][t].ifCheck = 1
      //             this.checkedName.push(this.user[k][t].userName)
      //             this.checkedId.push(this.user[k][t].userId)
      //           }
      //         }
      //       }
      //     }
      //     this.disableFun()
      //     this.backfill()
      //     if (this.isDeleteCurrent) {
      //       this.deleteCurrent()
      //     }
      //   })
      //   .catch(reject => {
      //     this.$message.error(reject)
      //   })
        this.user = {
            A: [
            {
            userId: 24344,
            userName: "人员1",
            companyDepartmentName: "超级管理员1",
            projectDepartmentName: null,
            companyRoleType: null
            },
            {
            userId: 24345,
            userName: "人员2",
            companyDepartmentName: "超级管理员2",
            projectDepartmentName: null,
            companyRoleType: 1
            },
            {
            userId: 24346,
            userName: "人员3",
            companyDepartmentName: "超级管理员3",
            projectDepartmentName: null,
            companyRoleType: 2
            }
            ]
        }
        for (let k in this.user) {
            for (let t in this.user[k]) {
              this.user[k][t].ifCheck = -1
              // 设置默认勾选
              for (let i in this.morenUserId) {
                if (this.user[k][t].userId == this.morenUserId[i]) {
                  this.user[k][t].ifCheck = 1
                  this.checkedName.push(this.user[k][t].userName)
                  this.checkedId.push(this.user[k][t].userId)
                }
              }
            }
          }
          this.disableFun()
          this.backfill()
          if (this.isDeleteCurrent) {
            this.deleteCurrent()
          }
    },
    getCPData(index, type) {
      if (index == 2) {
        // 企业人员
        this.getCData()
        return
      }
      let params = new URLSearchParams(),
        useData = this.$user.get_data('userData'),
        token = useData.token,
        projectId = this.$user.get_data('pro_id'),
        // 企业人员登录，未进入项目时获取的project_id
        projectId2 = this.apProjectId
      params.append('token', token)
      if (this.fuzzySearch) {
        params.append('fuzzySearch', this.fuzzySearch)
      }
      if (type != undefined) {
        params.append('projectCompanyRoleType', type) //========
        this.getPData(type)
        return
      }

      this.axios({
        method: 'POST',
        url: '/api/roster/' + (projectId || projectId2) + '/queryCompanyProjectUserRoster',
        data: params
      }).then(res => {
        if (res.data.code == 1) {
          this.user = res.data.data

          for (let k in this.user) {
            for (let t in this.user[k]) {
              this.user[k][t].ifCheck = -1
              // 设置默认勾选
              for (let i in this.morenUserId) {
                if (this.user[k][t].userId == this.morenUserId[i]) {
                  this.user[k][t].ifCheck = 1
                  this.checkedName.push(this.user[k][t].userName)
                  this.checkedId.push(this.user[k][t].userId)
                }
              }
            }
          }
          this.disableFun()
          this.backfill()
          if (this.isDeleteCurrent) {
            this.deleteCurrent()
          }
        }
        if (res.data.code == 0) {
          this.$message.error(res.data.errorMessage)
        }
      })
    },
    clearCheck() {
      for (let k in this.user) {
        for (let t in this.user[k]) {
          this.user[k][t].ifCheck = -1
          this.checkedName = []
          this.checkedId = []
        }
      }
    },
    disableFun() {
      // 禁选人员
      for (let k in this.user) {
        for (let j in this.user[k]) {
          // this.user[k][j].isDisable = false
          for (let t in this.disabledArr) {
            if (this.user[k][j].userId == this.disabledArr[t]) {
              // this.user[k][j].isDisable = true
              this.user[k][j].ifCheck = -2
            }
          }
        }
      }
    },
    // 删除当前登录人
    deleteCurrent() {
      for (let k in this.user) {
        for (let j in this.user[k]) {
          if (this.user[k][j].userId == this.currentUserId) {
            this.user[k].splice(j, 1)
          }
        }
      }
    },
    // 获取登录人角色
    getUserRoster() {
      let params = new URLSearchParams(),
        useData = this.$user.get_data('userData'),
        token = useData.token,
        projectId = this.$user.get_data('pro_id'),

        // 企业人员登录，未进入项目时获取的project_id
        projectId2 = this.apProjectId
      params.append('token', token)
      params.append('projectId', projectId || projectId2)
      this.axios({
        method: 'POST',
        url: '/api/roster/userRoster',
        data: params
      }).then(res => {
        if (res.data.code == 1) {
          if (res.data.data.userRole == -1) {
            this.userRole = -1 // 项(所有)，企, 全部
          } else if (res.data.data.userRole == -2) {
            this.userRole = -2 // 企, 全部
          } else if (res.data.data.userRole == 0) {
            this.userRole = 0 // 建, 企, 全部
          } else if (res.data.data.userRole == 1) {
            this.userRole = 1 // 设
          } else if (res.data.data.userRole == 2) {
            this.userRole = 2 // 施
          } else if (res.data.data.userRole == 3) {
            this.userRole = 3 // 监
          } else if (res.data.data.userRole == 9) {
            this.userRole = 9 // 行
          } else {
            this.userRole = -3
          }
        }
      })
    },
    init() {
      this.userRole = -3

      if (this.type == 'company') {
        this.getCData()
      } else if (this.type == 'project') {
        this.getPData()
      } else if (this.type == 'pAndC') {
        this.getUserRoster()
        this.getCPData()
      }
    },
    apRoster(data) {
      this.isShow = true
      this.type = data.type
      if (this.type == 'company') {
        this.isAll = 2
      } else {
        this.isAll = 1
      }
      this.allowMaxNum = data.allowMaxNum
      this.disabledArr = data.disabledArr
      this.morenUserId = data.morenUserId
      this.typeChoose = data.typeChoose
      this.isDeleteCurrent = data.isDeleteCurrent
      this.apProjectId = data.apProjectId
      this.currentUserId = this.$user.get_data('userData').userId
      this.init()
    }
  }
}
</script>

<style scoped lang="scss">
.mask {
  background-color: rgba(0, 0, 0, 0.5);
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 99999;

  .wrap {
    width: 490px;
    position: absolute;
    top: 0px;
    right: 0px;
    // height: 100vh;
    background-color: #fff;
    padding-bottom: 125px;

    .search {
      padding: 10px 0px;
      border-bottom: 1px solid #e8e8ee;

      input {
        display: block;
        width: 436px;
        height: 32px;
        margin-left: 28px;
        outline: none;
        border-radius: 4px;
        border: 1px solid rgba(0, 0, 0, 0.15);
        font-size: 14px;
        padding-left: 16px;
      }
    }

    .nav {
      width: 490px;
      padding-left: 40px;
      margin: 15px 0;
      box-sizing: border-box;

      span {
        display: inline-block;
        width: 54px;
        height: 24px;
        border-radius: 4px;
        border: 1px solid #fff;
        color: rgba(0, 0, 0, 0.65);
        background-color: #fff;
        font-size: 14px;
        line-height: 24px;
        text-align: center;

        &:hover {
          cursor: pointer;
          color: #1890ff;
          background-color: #e6f7ff;
          border: 1px solid #91d5ff;
          transition: all 0.3s;
        }
      }

      .active {
        color: #1890ff;
        background-color: #e6f7ff;
        border: 1px solid #91d5ff;
      }

      .isDisabled {
        color: #c0c4cc;
        background-color: #f2f6fc;
        border: 1px solid #dcdfe6;

        &:hover {
          cursor: default;
          color: #c0c4cc;
          background-color: #f2f6fc;
          border: 1px solid #dcdfe6;
        }
      }
    }

    .main {
      // width: 500px;
      height: 700px;
      padding-left: 30px;
      overflow-y: scroll;
      background-color: #fff;

      .item {
        > h1 {
          font-size: 12px;
          color: rgba(0, 0, 0, 0.45);
          height: 25px;
          line-height: 25px;
          background-color: #fafafa;
          border-bottom: 1px solid #f2f2f2;
        }

        > div {
          line-height: inherit;
        }

        .details {
          display: flex;
          align-items: center;
          padding: 15px 0px;
          border-bottom: 1px solid #f2f2f2;

          .text {
            line-height: inherit;

            p {
              display: flex;
              align-items: center;
              font-size: 12px;
              color: rgba(0, 0, 0, 0.45);
              margin-left: 8px;
              margin-bottom: 5px;
              line-height: initial;

              b {
                font-style: normal;
                font-size: 16px;
