  time_value(num) { //日期格式返回
    var date = new Date(num); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
    var Y = date.getFullYear() + '-';
    var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
    var D = (date.getDate() + 1 < 10 ? '0' + date.getDate() : date.getDate());
    var h = date.getHours() + ':';
    var m = date.getMinutes() + ':';
    var s = date.getSeconds();

    return Y + M + D;
  },
  regExp(val) { //文件格式校验
    val = val.toLocaleLowerCase()
    var doc = new RegExp("(.doc|.docx|doc|docx)$");
    var img = new RegExp("(.jpg|.jepg|.png|.svg|.gif|jpg|jepg|gif|png|svg)$");
    var pdf = new RegExp("(.pdf|pdf)$");
    var xls = new RegExp("(.xls|.xlsx|xls|xlsx)$");


    if (doc.test(val)) {
      return 'doc'
    } else if (img.test(val)) {
      return 'img'
    } else if (pdf.test(val)) {
      return 'pdf'
    } else if (xls.test(val)) {
      return 'xls'
    } else {
      return 'file'
    }
  },
  distinct(arrObj) { //数组去重
    var arr = arrObj,
      result = [],
      i,
      j,
      len = arr.length;
    for (i = 0; i < len; i++) {
      for (j = i + 1; j < len; j++) {
        if (arr[i] === arr[j]) {
          j = ++i;
        }
      }
      result.push(arr[i]);
    }
    return result;
  },
  // 前台数据分页  
  limit(allData, index, size) {
    // index 页码 size  每页条数  this.currentPageData 全局遍历数组
    // this.currentPageData = allData.splice((index - 1)*size, size)
    return allData.splice((index - 1) * size, size)
  },
