  // vue锚点定位滚动过渡（ele 需具备scrool属性, 或html元素） ele： this.anchorRoll("#sec2", 20, 200, 20, 'up)
  // <div style="width: 300px;height: 200px;overflow-y: scroll;" id="sec2">
  //     <div style="width: 300px;height: 500px;background-color: red">滚送</div>
  //   </div>
  anchorRoll(thisObj, ele, unit, pos, time, direction) {
    thisObj.$nextTick(()=>{
      if(direction == 'up') {
        let times = setInterval(function() {
          document.querySelector(ele).scrollTop -= unit;
          if(document.querySelector(ele).scrollTop <= pos) {
            clearInterval(times)
          }
        }, time)
      } 
      if(direction == 'down') {
        let times = setInterval(function() {
          document.querySelector(ele).scrollTop += unit;
          if(document.querySelector(ele).scrollTop >= pos) {
            clearInterval(times)
          }
        }, time)
      }
    })
  },
  // 回到顶部
  toTop(unit, time) {
    let times = setInterval(function() {
      document.querySelector('html').scrollTop -= unit;
      if(document.querySelector('html').scrollTop <= 0) {
        clearInterval(times)
      }
    }, time)
  },
