<template>
  <div>
    <span>数字滚动</span>
    {{initNum}}
  </div>
</template>

<script>
export default {
  data() {
    return {
      initNum: 0
    }
  },
  props: {
    num: {
      type: Number,
      default: 100
    },
    speed: {
      type: Number,
      default: 2000
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    init() {
      this.initNum = 0
      let times = setInterval(()=> {
        this.initNum += 10
        if(this.initNum >= 101) clearInterval(times)
      }, 500)
      
    }
  }
}
</script>

<style>

</style>


