<template>
  <div class="ap-select">
    <div class="ap-selectedVal-out" @click="showOption">
      <input type="text" class="ap-selectedVal" placeholder="可输入选择" disabled v-model="checkedVal" id="apSelect"
        @keyup.native="validate1" @change.native="checknum1">
      <i class="showOptionIcon">></i>
    </div>

    <div class="ap-select-option" :class="{'ap-select-option-active': isActive}">
      <ul>
        <li v-for="(k, index) in apSelectOptions" :key="index" @click="chooseCurrentOption(k)">{{k.label}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        apSelectOptions: [{
            label: '自建',
            id: 1
          },
          {
            label: '建设-转让（BT）',
            id: 2
          },
          {
            label: '建设-转让-经营（BOT）',
            id: 3
          },
          {
            label: '建设-拥有-经营（BOO）',
            id: 4
          },
          {
            label: '建设-拥有-经营-转让（BOOT）',
            id: 5
          },
          {
            label: '代建制',
            id: 6
          },
          {
            label: '其他',
            id: 7
          }
        ],
        checkedVal: '',
        isActive: true
      }
    },
    props: {
      moshi: {
        type: String,
        default: ''
      },
      rules: {
        name: [{
            required: true,
            message: '请输入活动名称',
            trigger: 'blur'
          },
          {
            min: 3,
            max: 5,
            message: '长度在 3 到 5 个字符',
            trigger: 'blur'
          }
        ]
      }
    },
    watch: {
      checkedVal() {
        this.$emit('checkVal', this.checkedVal)
      }
    },
    methods: {
      init(obj) {
        this.rules = obj.rules
      },
      chooseCurrentOption(cb) {
        this.checkedVal = cb.label
        this.isActive = true
      },
      showOption() {
        this.isActive = !this.isActive
      },
      init(data = {}) {
        if(data) this.checkedVal = data.value;
        if (data.flag == 2) {
          this.apSelectOptions = [{
            label: '砖木结构',
            id: 1
          }, {
            label: '砖混结构',
            id: 2
          }, {
            label: '钢筋混凝土结构',
            id: 3
          }, {
            label: '钢结构',
            id: 4
          }, {
            label: '框架结构',
            id: 5
          }, {
            label: '剪力墙结构',
            id: 6
          }, {
            label: '框剪结构',
            id: 7
          }, {
            label: '框筒结构',
            id: 8
          }, {
            label: '其他',
            id: 9
          }]
        }
      }
    },
    mounted() {
      let that = this
      this.init() // 初始化 ，可传入更新
      document.onclick = function (event) {
        let e = event || window.event; //兼容ie和非ie的event
        let srcElement = e.srcElement || e.target; //兼容ie和非ie的事件源
        let id = srcElement.id
        if (id != 'apSelect') {
          that.isActive = true
        }
      }
    },
    destroyed() {
      document.onclick = null
    }
  }

</script>

<style lang="scss" scoped>
  .ap-select {
    position: relative;
    width: 453px;

    .ap-selectedVal-out {
      position: relative;
      width: 100%;

      .ap-selectedVal {
        width: 100%;
        height: 38px;
        border: 1px solid #C0C4CC;
        padding-left: 15px;
        padding-right: 30px;
        line-height: 38px;
        border-radius: 4px;
        outline: none;
        font-size: 14px;
        color: #606266;
      }

      .showOptionIcon {
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
        transition: all .3s;
        color: #C0C4CC;
        font-size: 14px
      }
    }

    .ap-select-option {
      position: absolute;
      z-index: 9999;
      width: 500px;

      ul {
        list-style: none;
        padding: 6px 0;
        margin: 0;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        border: 1px solid #E4E7ED;
        border-radius: 4px;
        background-color: #FFF;
        box-shadow: 0 2px 12px 0 rgba(0, 0, 0, .1);
        margin: 5px 0;
        // transition: all .5s;

        li {
          font-size: 14px;
          padding: 0 20px;
          position: relative;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          color: #606266;
          height: 34px;
          line-height: 34px;
          -webkit-box-sizing: border-box;
          box-sizing: border-box;
          cursor: pointer;
          transition: all .5s;

          &:hover {
            background-color: #f5f7fa;
          }
        }
      }
    }

    .ap-select-option-active {
      ul {
        padding: 0;
        margin: 0;
        border: 0;
        box-shadow: none;
        // transition: all .2s;

        li {
          height: 0;
          line-height: 0;
          transition: all .2s;
        }
      }
    }
  }

</style>



