<template>
  <div>
    <span @click="isOpen = !isOpen">打开抽屉</span>
    <div class="drawer" :class="{'drawer-open': isOpen}">
      <header>
        <span>标题</span>
        <span @click="isOpen = false">X</span>
      </header>
      <main>
        内容
      </main>
      <footer></footer>
    </div>
  </div>
</template>

<script>
import { setTimeout } from 'timers';
  export default {
    data() {
      return {
        isOpen: false,
      }
    }
  }

</script>

<style scoped lang="scss">
  .drawer {
    position: fixed;
    top: 0;
    width: 1000px;
    height: 100%;
    z-index: 1000;
    transform: translateX(173%);
    box-shadow: -2px 0 8px rgba(0, 0, 0, 0.15);
    transition: transform 0.3s cubic-bezier(0.7, 0.3, 0.1, 1);

    header {
      padding: 10px;
      border-bottom: 1px solid #333
    }

    main {
      height: 500px;
    }
  }
  .drawer-open {
    transform: translateX(100%);
    transition: transform 0.3s cubic-bezier(0.7, 0.3, 0.1, 1)
  }

</style>



