<template>
  <!--快捷导航--面包屑-->
  <div id="projectSubject">
    <div id="mainContent">
      <!-- <div class="shortcut" :style="{width:bodyWidth*0.8+'px'}"> -->
      <div class="shortcut">
        <!-- <span v-for="k,i  in crumbs_list" @click="route(k)" class="right_list" :style="{background:i==0?'#394a5a':i==1?'#616e7b':i==2?'#838e97':'#aeb6bb'}"> -->
        <span v-for="k,i  in crumbs_list" @click="route(k)" class="right_list" :style="{background:i==0?'#2154b9':i==1?'#616e7b':i==2?'#838e97':'#1a72da'}">
          <b>{{k.name}}</b>
          <!-- <b class="right_dire" :style="{borderLeftColor:i==0?'#394a5a':i==1?'#616e7b':i==2?'#838e97':'#aeb6bb'}"></b> -->
          <b class="right_dire" :style="{borderLeftColor:i==0?'#394a5a':i==1?'#616e7b':i==2?'#2154b9':'#1a72da'}"></b>
        </span>
      </div>
    </div>
  </div>
</template>

<script>
  import {
    mapState,
    mapGetters,
    mapMutations,
    mapActions
  } from 'vuex' //使用state快捷方式   引入mapstate来操作
  export default {
    data() {
      return {
        crumbs_list: [], //面包屑导航
        sideList:this.$store.state.page.items,//所有页面导航
        toPath:"/index/projectsubject",//当前跳转到的路由
        treeOpen:null,//路由所在的关系
      }
    },
    created: function () {

      if (this.crumbs_list.length == 0) { //默认路径
        this.crumbs_list = this.$user.getData("crumbsList")

        console.log(this.$user.getData("crumbsList"))
        if (this.crumbs_list) {
          this.$router.push({
            path: this.crumbs_list[this.crumbs_list.length - 1].router
          })
        }
      }
      this.$forceUpdate()
    },
    watch: {
      $route(to, from) {

        this.toPath = to.path

        let arr = this.sideOpen(this.sideList)


        if (arr != undefined) {
          this.treeOpen = this.crumbs_list = arr.reverse()
          this.$user.saveData("crumbsList", this.crumbs_list)

          this.openTree(this.sideList, 0)
        }
      },
    },
    computed: {
      ...mapGetters(["bodyWidth", "bodyHeight"]),
    },
    methods: {
      sideOpen(list) {
        for (var i in list) {

          if (list[i].router == this.toPath) {
            let arr = [];
            arr.push(list[i]);
            return arr;
          } else {
            if (this.sideOpen(list[i].child) != undefined) {
              let arr = this.sideOpen(list[i].child);
              arr.push(list[i])
              return arr;
            }
          }
        }
      },
      route(k) {
        this.$router.push({
          path: k.router
        })
      },
      openTree(list, val) {
        for (var i in list) {
          if (list[i].router != this.treeOpen[this.treeOpen.length - 1].router) {
            if (list[i].router == this.treeOpen[val].router) {
              list[i].hide = 11


              if (list[i].child[0].name == '企业名片') { //特殊路由处理（项目列表
                list[i].child[0].hide = 11
              }


              if (list[i].child[0].name == '项目列表') { //特殊路由处理（项目列表
                list[i].child[0].hide = 10
              } else {
                list[i].child[0].hide = 11
              }

              if (list[i].child.length != 0) {
                this.openTree(list[i].child, val + 1)
              }
            } else {
              list[i].hide = 10

            }
          }

        }
      }

    }
  }

</script>

<style>
  #projectSubject {
    overflow: hidden;
    background: #FFFFFF;
    position: relative;
  }

  #mainContent {
    transition: all 300ms;
    box-sizing: border-box;
    /* overflow: scroll;
    float: left;
    padding: 10px 20px; */
  }

  #projectSubject .content {
    box-sizing: border-box;
    position: relative;
  }



  /*快捷导航*/
  #projectSubject .shortcut {
    width: 100%;
    height: 40px;
    background: linear-gradient(90deg, #cccccc 20%, #ffffff 80%);
    transition: all 300ms;
  }

  /*右箭头*/
  #projectSubject .right_list {
    width: 120px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    display: inline-block;
    background: #777;
    position: relative;
    color: #fff;
    cursor: pointer;
    transition: all 300ms;
  }

  #projectSubject .right_list:hover {
    width: 130px;
  }

  #projectSubject .right_dire {
    position: absolute;
    right: -20px;
    top: 0;
    z-index: 2;
    width: 0;
    height: 0;
    border-top: 20px solid transparent;
    border-left: 20px solid;
    border-bottom: 20px solid transparent;
  }

</style>



