<template>
  <!-- <div id="projectSubject" :style="{height:$store.state.information.height==0?1150+'px':$store.state.information.height+'px',width:$store.state.information.width+'px'}"> -->
  <div id="projectSubject" :style="{height: 1200==0?1150+'px':1200+'px',width:1200+'px'}">
    <div id="mainContent" :style="{height:600-62+'px',width:1200*0.818+'px'}">
      <!-- <div id="mainContent" :style="{height:bodyHeight-62+'px',width:bodyWidth*0.818+'px'}"> -->
      <div class="shortcut" :style="{width:bodyWidth*0.8+'px'}">
        <span v-for="k,i  in crumbs_list" @click="route(k)" class="right_list"
          :style="{background:i==0?'#394a5a':i==1?'#616e7b':i==2?'#838e97':'#aeb6bb'}">
          <b>{{k.name}}</b>
          <b class="right_dire" :style="{borderLeftColor:i==0?'#394a5a':i==1?'#616e7b':i==2?'#838e97':'#aeb6bb'}"></b>
        </span>
      </div>
      <div class="content" :style="{height:800+'px',width:1500+'px'}">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
  import sideListData from './../../store/crumbs'
  export default {
    data() {
      return {
        crumbs_list: [], //面包屑导航

        sideList: sideListData.state.items, //所有页面导航
        toPath: "/index/projectsubject/projectManagement/myApproval", //当前跳转到的路由
        treeOpen: null, //路由所在的关系

        bodyWidth: 1200
      }
    },
    // created: function () {
    //   console.log(this.sideList, '导航')
    //   // this.$utils.userData.remove_data("crumbsList")

    //   if (this.crumbs_list.length == 0) { //默认路径
    //     // this.crumbs_list = this.$utils.userData.get_data("crumbsList")
    //     this.crumbs_list = this.toPath
    // console.log(this.crumbs_list, '获得导航。。。')
    //     if (this.crumbs_list) {
    //       this.$router.push({
    //         path: this.crumbs_list[this.crumbs_list.length - 1].router
    //       })
    //     }
    //   }
    //   this.$forceUpdate()
    // },
    created() {

      let localUrl1 = [{
        child: [],
        hide: "11",
        icon: "iconfont icon-ziyuan",
        id: "H0000",
        name: "意见反馈",
        router: "/index/projectsubject/feedback"
      }]
      let localUrl = JSON.stringify(localUrl1)
      localStorage.setItem('crumbs', localUrl)

      // 模拟登录初始化数据
      let data = {
        companyName: '我的企业'
      }
      this.$user.save_data('userData', data)

      // 初始面包屑导航
      let crumbs_list = [{
          name: "我的审批",
          router: "/index/projectsubject/projectManagement/myApproval"
        },
        {
          name: "文件审批",
          router: "/index/projectsubject/projectManagement/myApproval/fileApproval"
        }
      ]
      this.$user.save_data('crumbs_list', crumbs_list)

    },
    watch: {
      $route(to, from) {

        this.toPath = to.path

        let arr = this.sideOpen(this.sideList)


        if (arr != undefined) {
          this.treeOpen = this.crumbs_list = arr.reverse()
          this.$utils.userData.save_data("crumbsList", this.crumbs_list)

          this.openTree(this.sideList, 0)
        }
      },
    },
    methods: {
      sideOpen(list) {
        for (var i in list) {

          if (list[i].router == this.toPath) {
            let arr = [];
            arr.push(list[i]);
            return arr;
          } else {
            if (this.sideOpen(list[i].child) != undefined) {
              let arr = this.sideOpen(list[i].child);
              arr.push(list[i])
              return arr;
            }
          }
        }
      },
      route(k) {
        this.$router.push({
          path: k.router
        })
      },
      openTree(list, val) {
        for (var i in list) {
          if (list[i].router != this.treeOpen[this.treeOpen.length - 1].router) {
            if (list[i].router == this.treeOpen[val].router) {
              list[i].hide = 11


              if (list[i].child[0].name == '企业名片') { //特殊路由处理（项目列表
                list[i].child[0].hide = 11
              }


              if (list[i].child[0].name == '项目列表') { //特殊路由处理（项目列表
                list[i].child[0].hide = 10
              } else {
                list[i].child[0].hide = 11
              }

              if (list[i].child.length != 0) {
                this.openTree(list[i].child, val + 1)
              }
            } else {
              list[i].hide = 10

            }
          }

        }
      }

    },
  }

</script>

<style scoped>
  #projectSubject .shortcut {
    width: 100%;
    height: 40px;
    background: linear-gradient(90deg, #cccccc 20%, #ffffff 80%);
    transition: all 300ms;
  }

  #projectSubject .right_list {
    width: 120px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    display: inline-block;
    background: #777;
    position: relative;
    color: #fff;
    cursor: pointer;
    transition: all 300ms;
  }

  #projectSubject .right_list:hover {
    width: 130px;
  }

  #projectSubject .right_dire {
    position: absolute;
    right: -20px;
    top: 0;
    z-index: 2;
    width: 0;
    height: 0;
    border-top: 20px solid transparent;
    border-left: 20px solid;
    border-bottom: 20px solid transparent;
  }

  /* 内容 */
  .content {
    border: 1px solid red;
    margin-top: 20px;
  }

</style>



