<template>
  <div class="wrap">
    <div class="popconfirm" v-if="isShow">
      <i class="el-icon-warning"></i>
      <h1>{{title}}</h1>
      <p>(一旦删除，数据不可恢复)</p>
      <div>
        <button @click="confirm">确定</button>
        <button @click="isShow = false">取消</button>
      </div>
    </div>
    <div @click="isShow = true">
      <slot></slot>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        isShow: false
      }
    },
    props: {
      title: {
        type: String,
        default: '一级标题'
      }
    },
    methods: {
      confirm() {
        this.$emit('confirm')
      }
    }
  }

</script>

<style scoped>
  .wrap {
    position: relative;
    box-sizing: border-box;
  }

  .popconfirm {
    position: absolute;
    top: -152px;
    left: 3px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 15px 30px;
    box-sizing: border-box;
    background-color: #fff;
    box-shadow: 0 0 7px 0 red
  }

  .popconfirm i {
    margin-bottom: 20px;
  }

  .popconfirm h1 {
    font-size: 18px;
    margin: 0;
  }

  .popconfirm p {
    margin: 0
  }

  .popconfirm div {
    margin-top: 20px;
  }

  .popconfirm div button {
    display: inline-block;
    outline: none;
    border: none;
    width: 57px;
    height: 30px;
    border: 1px solid #eee;
    border-radius: 4px;
    background-color: #fff;
    color: #333;
    cursor: pointer;
  }

  .popconfirm div button:nth-child(1) {
    margin-right: 20px;
    border: 0;
    background-color: #198cff;
    color: #fff
  }

</style>



