<!--/* 
 * 备注：因为这是一个全局插件   但是应为需求需要临时存储数据，在不能使用vuex的情况下选择使用locastorage，但是基于locastorage有控件大小的环境下，每一次在使用此插件的父级组件
 * 关闭是请清除locastorage中的sealData数据。
 * 改动————由全局插件改为独立组件，在需要使用的地方进行引用，同时需要在父级组件调用本组件的init方法
 * 2018.11.21 改动————由独立组件改为全局插件（同时也兼容子组件调用），在需要使用的地方调用 this.$pdfFile.install(a, b) a参数为文件Id、b参数为123（分别是签署意见、签名、盖章）
 * 2018.11.26 改动————把签署意见是的背景修改为透明
 * 2018.12.27 新增————文件展示 suffix代表传入文件类型 多传入文件后缀的对象（suffixData:{fileType: '文件后缀'，imgSrc：'图片的路径'}） 这个参数针对图片文件 非图片文件可以不传 毕竟传进来也没什么卵用
 * 2018.12.26 新增————打印 全文件打印功能
 * 2019.1.2   修改————在需要使用的地方调用 this.$pdfFile.install(a, b) a参数改为传入文件的对象(包括{fileId:'文件Id',displayUrl:'displayUrl')、b参数为1234（分别是签署意见、签名、盖章、查看）
 * 2019.1.15  修改————对图片展示做轮播处理
 * 2019.3.15  修改————点击拖动——>按住拖动  单意见签署——>多意见签署
 */-->
<template>
  <div>
    <div class="loadingClass" v-show="percentageType">
      <div class="loadingClassCentre">
        <el-progress type="circle" :percentage="percentage"></el-progress>
      </div>
    </div>
    <!-- <div class='pdf-page'> -->
    <!-- <canvas id="the-canvas"></canvas> -->
    <!-- <div style="width:100px;height:100px ;position: relative;  z-index: 9999;
    background: red;" id="move11" ondragstart='return false;'>   
        <img src="../../../../../../assets/img/dianhua.png" @mousedown="move1" onselectstart="return false;" unselectable="on" style="-moz-user-select:none;width:100%" alt="">
    </div> -->
    <div id="container" v-if="show">
        <div id="the-canvas" @scroll="scrollPdf($event)">
          <div class="pdfClass" :class="{pdfClass1:pdfBig}" :id="'div-pdf-'+pageNum" :style="{width: pdfStyle.width+'px',height: pdfStyle.height+'px'}">
            <div id="seal_pic_div" v-show="sealState == pageNum && opertation.seal" class="sealPicDiv" :style="{top:sealPicDivList.top*actualScale+sealMove.scrollHeight+'px',left:sealPicDivList.left*actualScale+'px',width:sealPicDivList.width+'px',height:sealPicDivList.height+'px',transform:'scale('+actualScale+')'}">
              <img :src="imgUrl" id="imgId" alt="">
            </div>
            <!--图片显示-->
            <div v-if="suffix == 'img'" class="img_list">
              <div class="suffix_img" @mousedown="move1($event)" >
                <img :src="imgSrc" ondragstart='return false;'/>
              </div>
              <div class="img_list_footer">
                <div class="img_list_footer_div" @click="buttonElectImg(-1)"><i class="iconfont icon-zuo"></i></div>
                <div class="img_list_footer_ul">

                <ul :style="{'width': constObj.imgListWidth + 'px', 'marginLeft': constObj.marginLeft + 'px'}">
                  <!-- <li @click="buttonElectImg(-1)"><i class="iconfont icon-zuo"></i></li> -->
                  <li v-for="(item,key) of lookImgList" @click="electImg(item.displayUrl,key)" :class="{'img_list_footer_border':key == imgIndexSrc}"><img
                      :src="item.displayUrl" /></li>
                  <!-- <li @click="buttonElectImg(1)"><i class="iconfont icon-icon-arrow-right2"></i></li> -->
                </ul>
                </div>
                <div class="img_list_footer_div" @click="buttonElectImg(1)"><i class="iconfont icon-icon-arrow-right2"></i></div>
              </div>
            </div>
            <div v-else id="showPDF" :class="{cursor_move:sealMove.isDown}"></div>
    <!-- <div class="cover_class" :class="{cursor_move:sealMove.isDown}" v-show="sealState == pageNum" :style="{width: pdfStyle.width+'px',height: pdfStyle.height+'px'}"
              title="单击拖动位置" @mousedown="move($event)" @click="moveup()"> -->
            <div class="cover_class" :class="{cursor_move:sealMove.isDown}" :style="{width: pdfStyle.width+'px',height: pdfStyle.height+'px'}">
              <!-- <div id="seal_pic_div1" @mousedown="move($event)" class="sealPicDiv" v-show="!sealMove.isDown && opertation.seal" :style="{top:sealPicDivList.top*actualScale+sealMove.scrollHeight+'px',left:sealPicDivList.left*actualScale+'px',width:sealPicDivList.width+'px',height:sealPicDivList.height+'px'}"> -->
              <div id="seal_pic_div1" @mousedown="move($event)" class="sealPicDiv" v-show="opertation.seal" :style="{top:sealPicDivList.top*actualScale+sealMove.scrollHeight+'px',left:sealPicDivList.left*actualScale+'px',width:sealPicDivList.width+'px',height:sealPicDivList.height+'px',transform:'scale('+actualScale+')'}">
              </div>
              <div v-for="(item,index) in signerReturnList" v-show="item.pageIndex == pageNum && opertation.signer" class="signer_return_class" style="transform-origin: 0 0" :style="{top:item.top*actualScale+sealMove.scrollHeight+'px',left:item.left*actualScale+'px',transform:'scale('+actualScale+')'}">
                <div class="signer_return_class_drag">
                  <span title="删除当前意见" @click="deleteSigner(index,1)">
                    <i class="iconfont icon-close"></i>
                  </span>
                </div>
                <!-- {{item.top}}{{item.left}} -->
                <img :src="item.imgCode" alt="" style="-moz-user-select:none;">
              </div>
              
              <div v-for="(item,index) in signerList" v-show="item.pageIndex == pageNum && opertation.signer" class="signer_class" :id="'signer_class_drag'+index" style="transform-origin: 0 0" :style="{top:item.top*actualScale+sealMove.scrollHeight+'px',left:item.left*actualScale+'px',transform:'scale('+actualScale+')'}" ondragstart='return false;'>
                <div class="signer_class_drag" @mousedown="move($event,index)">
                  <span title="单击拖动位置">
                    <i class="iconfont icon-pdf_icon_move"></i>
                  </span>
                  <span title="删除当前意见" @click="deleteSigner(index,2)">
                    <i class="iconfont icon-close"></i>
                  </span>
                </div>
                <!-- {{item.top}}{{item.left}} -->
                <div :id="'signer_class'+index">
                  <textarea name="意见" maxlength="225" v-model="item.text" id="my-node" @click.stop="textareaFocus" class="textarea_class" style="width:150px;height:100px;transform-origin: 0 0"
                      placeholder="请输入意见"></textarea>
                </div>
              </div>
              
            </div>

          </div>
        </div>
        <!--菜单栏-->
        <div class="pdf_titleBar">

          <!--文件名称-->
          <div class="file_name floatLeft">
            {{fileName | substring18}}
          </div>
          <!-- 确定 -->
          <el-button class="button floatRight" type="primary" v-if="opertation.seal || opertation.signer" @click="proceedSeal">确定</el-button>

          <!-- 取消 -->
          <el-button class="button floatRight" @click="close">
            <span v-if="opertation.seal || opertation.signer">取消</span>
            <span v-else>关闭</span>
          </el-button>
          
          <!-- 签署意见 -->
          <div class="zoomInOut floatRight" v-if="opertation.operate == 1 && (signerReturnList.length + signerList.length) <= 49" @click="sealPivState($event)">
            <i class="iconfont icon-pdf_icon_font"></i>
            <p>签署意见</p>
          </div>

          <!-- 个人签章 -->
          <div class="zoomInOut floatRight" v-else-if="opertation.operate == 2 " @click="sealPivState($event)">
            <i class="iconfont icon-pdf_icon_PersonalSea"></i>
            <p>个人签章</p>
          </div>

          <!-- 企业签章 -->
          <div class="zoomInOut floatRight" v-else-if="opertation.operate == 3 " @click="sealPivState($event)">
            <i class="iconfont icon-pdf_icon_CompanySeal"></i>
            <p>企业签章</p>
          </div>

          <!-- 适应页面 -->
          <div class="zoomInOut floatRight" @click="print()">
            <i class="iconfont icon-pdf_icon_print"></i>
            <p>打印</p>

          </div>

          <!-- 适应页面 -->
          <div class="zoomInOut floatRight" @click="scaleStartSize($event)">
            <i class="iconfont icon-pdf_icon_zoom_fit"></i>
            <p>适应页面</p>
          </div>

          <!--放大-->
          <div class="zoomInOut floatRight" @click="scaleSize('big')">
            <i class="iconfont icon-pdf_icon_zoom"></i>
            <p>放大</p>
          </div>

          <!--缩小-->
          <div class="zoomInOut floatRight" @click="scaleSize('small')">
            <i class="iconfont icon-pdf_icon_zoom-"></i>
            <p>缩小</p>
          </div>
          

          <!--页码-->
          <div class="floatCenter" v-if="suffix != 'img'">
            <!--上一页-->
            <div class="zoomInOut display" @click="onPrevPage">
              <i class="iconfont icon-pdf_icon_PgUp"></i>
              <p>上一页</p>
            </div>
            <!--第一页-->
            <div class="zoomInOut page_num display">
              <input type="text" class="page_inp" v-model="pageNum" @keyup="jump" />
              <span class="color"> / {{pdfLength}}</span>
            </div>
            <!--下一页-->
            <div class="zoomInOut display" @click="onNextPage">
              <i class="iconfont icon-pdf_icon_PgDn"></i>
              <p>下一页</p>
            </div>
          </div>

        </div>
        <!-- </div> -->
        <div id="prantId" style="display:none"></div>
    

    </div>
    
  </div>
</template>
<script>
  import PDFJS from "pdfjs-dist-di";
  // import PDFJS from '../../../../../config/pdf.js'
  // import domtoimage from 'dom-to-image'
  import html2canvas from "html2canvas";
  // import PDFJS from '../../static/pdf.js'
  //创建和pdf页数等同的canvas数
  function createSeriesCanvas(num, template) {
    var id = "";
    for (var j = 1; j <= num; j++) {
      id = template + j;
      createPdfContainer(id, "pdfClass1", template + j);
    }
  }
  //创建
  function createPdfContainer(id, className, divId) {
    if (divId == "prantId1") {
      var pdfContainer = document.getElementById("prantId");
    } else {
      var pdfContainer = document.getElementById("showPDF");
    }
    var canvasNew = document.createElement("canvas");
    canvasNew.id = id;
    // canvasNew.className = className
    pdfContainer.appendChild(canvasNew);
  }
  export default {
    data() {
      return {
        operationState: false, // 同一文件是否已经进行过操作
        pdfDoc: null, // pdf 原型对象
        pageNum: 1, // pdf当前显示页码
        pageRendering: false,
        pageNumPending: null,
        scale: 1.0, // pdf缩放比例
        actualScale: 1.0, // pdf缩放比例
        // 其他数据
        show: false, // 显示PDF
        pdfLength: 0, // pdf总页数
        pdfStyle: {
          // 单页PDF的大小
          width: "",
          height: ""
        },
        pdfBig: false, // pdf单页实际高度是否比显示高度大
        sealPicDivList: {
          // 印章签名的大小及位置控制
          top: "",
          left: "",
          width: "",
          height: "",
          initialWidth: 120, // 初始印章宽度
          initialHeight: 120 // 初始印章高度
          // initialWidth: 0, // 初始印章宽度
          // initialHeight: 0 // 初始印章高度
        },
        sealMove: {
          // 印章拖拽定点位置
          startx: "", // clientX
          starty: "", // clientY
          startLeft: "", // left
          startTop: "", // top
          endTop: "",
          endLeft: "",
          isDown: false, // 是否定点操作
          getX: 0, // 坐标点（数据传输用）
          getY: 0, // 坐标点（数据传输用）
          scrollHeight: 0, // 滚动高度
          scrollTop: 0,
          scrollHeight1: 0, // 滚动高度预处理1
          scrollHeight2: 0 // 滚动高度预处理2
        },
        sealState: "", // 章印的页数
        signState: true,
        signState1: true,
        opertation: {
          // 操作
          seal: false, // 盖章操作
          signer: false, // 签署意见操作
          operate: 0 // 具体操作 1：个人意见，2：个人签章，3：企业意见
        },
        imgUrl: "",
        imgState: false,
        sealData: {
          fileId: 0
        }, // 数据
        updateData: {
          state: false, // 是否修改  默认false不是修改
          id: "" // 修改的ID
        }, // 修改时的数据
        twice: false, // 是否是翻页
        imgSrc: "../../../../../../assets/img/shortTime.png",
        suffix: "",
        lookImgList: [], // 查看图片的集合
        imgIndexSrc: 0, // 当前展示图片文件在所在图片集合中的序号
        positionX:'',
        positionY:'',
        signerReturnList: [], // 多意见数据集合
        signerList: [], // 多意见数据集合
        signerId: 0, // 多意见生成临时ID
        canvasSize: {
          width: '',
          height: ''
        }, // 画布大小
        percentage: 0,
        percentageType: false,
        fileName: null,
        constObj: {
          imgListWidth: 0,
          marginLeft: 0,
          imgListLength: 0, // imgList总长度 
          imgListIndex: 0,  // img当前展示的index
        }
      };
    },
    props: ["pdfId"],
    mounted() {
      // this.init()
      
    },
    methods: {
      _loading() {
        loadingInstance = this.$loading({
          lock: true,
          text: "Loading",
          spinner: "el-icon-loading",
          background: "rgba(0, 0, 0, 0.2)"
        });
      },
      // 进度百分比
      percentageCount(){
        let _a = setTimeout(val => {
          this.percentage += 1
          if(this.percentage < 30 && this.percentageType){
            this.percentageCount()
          }else if(this.percentageType){
            this.percentageCount1()
          }
        },200)
      },
      percentageCount1(){
        let _a = setTimeout(val => {
          this.percentage += 1
          if(this.percentage < 60 && this.percentageType){
            this.percentageCount1()
          }else if(this.percentageType){
            this.percentageCount2()
          }
        },400)
      },
      percentageCount2(){
        let _a = setTimeout(val => {
          this.percentage += 1
          if(this.percentage < 90 && this.percentageType){
            this.percentageCount2()
          }else if(this.percentageType){
            this.percentageCount3()
          }
        },800)
      },
      percentageCount3(){
        let _a = setTimeout(val => {
          this.percentage += 1
          if(this.percentage < 96 && this.percentageType){
            this.percentageCount3()
          }
        },2000)
      },
      // 计算选中展示位置
      imgPitchon(imgListLength,imgindex){
        this.constObj.imgListIndex = imgindex
        this.constObj.imgListLength = imgListLength
        if(imgListLength<=14 || imgListLength>14 && imgindex<=7)
          return
        if(imgListLength>14 && imgindex > 7){
          this.constObj.marginLeft = -(imgindex-7) * 68
        }
      },
      // 插件初始方法
      init(fileID, operate, suffixData) {
        // 打开文件
        this.signerList = []
        this.signerReturnList = []
        if (typeof suffixData != "undefined" && suffixData.fileType == "img") {
          this.suffix = suffixData.fileType;
          this.imgSrc = suffixData.imgList[suffixData.imgIndex].displayUrl;
          suffixData.imgList[suffixData.imgIndex].fileName?this.fileName = suffixData.imgList[suffixData.imgIndex].fileName : null;
          this.imgIndexSrc = suffixData.imgIndex
          this.lookImgList = suffixData.imgList;
          this.constObj.imgListWidth = suffixData.imgList.length * 68
          // 因为存在多张图片 所以需要计算打开时展示定位位置
          this.imgPitchon(suffixData.imgList.length,suffixData.imgIndex)
          this.opertation.operate = operate;
          this.show = true;
        } else {
          // this._loading();
          if(loadingInstance!=null){
            loadingInstance.close()
          }
          this.percentageType = true
          this.percentageCount()

          let params = new URLSearchParams();
          params.append("token", this.$user.get_data("userData").token);
          params.append("fileId", fileID);
          // typeof suffixData != "undefined" ?
          //   params.append("fileType", suffixData.fileMold) :
          //   "";
          if(typeof suffixData != "undefined" && suffixData.fileMold){
            params.append("fileType", suffixData.fileMold)
          }
          let that = this;
          this.axios({
              method: "POST",
              url: "/api/project/showFile",
              responseType: "arraybuffer",
              headers: {
                'Content-Length': 348,
                "Content-Encoding": "UTF-8"
			        },
              data: params,
              onUploadProgress:function(progressEvent){ //原生获取上传进度的事件
                if(progressEvent.lengthComputable){
                    //属性lengthComputable主要表明总共需要完成的工作量和已经完成的工作是否可以被测量
                    //如果lengthComputable为false，就获取不到progressEvent.total和progressEvent.loaded
                    // callback1(progressEvent);
                    let loaded = progressEvent.loaded,
                    total = progressEvent.total;
                    that.$nextTick(() =>{
                      // _this.precent = (loaded/total) * 100;
                      // console.log((loaded/total) * 100*0.2)
                    })
                }
            },
              onDownloadProgress:function(progressEvent){ //原生获取下载进度的事件
                if(progressEvent.lengthComputable){
                    //属性lengthComputable主要表明总共需要完成的工作量和已经完成的工作是否可以被测量
                    //如果lengthComputable为false，就获取不到progressEvent.total和progressEvent.loaded
                    // callback1(progressEvent);
                    let loaded = progressEvent.loaded,
                    total = progressEvent.total;
                    that.$nextTick(() =>{
                      // that.percentage = parseInt((loaded/total) * 98)
                      if(parseInt((loaded/total) * 98)>=98){

                      }
                      
                    })
                }
              },
              headers: {
                "Content-Type": "application/x-www-form-urlencoded"
              }
            })
            .then(response => {
              //创建blob对象
              // if(response.data.code != 1)
              //   return this.$message.error(response.data.errorMessage)
              let blob = new Blob([response.data]);
              this.showPDF(URL.createObjectURL(blob), operate, fileID);
              suffixData&&suffixData.fileName?this.fileName = suffixData.fileName : null
            })
            .catch(reject => {
              // loadingInstance.close();
              this.close()
              this.$message.error("系统错误");
            });
        }
      },

      // 插件调用方法 operate 1=签署意见 2=签名 3=签章
      showPDF(url, operate, fileID) {
        Object.assign(this.$data, this.$options.data());

        let _this = this;
        _this.show = true;
        this.sealData.fileId = fileID;
        // PDFJS.workerSrc = '../../static/pdf.worker.js'
        //   let _url = encodeURIComponent(url) // 转化汉字
        PDFJS.getDocument(url)
          .then(function (pdf) {
            _this.pdfDoc = pdf;
            var id = "";
            var idTemplate = "cw-pdf-";
            var pageNum = pdf.numPages;
            _this.pdfLength = pdf.numPages;
            //根据页码创建画布
            createSeriesCanvas(1, idTemplate);

            //将pdf渲染到画布上去
            _this.renderPage(pdf, 1, idTemplate + "1");
            // loadingInstance.close();
            _this.dataDispose(operate);
            _this.percentageType = false
          })
          .catch(reject => {
            _this.show = false;
            // loadingInstance.close();
            _this.close()
            return this.$message.error("PDF文件加载失败，请重试！！！");
          });
      },
      // 获取章印图片方法
      getSealImg(operate) {
        let _url = "";
        let userData = this.$user.get_data("userData");
        let params = new URLSearchParams();
        params.append("token", userData.token);
        if (operate == 2) {
          _url = "/api/sign/image"; // 个人章
        } else {
          _url = "api/enter/sign/library";
        }
        this.axios({
            method: "POST",
            url: _url,
            data: params
          })
          .then(res => {
            // loadingInstance.close();
            this.percentageType = false
            if (res.data.code != 1) {
              return this.$message.error(res.data.errorMessage);
            }
            setTimeout(val => {
              if (operate == 2) {
                this.imgUrl = "data:image/png;base64," + res.data.data;
              } else {
                this.imgUrl = "data:image/png;base64," + res.data.data[0].img;
              }
            }, 100);

            // this.sealPicDivList.width = document.getElementById('imgId').style.width
          })
          .catch(reject => {
            this.close()
            // loadingInstance.close();
            this.close()
            this.$message.error("系统错误");
          });
      },
      // 数据的二次反现
      dataDispose(operate) {
        this.percentageType = false
        let _sealDataList = this.$store.state.sealData.sealList;
        let _sealData;
        let _sealFrimData;
        // for(let i of _sealDataList){

        //   if(i.id == id){
        //     localStorage.setItem('sealData',i)
        //   }
        // }
        if (operate == 1) {
          this.opertation.signer = true;
          if (_sealDataList.length != 0) {
            _sealData = _sealDataList[0];
          }
          // loadingInstance.close();
          this.percentageType = false
          this.percentage = 0
        } else if (operate == 2) {
          this.opertation.seal = true;
          if (_sealDataList.length != 0) {
            _sealData = _sealDataList[0];
          }
          // 获取章印图片
          this.getSealImg(operate);
        } else if (operate == 3) {
          this.opertation.seal = true;
          if (_sealDataList.length != 0) {
            _sealFrimData = _sealDataList[0];
          } else {
            // 获取章印图片
            this.getSealImg(operate);
          }
        } else {}
        this.opertation.operate = operate;

        if (_sealData) {
          setTimeout(val => {
            this.operationState = _sealData.operationState;
            _sealData.sealPicDivList.top = _sealData.sealPicDivList.top + 24
            this.sealPicDivList = _sealData.sealPicDivList;
            this.sealMove = _sealData.sealMove;
            // this.imgUrl = _sealData.imgCode;
            this.imgState = true;
            this.signState = false;
            this.signState1 = false;
            this.sealState = _sealData.sealState;
            this.signerReturnList = _sealDataList;
            // loadingInstance.close();
            this.percentage = 0
            this.percentageType = false
          }, 200);
        }
        if (_sealFrimData) {
          setTimeout(val => {
            this.updateData.state = true;
            this.updateData.id = _sealFrimData.id;
            this.operationState = true;
            this.sealState = _sealFrimData.pageNum;
            this.imgUrl = "data:image/png;base64," + _sealFrimData.img;
            this.imgState = true;
            this.signState = false;
            this.signState1 = false;
            this.sealPicDivList.top = _sealFrimData.yPoint * this.actualScale;
            this.sealPicDivList.left = _sealFrimData.xPoint * this.actualScale;
            this.sealPicDivList.width =
              _sealFrimData.stampWidth * this.actualScale;
            this.sealPicDivList.height =
              _sealFrimData.stampHeight * this.actualScale;
            // loadingInstance.close();
            this.percentage = 0
            this.percentageType = false
          }, 200);
