        // localStorage.removeItem('sealData')
      },
      // 处理印章大小
      sealPicDivFun(width, height) {
        this.sealPicDivList.top = 0 - height / 2;
        this.sealPicDivList.left = 0 - width / 2;
        // 按比例控制印章大小的展示
        this.sealPicDivList.width = width * this.actualScale;
        this.sealPicDivList.initialWidth = width * this.actualScale;
        this.sealPicDivList.height = height * this.actualScale;
        this.sealPicDivList.initialHeight = height * this.actualScale;
      },
      // 渲染PDF
      renderPage(pdf, i, id, type) {
        this.pageRendering = true;
        let _this = this;
        this.pdfDoc.getPage(i).then(function (page) {
          if (!type) {
            _this.actualScale =
              ((document.body.clientHeight - 70) /
                page.getViewport(1.0 - (_this.scale - 1.0)).height);
          } else {
            _this.actualScale = 1.0;
          }
          var viewport = page.getViewport(_this.actualScale);
          // 调用处理印章签名的大小方法
          if (!_this.twice) {
            _this.twice = true;
            if (_this.opertation.operate == 2) {
              _this.sealPicDivFun(120, 120 / 2);
            } else if (_this.opertation.operate == 3) {
              _this.sealPicDivFun(120, 120);
            }
          }

          // _this.shify() // 暂时停用   后面补上
          let canvas = document.getElementById(id);
          canvas.height = viewport.height;
          canvas.width = viewport.width;
          _this.canvasSize.width = viewport.width
          _this.canvasSize.height = viewport.height
          _this.pdfStyle.width = viewport.width;
          _this.pdfStyle.height = viewport.height;
          // Render PDF page into canvas context
          var renderContext = {
            canvasContext: canvas.getContext("2d"),
            viewport: viewport
          };
          var renderTask = page.render(renderContext);
          if (
            viewport.height <
            document.getElementById("the-canvas").offsetHeight - 56
          ) {
            _this.pdfBig = true;
          } else {
            _this.pdfBig = false;
          }

          // Wait for rendering to finish
          renderTask.promise.then(function () {
            _this.pageRendering = false;
            if (_this.pageNumPending !== null) {
              // New page rendering is pending
              this.renderPage(_this.pageNumPending);
              _this.pageNumPending = null;
            }
          });
        });
      },
      // 渲染PDF
      renderPage1(pdf, i, id) {
        createSeriesCanvas(1, "prantId");
        setTimeout(val => {
          let _this = this;
          this.pdfDoc.getPage(i).then(function (page) {
            var viewport = page.getViewport(1.0 * 4);
            // _this.shify() // 暂时停用   后面补上
            let canvas = document.getElementById("prantId1");
            canvas.height = viewport.height;
            canvas.width = viewport.width;
            // Render PDF page into canvas context
            var renderContext = {
              canvasContext: canvas.getContext("2d"),
              viewport: viewport
            };
            var renderTask = page.render(renderContext);
            // Wait for rendering to finish
            renderTask.promise.then(function () {
              _this.pageRendering = false;
              if (_this.pageNumPending !== null) {
                // New page rendering is pending
                this.renderPage1(_this.pageNumPending);
                _this.pageNumPending = null;
              }
            });
          });
        }, 100);
      },
      // 重置PDF上设定的印章或者签署意见的大小及位置
      shify() {
        this.sealPicDivList.top = this.sealMove.endTop * this.actualScale;
        this.sealPicDivList.left = this.sealMove.endLeft * this.actualScale;
        // this.sealPicDivList.width =
        //   this.sealPicDivList.initialWidth * this.actualScale
        // this.sealPicDivList.height =
        //   this.sealPicDivList.initialHeight * this.actualScale
      },
      queueRenderPage(num) {
        if (this.pageRendering) {
          this.pageNumPending = num;
        } else {
          this.renderPage(num);
        }
      },
      // 切换页码
      jump() {
        // this.naver('div-pdf-' + this.pageNum)
        let idTemplate = "cw-pdf-";
        this.renderPage("", Number(this.pageNum), idTemplate + 1);
      },
      onPrevPage() {
        if (this.pageNum <= 1) {
          return;
        }
        this.pageNum--;
        let idTemplate = "cw-pdf-";
        this.renderPage("", this.pageNum, idTemplate + 1);
        
        // this.naver('div-pdf-' + this.pageNum)
      },
      onNextPage() {
        if (this.pageNum >= this.pdfDoc.numPages) {
          return;
        }
        this.pageNum++;
        let idTemplate = "cw-pdf-";
        this.renderPage("", this.pageNum, idTemplate + 1);
        // this.naver('div-pdf-' + this.pageNum)
      },
      naver(id) {
        var obj = document.getElementById(id);
        var oPos = obj.offsetTop;
        return document.getElementById("the-canvas").scrollTo(0, oPos);
