      },
      scaleSize(size) {
        // 缩放功能
        if (this.sealMove.isDown)
          return this.$message.error("你正在进行签名或者盖章");
        let idTemplate = "cw-pdf-";
        if (size == "big") {
          if (this.suffix != "img") {
            if (this.scale > 2) {
              return;
            }
            this.scale = this.scale + 0.2;
            this.renderPage("", this.pageNum, idTemplate + 1);
            // this.sealPicDivList.top = this.sealMove.endTop * this.actualScale
            // this.sealPicDivList.left = this.sealMove.endLeft * this.actualScale
          } else {
            let odiv = document.getElementsByClassName('suffix_img')
            // debugger
            odiv[0].style.height = odiv[0].clientHeight * 1.2 + 'px'
          }
        } else {
          if (this.suffix != "img") {
            if (this.scale < 0.4) {
              return;
            }
            this.scale = this.scale - 0.2;
            this.renderPage("", this.pageNum, idTemplate + 1);
            // this.sealPicDivList.top = this.sealMove.endTop * this.actualScale
            // this.sealPicDivList.left = this.sealMove.endLeft * this.actualScale
          } else {
            let odiv = document.getElementsByClassName('suffix_img')
            // debugger
            odiv[0].style.height = odiv[0].clientHeight * 0.8 + 'px'
          }
        }
        // this.sealPicDivList.width = this.sealPicDivList.initialWidth * this.actualScale
        // this.sealPicDivList.height =
        //   this.sealPicDivList.initialHeight * this.actualScale
        
      },
