        }, 200);
      },
      // 切换图片
      electImg(item, index) {
        this.imgSrc = item;
        this.imgIndexSrc = index;
        this.constObj.imgListIndex = index;
        this.lookImgList[index].fileName?this.fileName = this.lookImgList[index].fileName:null;
      },
      // 左右切换图片
      buttonElectImg(index) {
        if (this.imgIndexSrc + index < 0 || this.imgIndexSrc + index > this.lookImgList.length - 1) return
        this.imgIndexSrc = this.imgIndexSrc + index;
        this.imgSrc = this.lookImgList[this.imgIndexSrc].displayUrl;
        this.lookImgList[this.imgIndexSrc].fileName?this.fileName = this.lookImgList[this.imgIndexSrc].fileName:null;
        this.constObj.imgListIndex = this.constObj.imgListIndex + index
        // if(this.constObj.imgListIndex < 7 || this.constObj.imgListIndex > this.constObj.imgListLength - 8){
          
        // }else{
          if(index == 1 && Math.abs(this.constObj.marginLeft) < (this.constObj.imgListLength-14) * 68){
            this.constObj.marginLeft = this.constObj.marginLeft - index * 68
          }else if(index == -1 && Math.abs(this.constObj.marginLeft) > 0){
            this.constObj.marginLeft = this.constObj.marginLeft - index * 68
          }
        // }
      }
    },
    filters:{
      substring18(val) {
          if(!val || val.length < 18) return val
          return val.substring(0, 18) + "···"
        },
    }
    // watch:{
    //   pdfId(){
    //     this.init()
    //   }
    // }
  };

</script>

