  }

  /*页码跳转*/
  .pdf_page_num {
    margin-left: 180px;
    width: 180px;
    height: 40px;
    float: left;
  }

  .pdf_page_num i {
    display: inline-block;
    width: 28px;
    height: 40px;
    font-size: 22px !important;
    line-height: 48px;
    cursor: pointer;
    transition: all 100ms;
    text-align: center;
  }

  .pdf_page_num i:hover {
    box-shadow: inset 0 0 12px 2px #333;
  }

  .page_num {
    width: 100px;
    height: 46px;
    line-height: 40px;
    margin: 5px 0 !important;
    vertical-align: top;
  }

  .page_num:hover {
    color: #fff;
  }

  .page_num .page_inp {
    width: 40px;
    text-align: center;
    height: 25px;
    background-color: #323232;
    border-radius: 2px;
    outline: none;
    border: none;
    color: #fff;
  }

  /*pdf框状态栏*/
  .box_state {
    width: 120px;
    height: 40px;
    float: right;
    color: #666;
  }

  .pdf_box_state {
    width: 40px;
    height: 40px;
    text-align: center;
    line-height: 45px;
    float: right;
    cursor: pointer;
    box-sizing: border-box;
    padding-left: 5px;
    transition: all 300ms;
  }

  .pdf_popup:hover {
    box-shadow: inset 0 0 15px 0px #333;
