  }

  .pdf_close:hover {
    box-shadow: inset 0 0 15px 0px #333;
    color: #f56c6c;
  }

  /*****************图标栏*/
  .pdf_titleBar .icon-tubiaozhizuomoban {
    color: #efb336;
  }

  .pdf_titleBar .icon-dayin {
    color: #1890ff;
  }

  .pdf_titleBar .icon-suoxiao {
    font-size: 18px;
  }

  .pdf_titleBar .icon-fangda {
    font-size: 18px;
  }

  .pdfClass {
    position: relative;
    margin: 0 auto;
    padding: 5px 0;
    box-sizing: border-box;
    overflow: hidden;
    border-radius: 4px;
    width: 100%;
    height: 100%;
  }

  .pdfClass1 {
    position: absolute;
    margin: auto;
    left: 0;
    right: 0;
    top: 56px;
    bottom: 0;
    transition: all 200ms;
  }

  .sealPicDiv {
    position: absolute;
    left: 0;
    top: 0;
    /* width: 200px;
  height: 200px; */
    box-sizing: border-box;
  }

  .sealPicDiv img {
    height: 100%;
  }

  .cover_class {
    position: absolute;
    left: 0;
    top: 0;
  }

  .cursor_move {
    z-index: 99999;
    cursor: move;
  }

  .signer_class {
    position: absolute;
    top: 20px;
