<template>
  <div class="wrap">
    <ap-tabs v-model="activeIndex">
      <ap-tab-item label="资金1" index="0">111</ap-tab-item>
      <ap-tab-item label="资金2" index="1">222</ap-tab-item>
      <ap-tab-item label="资金3" index="2">333</ap-tab-item>
      <ap-tab-item label="资金4" index="3">444</ap-tab-item>
      <ap-tab-item label="资金5" index="4">555</ap-tab-item>
    </ap-tabs>
  </div>
</template>

<script>
  import apTabs from './ap_tabs'
  import apTabItem from './ap_tab_item'
  export default {
    data() {
      return {
        activeIndex: 3
      }
    },
    components: {
      apTabs,
      apTabItem
    },
    methods: {

    }
  }

</script>

<style scoped>
  .wrap {
    background: #FFFFFF;
    border-radius: 2px;
    margin: 20px;
    position: relative;
  }

  .nav {
    border-bottom: 1px solid #e8e8e8;
    padding: 0 20px;
  }

  .nav span {
    font-size: 16px;
    color: #1890FF;
    line-height: 24px;
    padding: 15px;
    display: inline-block;
    margin-right: 20px;
    cursor: pointer;
  }

  .nav .current {
    border-bottom: 2px solid #1890FF;
    cursor: auto;
  }
</style>



