<template>
    <div>
        <div class="wrap" ref="wrap">
            <span class="test" @mousedown="move($event)">点</span>
        </div>
        <span>{{num}}</span>
        <span>{{value}}</span>
    </div>
</template>

<script>
export default {
    data() {
        return {
            num: 90
        }
    },
    props: {
        value: {
            type: String,
            default: '默认'
        }
    },
    watch: {
    },
    methods:{
        move(e) {
            //获取目标元素
            let odiv = e.target;
            //算出鼠标相对元素的位置
            let disX = e.clientX - odiv.offsetLeft;
            let disY = e.clientY - odiv.offsetTop;
            document.onmousemove = (e)=>{       //鼠标按下并移动的事件
                //用鼠标的位置减去鼠标相对元素的位置，得到元素的位置
                let left = e.clientX - disX;    
                let top = e.clientY - disY;
                
                //绑定元素位置到positionX和positionY上面
                this.positionX = top;
                this.positionY = left;
                
                //移动当前元素
                // odiv.style.left = left + 'px';
                // odiv.style.left = top + 'px';
                // odiv.style.top = 0 + 'px';
                this.$refs.wrap.style.borderLeftWidth = left + 'px';
                this.num = left
            }
            document.onmouseup = (e) => {
                document.onmousemove = null;
                document.onmouseup = null;
            }
        }
    }
}
</script>

<style scoped lang="scss">
    .wrap {
        width: 500px;
        height: 20px;
        border-radius: 10px;
        background-color: #999;
        border-left: 0px solid red;
        box-sizing: border-box;
        position: relative;
        span {
            position: absolute;
            left: 0px;
            display: inline-block;
            width: 30px;
            height: 30px;
            line-height: 30px;
            text-align: center;
            border-radius: 50%;
            background-color: blue
        }
    } 
</style>



