<template>
  <div id="pdf_box" :class="max?'maxbox':'minbox'">
    <!-- 为文档类型时转化为fdf -->
    <pdf 
      v-if="cType=='application/pdf'" 
      v-for="i in numPages" 
      :src="src" 
      :key="i" 
      :page="currentPage" 
      class="pdf_text pdf_text_size"
      >
    </pdf>

    <!--为图片类型时-->
		<!--startprint-->
		<img v-if="cType!='application/pdf'"  class="pdf_text_size pdf_text img_nav" :src="imgsrc"/>
		<!--endprint-->

    <!-- 顶部菜单  -->
    <div class="pdf_titleBar" >

      <div class="pdf_print" @click="print()">
				<span>打印</span>
				<iframe src="" style="opacity: 0;" id="ifr" width="0" height="0"></iframe>
			</div>

      <!--页码-->
			<div class="pdf_page_num">
        <span @click="skip('forward')">上一页</span>
				<!-- <i class="iconfont icon-zuo" @click="skip('forward')"></i> -->
				<span class="page_num" >
					<input type="text" class="page_inp" v-model="currentPage" @keyup="jump()" /> 
					/ {{pageCount}}
				</span>
				<!-- <i class="iconfont icon-icon-arrow-right2" @click="skip('next')"></i> -->
        <span @click="skip('next')">下一页</span>
			</div>

      <!--放大缩小-->
			<div class="zoomInOut">
				<span @click="scale_size('big')">放大</span>
				<span @click="scale_size('small')">缩小</span>
			</div>

      <!--旋转-->
			<div class="pdf_rotate">
        <span @click="rotate_angle('left')">左旋转</span>
        <span @click="rotate_angle('right')">右旋转</span>
			</div>

      <div class="box_state">
				<!--关闭-->
				<div class="pdf_close pdf_box_state" @click="close()">
					<span>关闭</span>
				</div>
				<!--弹窗显示-->
				<div class="pdf_popup pdf_box_state" @click="max_box()">
					<span>最大化</span>
				</div>
			</div>

    </div>
  </div>
</template>

<script>
import axios from 'axios'
import pdf from 'vue-pdf'
  export default {
    data() {
      return {
        currentPage: 1,//当前页码
        pageCount: 1,//总页数
        numPages: 1,
        src:null,
        imgsrc:null,
        cType:'',
        dataFile: null,
        max:false,
        rotate:0,
        scale:60,
        left:20,
        top:10
      }
    },
    props:['menuPdfData'],
    components: {
      pdf
    },
    methods: {
      getType(){
					let fileSuffix=this.menuPdfData.fileSuffix
					let fileType=this.$utils.regExpFileIco(fileSuffix);//判断文件类型
					if(fileType!='img'){
						this.cType='application/pdf'
					}else{
						let suffix=this.menuPdfData.fileSuffix
						if(suffix=='png'){
							this.cType='application/x-png'
						}
						if(suffix=='jepg'||suffix=='jpg'){
							this.cType='application/x-jpg'
						}
						if(suffix=='gif'){
							this.cType='image/gif'
						}
						if(suffix=='svg'){
							this.cType='text/xml'
						}
					}
      },
      //  预下载便于查看
			download(){
				// let fileId=this.menuPdfData.fileId;//确认上传文件ID
				// let token=this.$user.get_data('userData').token
				let params = new URLSearchParams
					params.append('token', '3bd5b47c-fd9f-4e0d-9c02-52fe0be9444e')
					params.append('fileId', 11511)  //此id 返回doxc文档
					// params.append('fileId', 11512)  //此id 返回png图片
				let that=this;
				axios({
          method:"POST",
          url:'http://test.jianyitong.com.cn/api/project/showFile',
					responseType: 'arraybuffer',
					data: params,
//				    headers: {
//					    'Content-Type': this.cType+';charset=UTF-8'
//					}
				})
				.then(function (response) {
					if(response.data.code!=0){
						let blob = new Blob([response.data], {type: that.cType}); 
						if(that.cType=='application/pdf'){
							//创建blob对象
							that.gain(URL.createObjectURL(blob))
							that.dataFile=URL.createObjectURL(blob)
						}else{
							that.imgsrc=URL.createObjectURL(blob)
						}
					}
				})
      },
			gain(txt){ //获得
				let loadingTask = pdf.createLoadingTask(txt);
				this.src=loadingTask
				
//				loadingTask.then(this.pageSum())
				this.currentPage=Number(this.currentPage)
				
				let loding_pdf=setInterval(() => {
					if(this.src._transport==true){
						if(this.src._transport.numPages!=null){
						    this.pageCount=this.src._transport.numPages
	                  		clearInterval(loding_pdf)	
						}
					}
            	}, 500);
      },
      print(){//打印机
			    let id_str,dataURL;
			    var iframe = document.createElement('IFRAME');
			    	iframe.setAttribute('style', 'position:absolute;width:0px;height:0px;left:-500px;top:-500px;');
					document.body.appendChild(iframe);
				var doc = null;
					doc = iframe.contentWindow.document;
				
				
			    	if(this.cType=='application/pdf'){
			    		var canvas=document.getElementsByTagName("canvas")[0];
			    			dataURL=canvas.toDataURL("image/png")
			    			canvas.id="pdfPrint_open"
			   				id_str='pdfPrint_open'
			   				
							doc.write('<img src="'+dataURL+'"/>');
							
							setTimeout(()=>{
								doc.close();

								iframe.contentWindow.focus();
								iframe.contentWindow.print();
								
							},300)

			    	}else{
			    		doc.write('<img src="'+this.imgsrc+'"/>');
							
						setTimeout(()=>{
							doc.close();

							iframe.contentWindow.focus();
							iframe.contentWindow.print();
							
						},300)
			    	}
			    
				if (navigator.userAgent.indexOf("MSIE") > 0)
				{
				     document.body.removeChild(iframe);
				}
			    
//			    var c=canvas.getContext("2d");
//			    var dataURL = canvas.toDataURL("image/png");
//			    
//			    var printWin = window.open(document.getElementById("ifrm").src);
//              printWin.print();
			    
//			    var newWindow=window.open();
//			    newWindow.document.write('<img src="'+dataURL+'" style="width:'+600+'px;height:'+850+'px"/>');
//			    setTimeout(
//			    	()=>{
//			    		newWindow.print();
//			    	},2000)

      },
      skip(txt){//前后跳转
				if(txt=='forward'){
					this.currentPage=Number(this.currentPage-1)
					if(this.currentPage<=0){
						this.currentPage=1
					}
				}
				if(txt=='next'){
					this.currentPage=Number(this.currentPage+1)
					if(this.currentPage>this.pageCount){
						this.currentPage=this.pageCount
					}
				}
			},
			jump(){//跳转
				this.currentPage=Number(this.currentPage)
				
				if(this.currentPage==undefined||this.currentPage==null||this.currentPage==0||isNaN(this.currentPage)){
					this.currentPage=1
				}
				if(this.currentPage>=this.pageCount){
					this.currentPage=this.pageCount
				}
				this.gain()
				this.$forceUpdate()
      },
      
      rotate_angle(txt){//旋转
				let rotate_obj=document.querySelector('.pdf_text').style.transform
				if(txt=='left'){
					this.rotate=this.rotate-90
					
				}
				if(txt=='right'){
					this.rotate=this.rotate+90
					
				}
				document.querySelector('.pdf_text').style.transform="rotateZ("+this.rotate+"deg)"
			},
			scale_size(txt){//放大缩小
				let rotate_obj=document.querySelector('.pdf_text_size').style.width
				if(txt=='big'){
					this.scale=this.scale+2
					if(this.top>0){
						this.top=this.top-3.4	
					}else{
						this.top=0	
					}
					this.left=this.left-1
					
				}
				if(txt=='small'){
					this.scale=this.scale-2
					if(this.left>35){
						this.top=this.top+3.4	
					}else{
						this.top=0	
					}
					this.left=this.left+1
					
				}
				if(this.scale>120){
					this.scale=120
					this.top=0
					this.left=20
				}
				if(this.scale<2){
					this.scale=2
					this.top=47.6
					this.left=49
				}
//				if(this.$utils.regExpFileIco(this.menuPdfData.fileSuffix)!='application/pdf'){
//					document.querySelector('.pdf_text_size').style.top=this.top+20+"%"
//				}else{
//					document.querySelector('.pdf_text_size').style.top=this.top+"%"
//				}
				document.querySelector('.pdf_text_size').style.left=this.left+"%"
				document.querySelector('.pdf_text_size').style.width=this.scale+"%";
				this.gain(this.dataFile)
				this.$forceUpdate()
      },
      
      max_box(){//窗口放大
				this.max=!this.max
				this.download()
			},
			close(){//关闭
				
				this.src=null;
				this.currentPage=1;
				this.pageCount=0;
				this.numPages=1;
				this.max=false;
				this.rotate=0;
				this.scale=30;
				this.left=35;
				this.top=0;
				this.imgsrc=null;
				
				
				this.$emit('openpdf',false)
			}
    },
    created() {
      this.getType()
      this.download()
    }
  }

</script>

<style lang="scss" scoped>
#pdf_box {
  width: 100%;
  height: 100%;
  background: rgba(120,120,120,0.8);
  box-sizing: border-box;
  overflow-y: scroll;
  padding: 50px;
  padding-top: 65px;
  overflow-x: hidden;
  left: 0;
  top: 0;
  z-index: 99999;
  .pdf_print {
    width: 120px;
		height: 40px;
		float: left;
		text-align: center;
		cursor: pointer;
		margin-left: 20px;
  }
  .box_state{
		width: 120px;
		height: 40px;
		float: right;
		color: #666;
	}
  .pdf_box_state{
		width: 40px;
		height: 40px;
		text-align: center;
		line-height: 45px;
		float: right;
		cursor: pointer;
		box-sizing: border-box;
		padding-left: 5px;
		transition: all 300ms; 
	}
  /*页码跳转*/
	.pdf_page_num{
		margin-left: 180px;
		width: 180px;
		height: 40px;
		float: left;
	}
  .pdf_page_num span{
		color: #666;
		display: inline-block;
		width: 28px;
		height: 40px;
		font-size: 22px!important;
		line-height:48px;
		float: left;
		cursor: pointer;
		transition: all 100ms; 
		text-align: center;
	}	
	.pdf_page_num span:hover{
		box-shadow:inset 0 0 12px 2px #333 ;
	}
	.page_num{
		float: left;
		width: 100px;
		height: 40px;
	}
	.pdf_page_num .page_inp{
		width: 40px;
		text-align: center;
		height: 25px;
	}
}

/*旋转*/
	#pdf_box .pdf_rotate{
		width: 100px;
		height: 40px;
		margin-left:80px;
		float: left;
	}
	#pdf_box .pdf_rotate span{
		display: inline-block;
		width: 40px;
		height: 40px;
		margin: 0!important;
		color: #666;
		text-align: center;
		cursor: pointer;
		box-sizing: border-box!important;
	}
  /*放大缩小*/
	#pdf_box .zoomInOut{
		width: 180px;
		height: 40px;
		float: left;
		cursor: pointer;
		margin-left: 20px;
	}
	#pdf_box .zoomInOut span{
		width: 80px;
		height: 40px;
		display: inline-block;
		transition: all 300ms;
		text-align: center;
	}


.pdf_titleBar{
  position: absolute;
  left: 0px;
  top: 0px;
  color: red;
  font-size: 16px;
  height: 40px;
  width: 100%;
  background:rgba(255,255,255,.9);
  z-index: 200;
  color: #000;
  line-height: 40px;
  background: bisque;
}
/*小窗口*/
.minbox{
  position: absolute;
}
/*大窗口*/	
.maxbox{
  position: fixed;
  width: 100%;
  height: inherit;
}
</style>



