<template>
  <div id="side" :style="{height:bodyHeight+'px',width:bodyWidth*0.178+'px'}">
    <div class="logo_side">
      <!--<div id="logo">
				<img src="../../../../../../static/img/smallPicture/logo.png"/>
			</div>-->
      <h1 :style="{fontSize:280<$store.state.information.width*0.18?'18px!important'
						:''}" :title="'标题'">苹果家+</h1>
    </div>


    <sideNav :model='show' :num='1' class="side_scroll" :style="{height:bodyHeight*0.8+'px'}" @pitch="pitch" :cId='cId'>
    </sideNav>



  </div>
</template>

<script>
  import {
    mapState,
    mapGetters,
    mapMutations
  } from 'vuex' //使用state快捷方式   引入mapstate来操作
  import sideNav from './side_nave'

  export default {
    data() {
      return {
        height: document.body.offsetHeight * 0.8 + 'px',
        show: this.$store.state.page.items,
        cId: -1 //选中项
      }
    },
    props: ['bodyWidth', 'bodyHeight', 'togId'],
    components: {
      sideNav
    },
    created: function () {
      if (this.togId != -1) {
        this.cId = this.togId
      }
    },
    methods: {
      pitch(id) {
        this.cId = id.id
        this.$emit('xinFun')
      },
      sideHead() {
        this.$store.state.information.sideShow = !this.$store.state.information.sideShow
      },
      refresh() {
        this.show = this.$store.state.page.items
      }
    },


  }

</script>

<style>
  #side {
    /* float: left;
		width: 18%; */
    height: 100%;
    background: green;
    position: relative;
    transition: all 300ms;
  }

  #side .side_scroll {
    height: 800px;
    overflow-y: scroll;
    overflow-x: hidden;
    /* -ms-overflow-style: none;
    overflow: -moz-scrollbars-none; */
  }

  #side .side_scroll::-webkit-scrollbar {
    width: 0;
  }

  .side_show {
    margin-left: -17.9% !important;
  }

  /*侧边栏logo*/
  .logo_side {
    height: 60px;
    width: 100%;
    background: red;
  }

  #logo {
    width: 60px;
    height: 60px;
    float: left;
  }

  #logo img {
    width: 40px;
    height: 40px;
    margin-left: 20px;
    margin-top: 10px;
  }

  #side h1 {
    font-family: "微软雅黑";
    font-weight: 600;
    color: #FFFFFF !important;
    text-align: center;
    letter-spacing: 2px;
    line-height: 60px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }

  /*收缩按钮*/
  #side .conceal {
    height: 80px;
    width: 30px;
    position: absolute;
    right: 0;
    top: 40%;
    line-height: 80px;
    text-align: center;
    background: #fff;
    border-bottom-right-radius: 0px;
    border-top-right-radius: 0px;
    border-bottom-left-radius: 4px;
    border-top-left-radius: 4px;
    cursor: pointer;
    transition: all 300ms;
    color: #666;
    text-align: center;
  }

  #side .conceal_show {

    right: -30px;
    background: green;
    border-bottom-right-radius: 4px;
    border-top-right-radius: 4px;
    border-bottom-left-radius: 0px;
    border-top-left-radius: 0px;
    color: #fff;
  }

  #side .conceal i {
    margin-right: 5px;
    font-size: 30px;
    display: inline-block;
    width: 30px;
    height: 20px;
    line-height: 20px;
    transform: rotateZ(90deg);
  }

  #side .conceal_i_show {
    transform: rotateZ(-90deg) !important;
  }

</style>



