<template>
	<!--图片查看器-->
	<div id="img_look" 	>
		<div class="img_show" :class="whole?'fix':'pos'" 
			@mouseenter="assist('on')" @mouseleave="assist('off')">
			
			<!--主要展示图-->
				<img v-for="key,index in imgList" class="img_nav" :src="key.picUrl" v-if="index==pageimg"
				 	v-drag="{set:set,rec_set:rec_set}"  :style="[setplace]"  
				 	@mousewheel='soll_scale($event)' 	alt="图片加载异常..."
				 	/>
			
			
			<!--缩略图-->
			<div class="breviary"  
				:class="{'te_ce':whole?imgList.length<12:imgList.length<10,'img_hide':assistShow==false}" 
					:style="[brev]">
				<img class="breimg" v-for="key,index in imgList"  
					:src="imgList[index].picUrl" :class="{pitch:pageimg==index}"
					@click="page_set(index)" :title="key.picUrl"   alt="图片加载异常..."/>
			</div>
			<!--功能-->
			<div class="operation" :class="assistShow==false?'img_hide':''">
				<img src="./xuanzhuan1.png" alt="" @click="rotate_angle('left')">
				<img src="./xuanzhuan2.png" alt="" @click="rotate_angle('right')">
				<img src="./zuojiant.png" alt="" @click="next_prev('prev')">
				<img src="./youjiant.png" alt="" @click="next_prev('next')">
				<img src="./fangda.png" alt="" @click="scale_size('big')">
				<img src="./suoxiao.png" alt="" @click="scale_size('small')">
				<!-- <i class="iconfont icon-xuanzhuan1" @click="rotate_angle('left')"></i>
				<i class="iconfont icon-xuanzhuan" @click="rotate_angle('right')"></i>
				<i class="iconfont icon-zuo" @click="next_prev('prev')"></i>
				<i class="iconfont icon-icon-arrow-right2" @click="next_prev('next')"></i>
				<i class="iconfont icon-fangda" @click="scale_size('big')"></i>
				<i class="iconfont icon-suoxiao" @click="scale_size('small')"></i> -->
			</div>
			
			<!--关闭-->
			<div class="close_img" @click="close_img">
				<!-- <i class="iconfont icon-icon1"></i> -->
				<img src="./quxiao.png" alt="">
			</div>
			
			<!--放大-->
			<div class="amplification" @click="fullScreen">
				<!-- <i class="iconfont icon-fangda1"></i> -->
				<img src="./fangda.png" alt="">
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data () {
		    return {
		    	imgList:[],//从父元素获取的图片地址数组
		    	pageimg:0,//从父元素获取的跳转到第几页
		    	
		    	brev:{//缩略图相关
		    		width:null,
		    		transform:null,
		    	},
		    	whole:false,//窗口放大缩小
		    	
		    	scale:50,//图片放大缩小基准比率
		    	
		    	ts:{
		    		rotate:0,
		    		scale:1
		    	},
		    	
		    	setplace:{//图片位置
		    		left:'50%',
		    		top:'50%',
		    		transform:'translateX(-50%)translateY(-50%)',
		    	},
		    	
		    	standard:{//缩略图位移
		    		left:'0',
		    		top:'0'
		    	},
		    	
		    	assistShow:false,
		    	
		    }
		},
		props:['img','img_page'],
		created:function(){
			this.pageimg=this.img_page.page
			this.brev.width=this.img.length*160+'px'
			this.imgList=this.img
			

  		},
  		mounted:function(){
			this.recover()
  		},
		methods:{
			close_img(){
				this.img_page.show=false
			},
			rotate_angle(txt){//旋转
				if(txt=='left'){
					this.ts.rotate=this.ts.rotate-90
				}
				if(txt=='right'){
					this.ts.rotate=this.ts.rotate+90
				}
				this.tran_set()
			},
			scale_size(txt){//放大缩小
				if(txt=='big'){
					this.scale=this.scale+2
				}
				if(txt=='small'){
					this.scale=this.scale-2
				}
				if(this.scale>100){
					this.scale=100
				}
				if(this.scale<2){
					this.scale=2
				}
				this.ts.scale=this.scale/50
				
				this.tran_set()
			},
			tran_set(txt){//旋转设置，放大缩小设置
				this.setplace.transform='translateX(-50%)translateY(-50%)scale('+this.ts.scale+')rotateZ('+this.ts.rotate+'deg)'
			},
			next_prev(txt){
				if(txt=='next'){
					if(this.pageimg+1>=this.imgList.length-1){
						this.pageimg=this.imgList.length-1
					}else{
						this.pageimg=this.pageimg+1
					}
				}
				if(txt=='prev'){
					if(this.pageimg<1){
						this.pageimg=0
					}else{
						this.pageimg=this.pageimg-1
					}
				}

				this.tr_lfet()
				this.recover()
			},
			page_set(val){//点击跳转图片
				this.pageimg=val
				
				this.recover()
				this.tr_lfet()
			},
//			缩略图
			tr_lfet(){
				let cont=this.pageimg+1
				if(this.whole!=true){
					if(this.imgList.length>9){
						if(cont>5&&cont<=this.imgList.length-4){
							let val=(cont-5)*160-88
							this.brev.transform='translateX(-'+val+'px)'
						}
						if(cont<5){
							this.brev.transform='translateX('+0+'px)'
						}
					}	
				}else{
					if(this.imgList.length>9){
						if(cont>7&&cont<=this.imgList.length-6){
							let val=(cont-6)*160-88
							this.brev.transform='translateX(-'+val+'px)'
						}
						if(cont<7){
							this.brev.transform='translateX('+0+'px)'
						}
					}
				}
			},
//			放大缩小窗口
			fullScreen(){
				this.whole=!this.whole
				
			},
			set(val,value){
				this.setplace.left=(val+this.standard.left)+'px'
				this.setplace.top=(value+this.standard.top)+'px'
			},
			rec_set(){
				this.standard.left=parseInt(this.setplace.left)
				this.standard.top=parseInt(this.setplace.top)
			},
//			归位
			recover(){
//				可修改坐标
				this.setplace.left=document.getElementsByClassName('img_show')[0].offsetWidth/2+'px'
				this.setplace.top=document.getElementsByClassName('img_show')[0].offsetHeight/2+'px'
//				基准坐标
				this.standard.left=document.getElementsByClassName('img_show')[0].offsetWidth/2
				this.standard.top=document.getElementsByClassName('img_show')[0].offsetHeight/2
				
				this.setplace.transform='translateX(-50%)translateY(-50%)'
				this.ts.rotate=0
				this.ts.scale=1
				this.scale=50
			},
//			辅助功能显示隐藏
			assist(txt){
				if(txt=='on'){
					this.assistShow=true
				}
				if(txt=='off'){
					this.assistShow=false
				}
			},
			soll_scale(e){
				if(e.deltaY<0){
					this.scale_size('big')
				}
				if(e.deltaY>0){
					this.scale_size('small')
				}
			}
		},
		watch: {
		    whole: function (newQuestion, oldQuestion) {
		    	this.setplace.left='50%'
				this.setplace.top='50%'
				
		    }
		},
		directives:{
//			拖动自定义指令
            drag(el,bindings){
                el.onmousedown = function(e){
                	e.preventDefault()
					let init_left=e.clientX
					let init_top=e.clientY
                    document.onmousemove = function (el){
                    	e.preventDefault()
                    	let mvLeft=(el.clientX-init_left)
						let mvTop=(el.clientY-init_top)
						
            			bindings.value.set(mvLeft,mvTop)
						
                    	document.getElementsByClassName('img_nav')[0].style.cursor='move'
                    	
                    }
                    document.onmouseup = function(){
                        document.onmousemove = null
                        document.onmouseup = null;
                        
                        bindings.value.rec_set()
                        
                    }
//                  document.getElementsByClassName('img_show')[0].onmouseleave=function(){
//                  	console.log('fsf')
//                      document.onmousemove = null;
//                  }
                }
            }
        }
	}
</script>

<style>
img {
	width: 40px;
	height: 40px;
}
	#img_look{
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background: rgba(0,0,0,.6);
		position: absolute;
		z-index: 600;
	}
	#img_look .img_show{
		width: 100%;
		height: 100%;
		position: relative;
		display: flex;
		justify-content:center;
		transition: all 220ms;
	}
	#img_look .pos{
		position: relative!important;
	}
	#img_look .fix{
		position: fixed!important;
		background: #fff!important;
	}
	#img_look .img_nav{
		background: #fff;
		transition: all 100ms;
		position: absolute;
		width: 800px;
		height:auto;
	}
	#img_look .breviary{
		position: absolute;
		bottom: 10px;
		height: 100px;
		overflow-x: scroll;
		overflow-y: hidden;
		left: 0;
		transition: all 300ms;
	}
	#img_look .te_ce{
		width: 100%;
		text-align: center;
		left: initial!important;
	}
	#img_look .breviary::-webkit-scrollbar {
        display: none;
    }
	#img_look .breimg{
		float: left;
		width:160px;
		height: 100px;
		box-sizing: border-box;
	}
	#img_look .breimg:hover{
		border: 1px solid #ffcc33 ;
	}
	#img_look .pitch{
		border: 1px solid #ffcc33 ;
	}
	#img_look .operation{
		position: absolute;
		bottom: 120px;
		left: 40%;
		width: 300px;
		height: 50px;
		line-height: 60px;
		background: rgba(255,255,255,.7);
		border-radius:20px ;
		transition:all 300ms;
	}
	#img_look .operation i{
		font-size: 30px;
		width: 50px;
		height: 50px;
		float: left;
		display: inline-block;
		text-align: center;
		color: #333;
		cursor: pointer;
	}
	#img_look .operation i:hover{
		color: #0092DD;
	}
	/*关闭*/
	#img_look .close_img{
		width: 60px;
		height: 60px;
		position: absolute;
		right: 0;
		top: 0;
		border-bottom-left-radius: 100%;
		background: radial-gradient(at top right,  rgba(222,222,222,1), rgba(120,120,120,0) 70%);
		transition: all 300ms;
		cursor: pointer;
	}
	#img_look .close_img i{
		float: right;
		margin-right: 20%;
		margin-top: 20%;
		font-size: 20px;
		transition: all 300ms;
		color: #ff3366;
	}
	#img_look .close_img:hover{
		width: 100px;
		height:100px;
	}
	#img_look .close_img:hover i{
		font-size: 30px;
		color: #ff3333;
	}
	/*放大*/
	#img_look .amplification{
		width: 60px;
		height: 60px;
		position: absolute;
		left: 0;
		top: 0;
		border-bottom-right-radius: 100%;
		background: radial-gradient(at top left ,  rgba(222,222,222,1), rgba(120,120,120,0) 70%);
		transition: all 300ms;
		cursor: pointer;
	}
	#img_look .amplification i{
		float: left;
		margin-left: 20%;
		margin-top: 20%;
		font-size: 20px;
		transition: all 300ms;
		color: #333;
	}
	#img_look .amplification:hover{
		width: 100px;
		height:100px;
	}
	#img_look .amplification:hover i{
		font-size: 30px;
		color: #66ffcc;
	}
	/*辅助图片显示隐藏*/
	#img_look .img_hide{
		opacity: 0;
	}
</style>



