<!--
count: NUmeber -总数量
value: Number -选中个数
icon：String -图标类型
color: String -图标字体颜色
-->
<template>
  <span class="ap-rate">
    <span class="ap-rate_up">
      <i :class="icon+'1'" v-for="k in count" :key="k" @click="current(k)"></i>
    </span>
    <span class="ap-rate_down" :style="{'width': (value / count) * 100 + '%', color: color}">
      <i :class="icon" v-for="k in count" :key="k"></i>
    </span>
  </span>
</template>

<script>
  export default {
    data() {
      return {

      }
    },
    props: {
      count: {
        type: Number,
        default: 7
      },
      value: {
        type: Number,
        default: 0
      },
      icon: {
        type: String,
        default: 'icon-aixin'
      },
      color: {
        type: String,
        default: '#333'
      }
    },
    methods: {
      current(k) {
        this.$emit('input', k)
      }
    }
  }

</script>

<style scoped lang="scss">
  .ap-rate {
    position: relative;
    .ap-rate_up {
      position: relative;
      z-index: 60; // 确保回点
      i {
        // font-size: 20px;
        &:hover {
          transform: scale(1.1);
          transition: all .5s
        }
      }
    }

    .ap-rate_down {
      position: absolute;
      display: inline-block;
      z-index: 20;
      overflow: hidden;
      left: 0px;
      top: 0;
      i {
        // font-size: 20px;
      }
    }
  }

</style>



