<template>
  <div>
    <input type="text" v-model="form.name">
    <input type="text" v-model="form.age">
    <custom></custom>
  </div>
</template>

<script>
import custom from './custom';
export default {
  data() {
    return {
      form: {
        name: '',
        age: 23
      }
    }
  },
  components: {
    custom
  },
  mounted() {
    
  }
}
</script>

<style>
  #form {
    width: 200px;
    height: 100px;
    color: #333;
    position: relative;
    z-index: 22;
  }
</style>


