<template>
  <!-- 提供 点击的当前列表@scrollToCurrentItem =scrollToCurrentItem（[index, cIndex, flag], lianxuK）index: '当前板块序列'，cIndex: '板块内item序列'， flag: 'item点击方向'
  , lianxuK： 连续序列 , 当前高亮currentFlag, 导航菜单列表name: listData-->
  <div class="wrap">
    <ul>
      <li v-for="(item, index) in listDataCopy" :key="index" :class="{'show-ed': item.isShow}">
        <div @click="showList(item, index)">
          <i class="iconfont icon-huaban" :style="item.isShow?{transform:'rotate(120deg)'}:''"></i>
          <span :class="{'currentColor': index == currentFlagCopy}">{{item.name}}</span>
        </div>
        <ul>
          <li v-for="(cItem, cIndex) in item.childen" :key="cIndex">
            <span :class="{'currentColor': index == currentFlagCopy && cIndex == currentChildrenFlagCopy}" @click="currentItem(index, cIndex)">{{cItem.name}}</span>
          </li>
        </ul>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        listDataCopy: [],
        currentFlagCopy: null,
        currentChildrenFlagCopy: null,
        toDown: true,
        lianxuK: 0
      }
    },
    props: {
      listData: {
        type: Array,
        default () {
          return [{
              name: '父标题1',
              childen: [
                {
                  name: '子标题2',
                  childen: [{
                      name: '子标题3',
                    },
                    {
                      name: '子标题4',
                    },
                    {
                      name: '子标题5',
                    }
                  ]
                },
                {
                  name: '子标题3',
                }
              ]
            },
            {
              name: '父标题2',
              childen: [{
                  name: '子标题1',
                },
                {
                  name: '子标题2',
                },
                {
                  name: '子标题3',
                }
              ]
            }
          ]
        }
      },
      currentFlag: {
        type: Number,
        default: 0
      },
      currentChildrenFlag: {
        type: Number,
        default: 0
      }
    },
    watch: {
      // 滚动时标记高亮
      currentFlag(n, o) {
        console.log(n, o, '父集序列')
        this.currentFlagCopy = n
        return
      },
      currentChildrenFlag(n, o) {
        console.log(n, o, '子集序列')
        this.currentChildrenFlagCopy = n
        return
      },
      // 返回回调方法==========
      currentFlagCopy(n, o) { 
        // 当前序列（连续）lianxuK 只在点击父节点时增加
        this.lianxuK = 0
        for(let k in this.listDataCopy) {
          if(k < this.currentFlagCopy) {
            this.lianxuK += this.listDataCopy[k].childen.length
          }
        }

        if (n > o) {
          this.$emit('scrollToCurrentItem', [this.currentFlagCopy, this.currentChildrenFlagCopy, true], this.lianxuK)
        } else {
          this.$emit('scrollToCurrentItem', [this.currentFlagCopy, this.currentChildrenFlagCopy, false], this.lianxuK)
        }

      },
      currentChildrenFlagCopy(n, o) {
        if (n > o) {
          this.$emit('scrollToCurrentItem', [this.currentFlagCopy, this.currentChildrenFlagCopy, true], this.lianxuK)
        } else {
          this.$emit('scrollToCurrentItem', [this.currentFlagCopy, this.currentChildrenFlagCopy, false], this.lianxuK)
        }
      }
    },
    methods: {
      showList(item, index) {
        // 主导航高亮
        this.currentFlagCopy = index
        this.listDataCopy[index].isShow = !this.listDataCopy[index].isShow
        this.$forceUpdate()
      },
      currentItem(index, cIndex) {
        this.currentFlagCopy = index
        this.currentChildrenFlagCopy = cIndex
      },
      testGetGoods() {
        this.axios({
          method: "get",
          url: "/testApi", //项目查看
        })
        .then(res => {
          console.lg(res, '所有')
        })
      }
    },
    created() {
      this.testGetGoods()
      // 同下，添加isShow属性 默认全部收缩
      this.listDataCopy = this.listData
      for (let k in this.listDataCopy) {
        this.listDataCopy[k].isShow = false
      }
      // 重赋props值copy 其他方法才能修改这个值 
      this.currentFlagCopy = this.currentFlag
      this.currentChildrenFlagCopy = this.currentChildrenFlag
    }
  }
</script>

<style lang="scss" scoped>
  .wrap {
    width: 156px;
    padding-right: 25px;
    border-right: 1px solid #666;
    position: fixed;
    // top: 20px;
    top: 360px;
    left: 22px;

    >ul {
      list-style: none;

      >li {
        height: 17px;
        overflow: hidden;
        transition: all 1s;

        div {
          i {
            display: inline-block;
            transition: all 1s
          }
        }

        ul {
          padding-left: 30px;

          li {
            color: #666;
          }
        }
      }

      >.show-ed {
        height: 97px;
        transition: all 1s
      }
    }
    .currentColor {
      color: blue!important
    }
  }
</style>


