<template>
  <div id="wrap">
    <div class="nav">
        <slot name="nav"></slot>
    </div>
    <div class="content">
        <slot></slot>
    </div>
</div>
</template>

<script>
  export default {
    data() {
      return {
        num: 0,
        spanNum: 0,

        item:[]
      }
    },
    watch: {
      num(n, o) {
        console.log('num变化' + n, o)
        // this.toItem(this.num)
      }
    },
    props: {
      navIndex: {
        type: Number,
        
      }
    },
    computed: {
      navIndex2() {
        return navIndex
      }
    },
    methods: {
      init() {
        let wrap = document.getElementById('wrap')
        let item = document.getElementsByClassName('item')

        this.item=item;

        this.spanNum = item.length
        let clientH = window.innerHeight;
        wrap.style.height = clientH + "px";
        let that = this
        // 添加导航点击事件
        let spanNode = document.getElementsByClassName('nav')[0].getElementsByTagName('span')
        for(let k = 0; k < spanNode.length; k++) {
          spanNode[k].onclick = function() {
            that.toItem(k)
          }
        }
        // 滚轮事件
        if(window.addEventListener) {
          window.addEventListener('DOMMouseScroll', wheel, false);
        }
        window.onmousewheel = wheel
        function wheel(event) {
          var delta=0;
          if(!event)//for ie
            event=window.event;
          if(event.wheelDelta){//ie,opera
            delta=event.wheelDelta/120;
          }else if(event.detail){
            delta=-event.detail/3;
          }
          if(delta){
            that.handle(delta);
          }
          if(event.preventDefault) event.preventDefault();
          event.returnValue=false;
        }
      },
      toItem(index) {
        let numpx = 100 * index
        let itemY = document.getElementsByClassName('content')[0]
        itemY.style.transform = 'translateY(-' + numpx + '%)'
      },
      handle(delta) {
        let arr=this.item;
        console.log(delta, arr)

        for(var i=0;i<arr.length;i++){
          if(arr[i].id){
            this.num = i;
            console.log(i+ 'sdfsd')
          }
        }

        if(delta > 0){//向上滚动
            this.num--
            
            if(this.num < 0) {
              this.num = 0
            }
            console.log('向上', this.num)

            for(var i=0;i<arr.length;i++){
              arr[i].removeAttribute('id', '1')
            }

            arr[this.num].setAttribute('id', '1')
            this.toItem(this.num)

        }else if(delta<0){//向下滚动
          this.num++

          if(this.num > 4) {
            this.num = 4
          }
          console.log('向下1', this.num)

          for(var i=0;i<arr.length;i++){
            arr[i].removeAttribute('id', '1')
          }

          arr[this.num].setAttribute('id', '1')
          this.toItem(this.num)
        }
        
      }
    },
    mounted() {
      this.init()
    }
  }
</script>

<style scoped lang="scss">
  #wrap {
    position: relative;
    .nav {
      position: fixed;
      top: 50%;
      right: 0;
      transform: translate(-50%, -50%);
      z-index: 22;
      span {
        text-decoration: none;
        display: block;
        color: #fff
      }
    }
    .content {
      font-size: 30px;
      color: #fff;
      height: 100%;
      transition: all 0.6s ease-in-out;
      .item {
        width: 100%;
        height: 100%;
      }
      .item1 {
        background-color: #333
      }
      .item2 {
        background-color: #666
      }
      .item3 {
        background-color: red
      }
      .item4 {
        background-color: blue
      }
      .item5 {
        background-color: green
      }
      .item6 {
        background-color: rgb(51, 6, 6)
      }
    }
  }
</style>


