<template>
    <div class="adjustpowerdiv">
      <div class="adjustpowerdiv-rightcont">
        <span class="adjustpowerdiv-rightcont-title">修改权限</span>        
        <div class="tree_adjust">
        	<adjust :data="data" @permissions="permissions" :upt="upt"></adjust>
        </div>
        <div class="adjustpowerdiv-rightcont-btns">
          <span class="btns-false" @click="guanbi">取消</span>
          <span class="btns-true" @click="getCheckedKeys">确定</span>
        </div>
      </div>
    </div>
</template>
<script>
  import adjust from './tree'
  import {treeJson} from './json';
	
export default {
  data(){
    return{
    	data:[],
    	
    	upt:[],
    	
    }
  },
  components:{
  		adjust
  },
  props:['departuserselect','departidanduserdepartid','projectid'],
  created(){
      this.init(); 
  },
  methods:{
    init(){//修改职位
      this.data = treeJson
      this.selectDefau(this.data)
      this.defauOpen(this.data)
      this.$forceUpdate()

    },
    defauOpen(obj){
      for (var i in obj) {
        	obj[i].open=true
  			this.defauOpen(obj[i].nodes)
  		}
    },
    selectDefau(obj){//初始默认选中的值
  		for (var i in obj) {
  			if(obj[i].ifPrivilege==true){
  				this.upt.push(obj[i].privilegeId)
  			}
  			this.selectDefau(obj[i].nodes)
  		}
  	},
  	permissions(arr,sta,rid,child){//选中时传回的数组
  		let childArr=child||[];
  		if(sta=='add'){
  			if(arr.indexOf(9)!=-1||arr.indexOf(13)!=-1||arr.indexOf(14)!=-1){
				arr.push(9);
				arr.push(13);
				arr.push(14);
  			}
  			this.upt=this.upt.concat(arr)
  			this.upt=this.$utils.distinct(this.upt)	
  		}else{
  			if(arr.indexOf(9)!=-1||arr.indexOf(13)!=-1||arr.indexOf(14)!=-1){
  				this.upt.splice(this.upt.indexOf(9),1)
  				this.upt.splice(this.upt.indexOf(13),1)
  				this.upt.splice(this.upt.indexOf(14),1)
  			}else{
  				for (var i in arr) {
	  				if(this.upt.indexOf(rid)!=-1){
	  					this.upt.splice(this.upt.indexOf(rid),1)
	  				}
  				}
  			}
  			
  			let child=this.$utils.distinct(childArr)
				for (var i in child) {
					if(this.upt.indexOf(child[i])!=-1){
						this.upt.splice(this.upt.indexOf(child[i]),1)
					}
				}
  			this.upt=this.$utils.distinct(this.upt)	
  			
  		}
  	},
    guanbi(){
      this.$emit('guanbi',1)
    },
    getCheckedKeys() {//
		//2019-1-4		
//		this.upt.splice(this.upt.indexOf(5),1)
//		this.upt.splice(this.upt.indexOf(6),1)
      let token = this.$user.get_data('userData').token;
      let departmentId = this.departidanduserdepartid;//全局的departmentID或人员的departmentid
      let params = new URLSearchParams();
      params.append("token", token);
      params.append("privilegeIds", this.upt);
      this.$axios(
        this.$axiosConst.organizationPrivilege2,
        params,
        departmentId
      )
      .then((res) => {
        if(res.data.code==1){
          this.$emit('guanbi',2)
        }else{
          this.$emit('guanbi',1);
          // return this.$message.error(res.data.errorMessage);
        }
      })
      .catch((res) => {
        // return this.$message.error('系统错误');
      })
    },
  }

}
</script>
<style scoped>
ul {
  list-style: none;
}
	.tree_adjust{
		overflow-y: scroll;
		overflow-x: hidden;
		width: inherit;
		height: 85%;
	}
  .adjustpowerdiv{
    position: fixed;
    z-index: 2;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    box-sizing: border-box;
    overflow: hidden;
  }
  .adjustpowerdiv .adjustpowerdiv-rightcont{
    width: 516px;
    height: 100%;
    background: #fff;
    float: right;
    box-sizing: border-box;
    position: relative;
  }
  .adjustpowerdiv .adjustpowerdiv-rightcont .adjustpowerdiv-rightcont-title{
    height: 53px;
    line-height: 53px;
    width: 100%;
    border-bottom: 2px solid #e8e8e8;
    padding-left: 20px;
    margin-bottom: 8px;
    font-weight: bold;
    font-size: 16px;
    color: #1890FF;
  }
  .adjustpowerdiv .adjustpowerdiv-rightcont .adjustpowerdiv-rightcont-container{
    height: 800px;
    overflow-y: auto;
  }
  .adjustpowerdiv .adjustpowerdiv-rightcont .adjustpowerdiv-rightcont-container .adjustpowerdiv-rightcont-ul .adjustpowerdiv-rightcont-ul-li{
    height: 32px;
    line-height: 32px;
    padding: 0 32px;
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    color: rgba(0,0,0,0.65);
  }
  .adjustpowerdiv .adjustpowerdiv-rightcont .adjustpowerdiv-rightcont-container .adjustpowerdiv-rightcont-ul .adjustpowerdiv-rightcont-ul-li>i{
    color: #1890FF;
  }
  .adjustpowerdiv .adjustpowerdiv-rightcont .adjustpowerdiv-rightcont-container .adjustpowerdiv-rightcont-ul .adjustpowerdiv-rightcont-ul-li:hover{
    background: #F2F2F2;
  }
  .adjustpowerdiv .adjustpowerdiv-rightcont .adjustpowerdiv-rightcont-btns{
    width: 100%;
    display: flex;
    justify-content: flex-end;
    position: absolute;
    bottom: 32px;
    right: 32px;
  }
  .adjustpowerdiv .adjustpowerdiv-rightcont .adjustpowerdiv-rightcont-btns .btns-false{
    font-size: 14px;
    height: 32px;
    line-height: 32px;
    width: 65px;
    color: #fff;
    border: 1px solid #1890FF;
    border-radius: 4px;
    background: #1890FF;
    text-align: center;
    margin-right: 8px;
    cursor: pointer;
  }
  .adjustpowerdiv .adjustpowerdiv-rightcont .adjustpowerdiv-rightcont-btns .btns-false:hover{
      background: #66b1ff;
      border-color: #66b1ff;
      color: #fff;
  }
  .adjustpowerdiv .adjustpowerdiv-rightcont .adjustpowerdiv-rightcont-btns .btns-true{
    width: 65px;
    height: 32px;
    line-height: 32px;
    text-align: center;
    border: 1px solid #1890FF;
    font-size: 14px;
    color: #fff;
    border-radius: 4px;
    margin-left: 8px;
    background: #1890FF;
    cursor: pointer;
  }
  .adjustpowerdiv .adjustpowerdiv-rightcont .adjustpowerdiv-rightcont-btns .btns-true:hover{
    background: #66b1ff;
    border-color: #66b1ff;
    color: #fff;
  }
</style>



