								ctx.textAlign = 'center';
								// ctx.fillText(this.time_value(Bararr[i].approveUsers[0].approveTime),step*(_i-1)+x+spacea + step + spacea,y+48);
								ctx.fillText(Bararr[i].approveNodeTime?this.time_value(Bararr[i].approveNodeTime):'',step*_i+x,y+48);//19-4-1
								if(_online){
									if(Bararr[i].approveNodeStatus !=2 && Bararr[i].approveNodeStatus !=5 && Bararr[i].approveNodeStatus !=7 && Bararr[i].approveNodeStatus !=8){
										ctx.beginPath();
										ctx.lineJoin = "round";
										ctx.lineWidth = "2";
										ctx.strokeStyle = '#d8d8d8';
										ctx.moveTo(step*_i+x+spacea*2.5,y);
										ctx.lineTo(step*(_i+1)+x,y);
										ctx.stroke();
									}
								}
							}

						}
					}
				}
      },
      statusa(num){
				if(num == -1){
					return '发起人'
				}else if(num == 1){
					return '待审批'
				}else if(num == 2){
					return '审批通过'
				}else if(num == 3){
					return '审批驳回'
				}else if(num == 4){
					return '待签字'
				}else if(num == 5){
					return '已签字'
				}else if(num == 6){
					return '待盖章'
				}else if(num == 7){
					return '已盖章'
				}else if(num == -2){
					return '再次发起审批'
				}
			}
    }
  }

</script>

