		font-weight: 600;
		
	}
	/*页面内容*/
	.download_advertising{
		margin: 0 auto;
	    height:100%;
	    width:1200px!important;
	    min-height:834px;	
	    margin-top: 112px;
	    overflow: hidden;
	    position: relative;
	}
	.download_advertising h2{
		font-size: 50px;
