		float: left;
		font-family: "微软雅黑";
		font-size: 26px;
		text-decoration: underline;
		text-align: center;
	}
	.dow_download dl dt{
		width: 240px;
		height: 50px;
		box-sizing: border-box;
		border-radius:20px ;
		transition: 300ms;
		position: relative;
		line-height: 50px;
		cursor: pointer;
	} 
	.dow_download dl dt:hover{
		background: rgb(0,100,167);
		background-color: #9bcb4a; 
		background-image: linear-gradient(to top, #0570c1, #1893dd); 
		box-shadow: 0px 0px 30px #555555 , inset 0 1px #20a5e3;
	}
	.dow_download dl dt:before{
		content: " ";
	    display: block;
	    width: 240px;
	    height: 50px;
	    box-sizing: border-box;
	    border-radius:20px ;
	    position: absolute;
	    left: 0;
	    top: 0;

	  
	}
	.dow_download dl dt:after{
		content: " ";
	    display: block;
	    width: 240px;
	    height: 50px;
	    box-sizing: border-box;
	    border-radius:20px ;
	    position: absolute;
	    left: 0;
	    top: 0;
	}
	
	.dow_download dl dt:hover::before{
	    animation: clipAni_before 1s linear;
	    border: 2px solid #ffffff;
	    clip:rect(0px,240px,50px,18px);
	}
	.dow_download dl dt:hover::after{
	    animation: clipAni_after 1s linear;
	    clip:rect(0px,222px,50px,0px);
	    border: 2px solid #ffffff;
	    
	}
	@keyframes clipAni_before{
		0%{clip:rect(0px,14px,2px,12px);}
		50%{clip:rect(0px,240px,2px,18px);}
		100%{ clip:rect(0px,240px,48px,18px);}
		
	}
	@keyframes clipAni_after{
		0%{clip:rect(38px,222px,50px,222px);}
		50%{clip:rect(38px,222px,50px,0px);}
		100%{clip:rect(2px,222px,50px,0px);}
	}
	.dow_download dl dd{
		margin: 0 auto;
		margin-top: 60px;
		width: 200px;
		height: 200px;
	}
