<template>
  <div>
    <span class="check_box" :class="{checked: ifCheck, ban: disabled}"
      @click="selectItem">
      <!-- <i class="iconfont icon-finish"></i> -->
    </span>
    <slot></slot>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        ifCheck: this.value
      }
    },
    props: {
      value: {
        type: Boolean,
        default: false
      },
      disabled: {
        type: Boolean,
        default: false
      }
    },
    computed: {
      ifChecked() {
        return this.value
      }
    },
    created() {
    },
    methods: {
      selectItem() {
        if(this.disabled) return
        this.ifCheck = !this.ifCheck
        // if (value) {
        //   item.ifCheck = -1
        // } else {
        //   item.ifCheck = 1
        // }
        // this.$emit('input')
      }
    }
  }

</script>

<style scoped>
  .check_box {
    position: relative;
    border: 1px solid #409eff;
    display: inline-block;
    width: 19px;
    height: 19px;
    cursor: pointer;
    text-align: center;
    line-height: 16px;
    /* transition: all 300ms; */
    box-sizing: border-box;
    margin-left: 10px;
  }
  .check_box::after {
    box-sizing: content-box;
    content: "";
    border: 1px solid #fff;
    border-left: 0;
    border-top: 0;
    height: 9px;
    left: 6px;
    position: absolute;
    top: 1px;
    transform: rotate(45deg) scaleY(0);
    width: 5px;
    transition: transform .15s ease-in .05s;
    transform-origin: center;
  }
  .check_box i {
    display: inline-block;
    font-size: 12px !important;
    color: #333;
    transform: scale(0);
  }

  .checked {
    background-color: #409eff;
  }
  .checked::after {
    transform: rotate(45deg) scaleY(1);
  }

  .checked i {
    color: #fff;
    transform: scale(1);
    transition: all 0.3s;
  }

  .ban {
    background-color: #edf2fc;
    border-color: #dcdfe6;
  }

  .ban i {
    color: #c0c4cc;
    transform: scale(1);
  }

</style>



