<template>
  <div class="c-clouds">
    <div ref="container" class="c-clouds__spriteContainer"></div>
  </div>
</template>

<script>
import ImageSprite from 'image-sprite'
import { PAGE_WIDTH, PAGE_HEIGHT } from './js/constants'
import Loader from 'resource-loader'
import { IMAGE_URLS } from './js/constants'

function getCloudImages (resources) {
  return new Array(13).fill('').map((item, index) => {
    return resources[`cloud${index}`].data
  })
}

export default {
  imageSprite: null,

  mounted () {
      this.initLoader()
    this.createImageSprite()
  },

  methods: {
      initLoader () {
      let loader = new Loader()

      Object.keys(IMAGE_URLS).forEach(name => {
        loader.add(name, IMAGE_URLS[name])
      })

      loader.onProgress.add(() => {
        this.progress = Math.round(loader.progress)
      })

      loader.onComplete.add(() => {
        setTimeout(() => {
          this.loading = false
        }, 500)
      })

      loader.load()
      window.loader = loader
    },
    createImageSprite () {
      let images = getCloudImages(window.loader.resources)
      let imageSprite = new ImageSprite(this.$refs.container, {
        interval: 80,
        width: PAGE_WIDTH,
        height: PAGE_HEIGHT,
        images: images
      })

      this.$options.imageSprite = imageSprite
    }
  }
}
</script>



