<template>
  <div>
    <table class="table" :border="tabBorder">
      <tr>
        <td>
          <span>第一行第一列</span>
          <span @click="handle">功能</span>
          <div class="operate" :style="{display: isShow?'block':'none'}">
            <span>筛选</span>
          </div>
        </td>
        <td>
          <span>第一行第二列</span>
        </td>
        <td>
          <span>第一行第三列</span>
        </td>
      </tr>
      <tr>
        <td>
          <span>第二行第一列</span>
        </td>
        <td>
          <span>第二行第二列</span>
        </td>
        <td>
          <span>第二行第三列</span>
        </td>
      </tr>
      <tr>
        <td>
          <span>第二行第一列</span>
        </td>
        <td>
          <span>第二行第二列</span>
        </td>
        <td>
          <span>第二行第三列</span>
        </td>
      </tr>
      <tr>
        <td>
          <span>第二行第一列</span>
        </td>
        <td>
          <span>第二行第二列</span>
        </td>
        <td>
          <span>第二行第三列</span>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        tabBorder: 1,
        isShow: false
      }
    },
    methods: {
      handle() {
        this.isShow = !this.isShow
      }
    }
  }

</script>

<style scoped lang="scss">
  .table {
    width: 1000px;
    // border: 1px solid red;
    margin: 0 auto;
    border-spacing: 0;
    tr {
      // border-bottom: 1px solid blue;
      td {
        padding: 0;
        height: 50px;
        position: relative;
      }
      &:hover {
        // background-color: red
      }
    }

    .operate {
      height: 200px;
      border: 1px solid red;
      position: absolute;
      width: 100%;
      background-color: beige;
      z-index: 2;
    }
  }

</style>



