<template>
  <!-- 提供 
  点击的当前列表@scrollToCurrentItem =scrollToCurrentItem（[index, cIndex, flag], lianxuK）
  参数： index: '当前板块序列'，cIndex: '板块item序列'， flag: 'item点击方向', lianxuK： 连续序列 
  距离顶部高度： topHeight：（Number）
  当前高亮currentHighlight：【'父级序列'，'子级序列'】, 
  导航菜单列表name: listData, 
  返回子菜单个数数组： @getChildNmArr

  备注：item.childen.length != 1 子项长度为1时 不展开， 隐藏当前列表(子菜单没有时也需要默认一个)
  -->
  <div class="wrap">
    <ul>
      <li v-for="(item, index) in listDataCopy" :key="index" :class="{'show-ed': item.isShow}" :style="{'height': initHeight}">
        <div @click="showList(item, index)" class="currentLi">
          <i class="iconfont icon-huaban" :style="item.isShow?{transform:'rotate(120deg)'}:''"></i>
          <span :class="{'currentColor': index == currentFlagCopy}">{{item.name}}</span>
        </div>
        <ul v-if="item.childen.length != 1">
          <li v-for="(cItem, cIndex) in item.childen" :key="cIndex">
            <span :class="{'currentColor': index == currentFlagCopy && cIndex == currentChildrenFlagCopy}" @click="currentItem(index, cIndex)">{{cItem.name}}</span>
          </li>
        </ul>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        listDataCopy: [],
        currentFlagCopy: 0,
        currentChildrenFlagCopy: 0,
        toDown: true,
        lianxuK: 0,
        flagNum: 0,
        liHeight: 0,  //展开高度
        initHeight: '21px' // 初始收缩高度
      }
    },
    props: {
      listData: {
        type: Array,
        default () {
          return [{
              name: '父标题1',
              childen: [{
                  name: '子标题1',
                },
                {
                  name: '子标题2',
                },
                {
                  name: '子标题3',
                }
              ]
            },
            {
              name: '父标题2',
              childen: [{
                  name: '子标题1',
                },
                {
                  name: '子标题2',
                },
                {
                  name: '子标题3',
                }
              ]
            }
          ]
        }
      },
      currentHighlight: {
        type: Array,
        default() {
          return [0, 0]
        }
      },
      eleNodeArray: {
        type: HTMLCollection
      },
      topHeight: {
        type: Number,
        default: 300
      }
    },
    watch: {
      // 滚动时标记高亮
      currentHighlight(n, o) {
        this.currentFlagCopy = n[0]
        this.currentChildrenFlagCopy = n[1]
      }
    },
    methods: {
      showList(item, index) {
      
        // 主导航高亮
        this.currentFlagCopy = index

        // 重置全局变量 为子集第一项 （防止当前父级的子集项条数少于上次点击时所保存的 全局变量currentChildrenFlagCopy条数 而报错）
        this.currentChildrenFlagCopy = 0

        if(!this.listDataCopy[index].isShow) {
          this.lianxuK = 0
          for(let k in this.listDataCopy) {
            if(this.listDataCopy[k].childen) { //确定有子项
              if(k < this.currentFlagCopy) {
                this.lianxuK += this.listDataCopy[k].childen.length
              }
            }
          }

          let elTop
          if(this.eleNodeArray) {
            let el = this.eleNodeArray[this.currentChildrenFlagCopy + this.lianxuK]
            elTop = this.topHeight + el.offsetTop;
          } else {
            elTop = this.topHeight
          }
          

          if (this.lianxuK + index > this.flagNum) {
            this.$emit('scrollToCurrentItem', [this.currentFlagCopy, this.currentChildrenFlagCopy, true], this.lianxuK, elTop)
          } else {
            this.$emit('scrollToCurrentItem', [this.currentFlagCopy, this.currentChildrenFlagCopy, false], this.lianxuK, elTop)
          }

          this.flagNum = this.lianxuK + this.currentChildrenFlagCopy
        }

        this.listDataCopy[index].isShow = !this.listDataCopy[index].isShow

        // 处理没有子项时（需设置一项，一直为收缩效果，不展开）
        if(item.childen.length == 1) {
          this.listDataCopy[index].isShow = false
        }
        // 获取, 设置展开列表项高度
        if(item.childen.length != 1) {
          let cNode = document.getElementsByClassName('currentLi')
          if(this.listDataCopy[index].isShow) {
            cNode[index].parentNode.style.height = `${(item.childen.length + 1) * 21 + (item.childen.length + 1) * 8}px` 
          } else {
            cNode[index].parentNode.style.height = '21px'
          }
        }

        
        this.$forceUpdate()
        
      },
      currentItem(index, cIndex) {
        this.currentFlagCopy = index
        this.currentChildrenFlagCopy = cIndex

        this.lianxuK = 0
        for(let k in this.listDataCopy) {
          
          if(this.listDataCopy[k].childen) { //确定有子项
            if(k < this.currentFlagCopy) {
              this.lianxuK += this.listDataCopy[k].childen.length
            }
          }
        }

        let elTop
        if(this.eleNodeArray) {
          let el = this.eleNodeArray[this.currentChildrenFlagCopy + this.lianxuK]
          elTop = this.topHeight + el.offsetTop;
        } else {
          elTop = this.topHeight
        }
        
        if (this.lianxuK + cIndex > this.flagNum) {
          this.$emit('scrollToCurrentItem', [this.currentFlagCopy, this.currentChildrenFlagCopy, true], this.lianxuK, elTop)
        } else {
          this.$emit('scrollToCurrentItem', [this.currentFlagCopy, this.currentChildrenFlagCopy, false], this.lianxuK, elTop)
        }

        this.flagNum = this.lianxuK + this.currentChildrenFlagCopy
      }
    },
    mounted() {
      // 返回子菜单个数 数组
      let cArr = []
      for (let k in this.listData) {
        cArr.push(this.listData[k].childen.length)
      }
      this.$emit('getChildNmArr', cArr)
    },
    created() {
      // 同下，添加isShow属性 默认全部收缩
      this.listDataCopy = this.listData
      for (let k in this.listDataCopy) {
        this.listDataCopy[k].isShow = false
      }
      // 重赋props值copy 其他方法才能修改这个值 
      this.currentFlagCopy = this.currentHighlight[0]
      this.currentChildrenFlagCopy = this.currentHighlight[1]
    }
  }
</script>

<style lang="scss" scoped>
  .wrap {
    width: 156px;
    padding-right: 25px;
    border-right: 1px solid #666;
    position: fixed;
    // top: 20px;
    top: 360px;
    left: 22px;

    >ul {
      list-style: none;
      font-size: 16px;

      >li {
        height: 21px;
        overflow: hidden;
        margin-top: 8px;
        transition: height 1s;

        div {
          i {
            display: inline-block;
            transition: all 1s
          }
        }

        ul {
          padding-left: 30px;

          li {
            color: #666;
            margin-top: 8px;
          }
        }
      }

      >.show-ed {
        font-size: 16px;
        transition: all 1s
      }
    }
    .currentColor {
      color: blue!important
    }
  }
</style>


