<template>
  <div>
    <span @click="getMap">地图</span>
    <div class="nav_list_step">
      <div class="stepAtit">
        <div class="step_nav">
          <div class="step_mod  ">
            <span class="circle">
              <i class="glyphicon glyphicon-ok"></i>
              <b>1</b>
            </span>
            <span class="txt_span">项目决策</span>
          </div>
          <div class="step_mod">
            <span class="circle">
              <i class="glyphicon glyphicon-ok"></i>
              <b>2</b>
            </span>
            <span class="txt_span">项目管理</span>
            <span class="henxian"></span>
          </div>
          <div class="step_mod">
            <span class="circle">
              <i class="glyphicon glyphicon-ok"></i>
              <b>3</b>
            </span>
            <span class="txt_span">产出指标</span>
            <span class="henxian"></span>
          </div>
          <div class="step_mod">
            <span class="circle">
              <i class="glyphicon glyphicon-ok"></i>
              <b>4</b>
            </span>
            <span class="txt_span">效益指标</span>
            <span class="henxian"></span>
          </div>
          <div class="step_mod">
            <span class="circle">
              <i class="glyphicon glyphicon-ok"></i>
              <b>5</b>
            </span>
            <span class="txt_span">满意度指标</span>
            <span class="henxian"></span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        aa: {
          bb: []
        }
      }
    },
    created() {
      
    },
    methods: {
      getMap() {
        this.aa.bb = [3,4]
        console.log(this.aa.bb, '胜多负少')
      }
    }
  }

</script>

<style scoped lang="scss">
  .nav_list_step {
    .stepAtit {
      width: 1000px;
      height: 100px;
      margin: 0 auto;
      margin-top: 20px;
      margin-bottom: 20px;

      .step_nav {
        float: left;
        width: 950px;
        height: 100px;

        .step_mod {
          width: 100px;
          height: 100px;
          margin-left: 90px;
          background: #fff;
          float: left;
          position: relative;
          cursor: pointer;

          span {
            display: inline-block;
          }

          .circle {
            width: 30px;
            height: 30px;
            border-radius: 50%;
            margin: 15px 35px;
            text-align: center;
            line-height: 30px;
            border: 1px solid rgb(153, 153, 153);
            color: rgb(153, 153, 153);

            i {
              display: none;
            }

            b {
              display: inline-block;
            }
          }

          .txt_span {
            font-family: "microsoft yahei";
            width: 100%;
            height: 16px;
            text-align: center;
            color: rgb(153, 153, 153);
          }

          .henxian {
            width: 130px;
            height: 1px;
            border: 1px solid rgb(153, 153, 153);
            position: absolute;
            top: 29%;
            left: -110px;
          }
        }

        .selected-step {
          border-bottom: 2px solid #eaba19;

          .circle {
            border: 1px solid #eaba19;
            color: #eaba19;

            b {
              display: none;
            }

            i {
              display: inline-block;
            }
          }

          .txt_span {
            color: #eaba19;
          }

          .henxian {
            border: 1px solid #eaba19;
          }
        }

        .pass-step {
          .circle {
            background: #eaba19 !important;
            border: 1px solid #eaba19;
            color: #fff !important;

            b {
              display: none;
            }

            i {
              display: inline-block;
            }
          }

          .txt_span {
            color: #666;
          }

          .henxian {
            border: 1px solid #eaba19;
          }
        }
      }
    }
  }

</style>



