<template>
  <div class="ap-row-wrap">
    <slot></slot>
  </div>
</template>

<script>
  export default {
    data() {
      return {

      }
    },
    props: {
      gutter: {
        type: Number,
        default: 0
      },
      span: {
        type: Number,
        default: 4
      }
    },
    watch: {
      gutter(n, o) {
        this.add()
      }
    },
    methods: {
      add() {
        let childArr = []
        let doc = document.getElementsByClassName('ap-row-wrap')
        for(let k = 0; k < doc.length; k++) {
          doc[k].style.marginLeft = `${-(this.gutter / 2)}px`
          doc[k].style.marginRight = `${-(this.gutter / 2)}px`
          childArr.push(document.getElementsByClassName('ap-row-wrap')[k].children)
        }
        for(let k = 0; k < childArr.length; k++) {
          // 非适配计算
          // cd[k][t].style.width = `${(this.span / 12) * 100}%`
          for(let t = 0; t < childArr[k].length; t++) {
            childArr[k][t].style.paddingLeft = `${this.gutter / 2}px`
            childArr[k][t].style.paddingRight = `${this.gutter / 2}px`
            childArr[k][t].style.marginBottom = `${this.gutter}px`
          }
        }
      }
    },
    mounted() {
      this.add()
    }
  }
</script>

<style scoped>
  .ap-row-wrap {
    overflow: hidden;
    clear: both;
  }
</style>


