<template>
  <div class="switch" :class="{'is_checked': isChecked}">
    <span class="switch__core" @click="checked"></span>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        isChecked: false
      }
    },
    methods: {
      checked() {
        this.isChecked = !this.isChecked
        this.$emit('change', this.isChecked)
      }
    }
  }

</script>

<style scoped lang="scss">
  .switch {
    margin: 20px 20px 20px 0;
    .switch__core {
      margin: 0;
      display: inline-block;
      position: relative;
      width: 40px;
      height: 20px;
      border-color: rgb(255, 73, 73);
      background-color: rgb(255, 73, 73);
      border: 1px solid #dcdfe6;
      outline: none;
      border-radius: 10px;
      box-sizing: border-box;
      background: #dcdfe6;
      cursor: pointer;
      transition: border-color .3s,background-color .3s;
      &::after {
        content: "";
        position: absolute;
        top: 1px;
        left: 1px;
        border-radius: 100%;
        transition: all .3s;
        width: 16px;
        height: 16px;
        background-color: #fff;
      }
    }
  }
  .is_checked {
    .switch__core {
      border-color: red;
      background-color: red;
      transition: border-color .3s,background-color .3s;
      &::after {
        left: 100%;
        margin-left: -17px;
        transition: all .3s;
      }
    }
  }

</style>



