<!--
说明：
==. 基本
表格标题- :titles Array [{name: '', width: ''}]
表格数据- :tableData Array
请求地址：url String

回调方法：@selection-change （所有选中项item）
默认项：morenIdArr []
禁选项：disabledIdArr []
是否允许拖拽- :draggable="true / false"
拖拽回调：@dragend(res) => res: id序列号 {id: index, '12': 0, '13': 1}
==. 
是否内置搜索：:filter Blooen(false)
筛选条件回调：@filterCondition
-->
<template>
  <div>
    <div class="filter_box" v-if="isFilter">
    <div>
      <div>
        <span>下拉选择条件：</span>
        <!-- <a-select showSearch v-model="gongzongDefault" style="width: 120px"
          @change="handleChangeGZ" :filterOption="filterOption">
          <a-select-option :value="item.workTypeId" v-for="(item, index) in gongzongLists" :key="index">{{item.workTypeName}}
          </a-select-option>
        </a-select> -->
      </div>
    </div>
    <div>
      <input placeholder="搜索姓名或身份证号" style="width: 200px" v-model="fitlterData.likeStr" @change="resetData" />
      <button @search="onSearch">点击搜索</button>
    </div>
  </div>
    <div class="ap-table">
    <div class="table-header">
      <p style="width: 16%;display: flex;justify-content: center;">
        <span class="check_box" :class="{checked: ifCheckAll}" @click="selectAllItem">
          <i class="iconfont icon-finish"></i>
        </span>
      </p>
      <p :style="{'width': item.width}" v-for="(item, index) in titles" :key="index">{{item.name}}</p>
    </div>
    <div class="table-list" v-for="(list, index) of tableData" :key="index" draggable="true" 
    @dragstart="dragstart(index, list)" 
    @dragenter="dragenter(index, list)" 
    @dragend="dragend(index, list)">
      <p style="width: 16%;display: flex;justify-content: center;">
        <span class="check_box" :class="{checked: list.ifCheck==1, ban: list.ifCheck == -2}"
          @click="selectItem(list, index, tableData)">
          <i class="iconfont icon-finish"></i>
        </span>
      </p>
      <p style="width: 28%">{{list.departmentName?list.departmentName:'无'}}</p>
      <p style="width: 28%">{{list.departmentUserCount?list.departmentUserCount:'无'}}</p>
      <p style="width: 28%">{{111}}{{index}}</p>
    </div>
  </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        checkedAllMsg: [], // 所有选中列表信息
        ifCheckAll: false,
        num: 0, // 选择项数
        oldNum: 0, // 拖拽
        newNum: 0,
        oldItem: {},
        fitlterData: { // 提交数据
          startTime: '',
          endTime: '',
          companyId: -1,
          teamId: null,
          workId: null,
          likeStr: '',
          flag: this.flag
        },
      }
    },
    props: {
      titles: {
        type: Array,
        default: () => [{
          name: '职位名称',
          width: '28%'
        }, {
          name: '人员数',
          width: '28%'
        }, {
          name: '操作',
          width: '28%'
        }]
      },
      tableData: {
        type: Array,
        default: () => [{
            name: '默认行一'
        }, {
            name: '默认行二'
        }, {
            name: '默认三'
        }]
      },
      isFilter: {
        type: Boolean,
        default: true
      }
    },
    methods: {
      init() {
        // this.getOptions() // 获取下拉项
        this.chuliNull()
      },
      reset() { // 切换页面时 重置当前选择项
        // this.danweiDefault = this.danweiLists[0].companyName // 处理下拉框默认显示，数据
        // this.fitlterData.companyId = this.danweiLists[0].companyId
        this.fitlterData.likeStr = ''
        this.chuliNull()
      },
      resetData(e){
        const { value } = e.target
        if(!value) {
          this.fitlterData.likeStr = ''
          this.chuliNull()
        }
      },
      onSearch(val) {
        this.chuliNull()
      },
      chuliNull() {
        if(this.fitlterData.companyId == -1) this.fitlterData.companyId = null // 特殊情况 根据后台要求
        this.$emit('filterCondition',this.fitlterData)
      },
      //设置默认项...
      //
      selectAllItem() {
        this.ifCheckAll = !this.ifCheckAll
        if(this.ifCheckAll) {
          for(let k in this.tableData) {
            this.tableData[k].ifCheck = 1
            this.checkedAllMsg.push(this.tableData[k])
          }
        } else {
          for(let k in this.tableData) {
            this.tableData[k].ifCheck = -1
            this.checkedAllMsg = []
          }
        }
        this.$emit('selection-change', this.checkedAllMsg)
      },
      selectItem(item, index, listData) {
        this.checkedAllMsg = [] // 备注：多个组件时置空旧数据 确保为当前组件数据
        if (item.ifCheck == -2) { // -2 禁选
          return
        }
        if (this.typeChoose == 'radio') {
          // 单选
        } else {
          // 复选
          if (item.ifCheck == 1) {
            item.ifCheck = -1
            this.checkedAllMsg.splice(index, 1)
          } else {
            item.ifCheck = 1
            // this.checkedAllMsg.push(item) // 不在这儿处理添加
          }

          // if (this.checkedName.length > this.allowMaxNum) {
          //   k.ifCheck = -1
          //   this.checkedName.splice(this.checkedName.indexOf(k.userName), 1)
          //   this.checkedId.splice(this.checkedId.indexOf(k.userId), 1)

          //   this.$message.warning('最大允许选择' + this.allowMaxNum + '项')
          // }
          // this.choseNum = this.checkedName.length
        }
        // 获取所有选中项: 即遍历所有ifCheck为1的item ( + 进入默认的) 故在这儿统一操作
        for(let k in listData) {
          if(listData[k].ifCheck == 1) {
            this.checkedAllMsg.push(listData[k])
          }
        }
        // 子集反选效果
        if(this.checkedAllMsg.length == listData.length) {
          this.ifCheckAll = true
        } else {
          this.ifCheckAll = false
        }
        this.num = this.checkedAllMsg.length
        this.$forceUpdate()
        this.$emit('selection-change', this.checkedAllMsg)
      },
      dragstart(index, item) {
        this.oldNum = index
      },
      // 拖拽
      dragend() {
        if (this.oldNum != this.newNum) {
          this.oldItem = this.tableData[this.oldNum];
          this.tableData.splice(this.oldNum, 1);
          this.tableData.splice(this.newNum, 0, this.oldItem);
          let paramsArr = []
          this.tableData.filter(item => {
            paramsArr.push(item.departmentId)
          })
          let paramsObj = {}
          paramsArr.forEach((item, index, arr) => {
            paramsObj[item] = index
          })
          console.log(paramsObj, 'paramsObj')
          this.$emit('dragend', paramsObj)
        }
      },
      dragenter(index) {
        this.newNum = index
      }
    }
  }

</script>

<style lang="scss" scoped>
  .ap-table {
    .table-header {
      display: flex;
      align-items: center;
      background-color: rgb(250, 250, 250);
      padding: 12px 0;
      p {
        font-size: 14px;
        font-weight: bolder;
        text-align: center
      }
    }
    .table-list {
      display: flex;
      align-items: center;
      padding: 12px 0;
      border-bottom: 1px solid #EBEEF5;
      &:hover {
        background-color: #5e5e5e;
      }
      p {
        font-size: 14px;
        text-align: center
      }
    }
    .table-list:hover {
      background-color: rgba(245, 247, 250, 1)
    }
    .check_box {
      border: 1px solid #409eff;
      display: inline-block;
      width: 14px;
      height: 14px;
      cursor: pointer;
      text-align: center;
      line-height: 12px;
      // transition: all 300ms;
      box-sizing: border-box;
      margin-left: 10px;
    }

    .check_box i {
      display: inline-block;
      font-size: 12px !important;
      color: #333;
      transform: scale(0);
    }

    .checked {
      background-color: #409eff;
    }

    .checked i {
      color: #fff;
      transform: scale(1);
      transition: all 0.3s;
    }

    .ban {
      background-color: #edf2fc;
      border-color: #dcdfe6;
    }

    .ban i {
      color: #c0c4cc;
      transform: scale(1);
    }
  }
</style>



