<template>
  <div id="project">
    <div class="left">
      <span>侧边栏</span>
      <span @click="lang(1)">中文</span>
      <span @click="lang(2)">英文</span>
      <router-link  to="/index">返回登录页</router-link>
      <h1 style="font-size: 16px; text-align: center;">{{ $t("message.hello") }}</h1>
      <side :togId="togId"></side>
    </div>
    <div class="right">
      <div class="nav">
        <ap-crumbs></ap-crumbs>
      </div>
      <div class="content">
        <router-view></router-view>
      </div>
    </div>
    
  </div>
</template>

<script>
  import apTree from './../../../xp/components/ap_tree/ap_tree'
  import side from './../navigation/side'
  import apCrumbs from './../navigation/ap_crumbs'
  export default {
    data() {
      return {
        crumbs_list: [],
        sideList: this.$store.state.page.items, //所有页面导航
        toPath: "", //当前跳转到的路由
        togId:-1
      }
    },
    created() {
      this.$forceUpdate()
      this.toPath = this.$store.state.page.HomeRoute
      this.pageTag()
    },
    components: {
      apTree,
      side,
      apCrumbs
    },
    watch: {
      $route(to, from) {
        this.toPath = to.path
        this.pageTag()
      },
    },
    methods: {
      lang(num) {
        console.log(num, '语言')
        if(num == 1) {
          this.$i18n.locale = 'zh'
        }
        if(num == 2) {
          this.$i18n.locale = 'en'
        }
      },
      pageTag(){
        let arr = this.sideOpen(this.sideList)
        if (arr != undefined) {
          this.treeOpen = this.crumbs_list = arr.reverse()
          this.$user.saveData("crumbsList", this.crumbs_list)

          this.openTree(this.sideList, 0)
        }
    	},
    	
      sideOpen(list) {
        for (var i in list) {
          if (list[i].router == this.toPath) {
            this.togId=list[i].id
            if(list[i].child.length!=0)
              list[i].hide = 11
            let arr = [];
            arr.push(list[i]);
            return arr;
          } else {
            if (this.sideOpen(list[i].child) != undefined) {
              let arr = this.sideOpen(list[i].child);
              arr.push(list[i])
              return arr;
            }
          }
        }
        // console.log(arr, '打开队列')
      },

      openTree(list, val) {
        for (var i in list) {
          if (list[i].router != this.treeOpen[this.treeOpen.length - 1].router) {
            // console.log(list[i].router, this.treeOpen[val].router, list[i].router == this.treeOpen[val].router)
            if (list[i].router == this.treeOpen[val].router) {

              // if(list[i].hide = 11) { // 新改。。。
              //   list[i].hide = 10
              // } else {
              //   list[i].hide = 11
              // }

              list[i].hide = 11
							
              if (list[i].child[0].name == '企业名片') { //特殊路由处理（项目列表
                list[i].child[0].hide = 11
              }


              if (list[i].child[0].name == '项目列表') { //特殊路由处理（项目列表
                list[i].child[0].hide = 10
              } else {
                list[i].child[0].hide = 11
              }
							
	
              if (list[i].child.length != 0) {
                this.openTree(list[i].child, val + 1)
              }
            } else {
              list[i].hide = 10  // 关闭上次所有打开的
            }
          }

        }
      }
    }
  }

</script>

<style>
  #project {
    width: 88%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between
  }

  #project .left {
    width: 30%;
    height: 650px;
    border: 1px solid red;
    overflow: scroll;
  }

  #project .right {
    width: 70%;
    height: 650px;
  }

  #project .right .nav {
    border: 1px solid green;
    margin-bottom: 20px;
    box-sizing: border-box
  }

  #project .right .content {
    padding-left: 40px;
    width: 100%;
    box-sizing: border-box;
    height: 620px;
    border: 1px solid black;
  }

</style>



