<template>
  <div>
    <div>
      <span v-for="(label, i) in labels" :key="i" :class="{'active': i == activeIndex}">{{label}}</span>
    </div>
    <slot></slot>
  </div>
</template>

<script>
export default {
  data() {
    return {
      labels: []
    }
  },
  props: {
    activeIndex: {
      type: Number,
      default: 0
    }
  },
  mounted() {
    console.log(this.$children);
    for(let i = 0; i < this.$children.length; i ++) {
      this.labels.push(Number(this.$children[i].label))
    }
  }
}
</script>

<style>
  .active {
    color: red
  }
</style>


