<!-- 
进度条
-->
<template>
  <div class="wrap">
    <div class="progress" ref="pro"></div>
    <span>{{percent}}%</span>
  </div>
</template>

<script>
  export default {
    data() {
      return {
      }
    },
    props: ['percent', 'time'],
    watch: {
      percent(n, o) {
        this.percent = n
        let t = 500 * n / 100
        this.$refs.pro.style.borderLeftWidth = `${t}px`
      },
      time(n, o) {
        this.$refs.pro.style.transition = `${n}s`
      }
    }
  }

</script>

<style scoped lang="scss">
  .wrap {
    .progress {
      box-sizing: border-box;
      width: 500px;
      height: 10px;
      background-color: #999;
      margin: 0 auto;
      border-radius: 5px;
      border-left: 0px solid red;
      float: left;
      margin-right: 10px;
      margin-top: 3px;
    }
    span {
      clear: both;
    }
  }

</style>



