import Vue from 'vue'
import Router from 'vue-router'
import NProgress from 'nprogress' // progress bar
import 'nprogress/nprogress.css'// progress bar style

import Home from '@/components/home'
import page1 from '@/components/page1'
import page2 from '@/components/page2'
import treePanel from '@/components/tree_panel/index'
import welcome from '@/components/welcome'

// 设备
import pcIndex from '@/components/pcIndex'
// 入口
import entrance from '@/components/entrance/entrance'
import login from '@/components/entrance/login'
import register from '@/components/entrance/register'
// 测试页面
import table2 from '../../xp/components/table2'

// 主体页面
import projectSubject from '@/components/project_subject/projectSubject'

// 用户权限可见
import administrative from '@/components/project_subject/admin_istrative/adminIstrative'
import projectDistribution from '@/components/project_subject/admin_istrative/projectDistribution'
import investmentProgress from '@/components/project_subject/admin_istrative/investmentProgress'
import qsMark from '@/components/project_subject/admin_istrative/qsMark'

// 平台管理者权限可见
import projectManagement from '@/components/project_subject/project_management/projectManagement'
import sc from '@/components/project_subject/project_management/sichuan/index'
import chengdu from '@/components/project_subject/project_management/sichuan/chengdu'
import deyang from '@/components/project_subject/project_management/sichuan/deyang'
import gz from '@/components/project_subject/project_management/guangzhou/index'

// 公共模板 捐赠
import myApproval from '@/components/project_subject/my_approval/myApproval'
import effectIndex from '@/components/project_subject/my_approval/effect_plugs/index'
import jsx from '@/components/project_subject/my_approval/jsx'
import map from '@/components/project_subject/my_approval/map'
import charts from '@/components/project_subject/my_approval/charts'

// 兴趣
import tasteIndex from '@/components/project_subject/taste/index'
import jiankang from '@/components/project_subject/taste/jiankang'
import music from '@/components/project_subject/taste/music'
import history from '@/components/project_subject/taste/history'
import year from '@/components/project_subject/taste/year'
import licai from '@/components/project_subject/taste/licai'
import cognition from '@/components/project_subject/taste/cognition'

// apUI
import apUiIndex from '@/apUI/index'

Vue.use(Router)

const router = new Router({
  routes: [{ // UI 组件
      path: '/',
      name: 'apUiIndex',
      component: apUiIndex,
      // redirect: '/drawer',
      // children: [{
      //   path: '/drawer',
      //   name: 'drawer',
      //   component: drawer
      // }, {
      //   path: '/tabs',
      //   name: 'tabs',
      //   component: tabs
      // }]
    },
    {
      path: '/page1',
      name: 'page1',
      component: page1
    },
    {
      path: '/table2',
      name: 'table2',
      component: table2
    },
    // 正式项目====
    {
      path: '/index',
      name: 'index',
      component: pcIndex,
      redirect: '/index/entrance',
      children: [{ // 登录注册
          path: '/index/entrance',
          name: 'entrance',
          component: entrance,
          redirect: '/index/entrance/login',
          children: [{
              path: '/index/entrance/login',
              name: 'login',
              component: login,
            },
            {
              path: '/index/entrance/register',
              name: 'register',
              component: register,
            },
            // 修改密码
            // 提示
          ]
        },
        { // 主体页面
          path: '/index/projectsubject',
          name: 'projectSubject',
          component: projectSubject,
          redirect: '/index/projectsubject/projectManagement',
          children: [
            // { // 预留未加入

            // },
            { // 用户权限可见 模块一
              path: '/index/projectsubject/administrative',
              name: 'administrative',
              component: administrative,
              redirect: '/index/projectsubject/administrative/projectDistribution',
              children: [{ //项目分布
                  path: '/index/projectsubject/administrative/projectDistribution',
                  name: 'projectDistribution',
                  component: projectDistribution,
                },
                { //投资进度
                  path: '/index/projectsubject/administrative/InvestmentProgress',
                  name: 'investmentProgress',
                  component: investmentProgress,
                },
                { //质量安全
                  path: '/index/projectsubject/administrative/QSMark',
                  name: 'qsMark',
                  component: qsMark,
                },
              ]
            },
            { // 平台权限可见 模块二
              path: '/index/projectsubject/projectManagement',
              name: 'projectManagement',
              component: projectManagement,
              redirect: '/index/projectsubject/projectManagement/sc',
              children: [
                { // 项目管理 =》项目列表 模块二 1
                  path: '/index/projectsubject/projectManagement/sc',
                  name: 'sc',
                  component: sc,
                  redirect: '/index/projectsubject/projectManagement/sc/chengdu',
                  children: [
                    {//项目列表
                      path: '/index/projectsubject/projectManagement/sc/chengdu',
                      name: 'chengdu',
                      component: chengdu
                    },
                    { // 德阳数据列表
                      path: '/index/projectsubject/projectManagement/sc/deyang',
                      name: 'deyang',
                      component: deyang
                    },
                  ]
                },
                {
                  path: '/index/projectsubject/projectManagement/gz',
                  name: 'gz',
                  component: gz,
                },
                { // 项目管理 =》我的审批 模块二 2
                  path: '/index/projectsubject/projectManagement/myApproval',
                  name: 'myApproval',
                  component: myApproval,
                  redirect: '/index/projectsubject/projectManagement/myApproval/jsx',
                  children: [{ //文件审批
                      path: '/index/projectsubject/projectManagement/myApproval/effect_plugs/index',
                      name: 'effectIndex',
                      component: effectIndex
                    },
                    {
                      path: '/index/projectsubject/projectManagement/myApproval/jsx',
                      name: 'jsx',
                      component: jsx
                    },
                    {
                      path: '/index/projectsubject/projectManagement/myApproval/map',
                      name: 'map',
                      component: map,
                    },
                    { //数据图表
                      path: '/index/projectsubject/projectManagement/myApproval/charts',
                      name: 'charts',
                      component: charts,
                  }]
                }
              ]
            },
            { // 兴趣
              path: '/index/projectsubject/taste/index',
              name: 'tasteIndex',
              component: tasteIndex,
              redirect: '/index/projectsubject/taste/jiankang',
              children: [{
                path: '/index/projectsubject/taste/jiankang',
                name: 'jiankang',
                component: jiankang,
              }, {
                path: '/index/projectsubject/taste/music',
                name: 'music',
                component: music,
              }, {
                path: '/index/projectsubject/taste/history',
                name: 'history',
                component: history,
              }, {
                path: '/index/projectsubject/taste/year',
                name: 'year',
                component: year,
              }, {
                path: '/index/projectsubject/taste/licai',
                name: 'licai',
                component: licai,
              }, {
                path: '/index/projectsubject/taste/cognition',
                name: 'cognition',
                component: cognition,
              }]
            }
          ]
        }
      ]
    }
  ]
})
// 添加路由守卫
router.beforeEach((to, from, next) => {
  NProgress.start()
  next()
  // if (to.path == "/login") {
  //     next();
  //     NProgress.done()
  // } else {
  //     isLogin ? next() : next("/login");
  //     NProgress.done()
  // }
})
router.afterEach(() => {
  NProgress.done()
})

export default router



