<template>
  <div>
    <div v-for="(item,index) in imgfiles" :key="index"
        style="display:inline-block;position:relative;width:128px;margin-left:16px;height:72px;vertical-align: bottom;margin-top:8px;">
        <img :src="item.picUrl" alt="fengcai" style="width:100%;height:100%">
    </div>
    <!-- 引用el的dialog弹框组件，默认data中设置croppaVisible=true -->
    <div>
      <span>上传图片</span>
      <input type="file" accept="image/*" @change="addmsg1">
    </div>
    <div v-if="croppaVisible" width="502px">
      <span>图片裁剪</span>
      <div class="croppa_box">
        <vue-crop :crop-url="getImgUrl" :ratio="ratio" :height="460" :width="460" class="c-crop--preview_right"
          @afterCrop="afterCrop">
        </vue-crop>
      </div>
      <span class="dialog-footer">
        <span @click="croppaVisible = false">取 消</span>
        <span type="primary" @click="uploadCroppedImage">确 定</span>
      </span>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        getImgUrl: '',
        croppaVisible: false,
        imgObject: { // 裁剪坐标信息

        },
        imgfiles: [], // 展示图片信息
        list: { // 传给后台图片集合

        },
      }
    },
    props: {
      ratio: {
        type: Number,
        default: 16 / 9
      }
    },
    methods: {
      addmsg1(e) {
        let imgtype = e.target.files[0].type;
        this.list = e.target.files[0]
        if (this.imgfiles.length < 10 && (imgtype == 'image/jpg' || imgtype == 'image/jpeg' || imgtype == 'image/png' ||
            imgtype == 'image/bmp')) {
          this.getImgUrl = window.URL.createObjectURL(e.target.files[0]);
          this.croppaVisible = true
        }
      },
      // 裁切完图片选择回调
      afterCrop(coord, url) {
        this.imgObject.coord = coord
      },
      // 图片裁剪完成后的上传
      uploadCroppedImage() {
        let _this = this
        if (!this.imgObject.coord) return alert('请在图片中选择你需要展示的位置！')
        let params = new FormData()
        let token = 'd8c07e81-eae5-4f44-8ac0-46d1127132c3'  // token 失效 会失败
        params.append('token', token)
        params.append('x', this.imgObject.coord.l)
        params.append('y', this.imgObject.coord.t)
        params.append('destWidth', this.imgObject.coord.w)
        params.append('destHeight', this.imgObject.coord.h)
        params.append('companyId', 5)
        params.append("imgFile", this.list)
        this.axios({
          method: 'POST',
          url: '/api/company/companyStyle',
          data: params,
        }).then((res) => {
          if (res.data.code != 1) {
            this.$message.error('上传失败！')
          }
          this.imgfiles.push({
            picUrl: res.data.pic.imgUrl,
            picId: res.data.pic.picId
          })
          this.croppaVisible = false
          this.imgObject = {}
        }).catch(reject => {

        })
      },
    }
  }

</script>

<style>

</style>



