<!--
步骤图：参数

-->
<template>
  <div id="wrap">
    <div class="step" v-for="(item, index) in lists" :key="index">
      <div class="step__head" :class="{'active': index <= current}">
        <hr>
        <div class="step__icon" @click="current = index">
          <i>成功</i>
          <b>{{index + 1}}</b>
        </div>
      </div>
      <div class="step__main">
        <span>{{item.state}}</span>
        <p>{{item.description}}</p>
      </div>
    </div>
    <progress2></progress2>
    <!-- 水波进度条 -->
    <div class="water-waves">
      <div class="water-wave1"></div>
      <div class="water-wave2"></div>
      <div class="water-wave3"></div>
    </div>
    <div class="content" width="200">
      <div class="center_text">
        <p class="title">75%</p>
      </div>
    </div>
    <p3></p3>
    <p4></p4>
  </div>
</template>

<script>
import progress2 from './progress2'
import p3 from '@xp/components/ap_progress/index'
import p4 from '@xp/components/ap_progressBall'
  export default {
    data() {
      return {
        current: 0
      }
    },
    components: {
      progress2,
      p3,
      p4
    },
    props: {
      direction: {
        type: String,
        default: 'vertical'
      },
      lists: {
        type: Array,
        default: () => [{
          state: '步骤一',
          description: '步骤一描述信息'
        }, {
          state: '步骤二',
          description: '步骤二描述信息'
        }, {
          state: '步骤二',
          description: '步骤二描述信息'
        }]
      }
    }
  }

</script>

<style scoped lang="scss">
  #wrap {
    display: flex;
    padding: 20px;

    .step {
      position: relative;

      .step__head {
        display: flex;
        align-items: center;

        .step__icon {
          width: 50px;
          height: 50px;
          line-height: 50px;
          text-align: center;
          border: 1px solid red;
          cursor: pointer;
          border-radius: 50%;

          i {
            display: none;
          }

          b {
            display: block;
          }
        }

        hr {
          width: 130px;
          height: 2px;
          background-color: red;
          outline: none;
          border: 0
        }
      }

      .active {
        .step__icon {
          border: 1px solid #333;
          background-color: #333;
          color: #fff;
          transition: all .5;

          i {
            display: block;
          }

          b {
            display: none;
          }
        }

        hr {
          background-color: #333
        }
      }

      .step__main {
        position: absolute;
      }

      &:nth-child(1) {
        hr {
          display: none;
        }
      }
    }

    // 水波效果，进度条
    .water-waves {
      margin: 0 auto;
      overflow: hidden;
      position: relative;
      width: 100px;
      height: 100px;
      border-radius: 50%;
      border: 1px solid red;
      animation: water-waves linear infinite;
    }

    .water-wave1 {
      position: absolute;
      top: 40%;
      left: -50%;
      background-color: blue;
      width: 200%;
      height: 200%;
      border-radius: 40%;
      opacity: .7;
      animation: inherit;
      animation-duration: 5s
    }

    .water-wave2 {
      position: absolute;
      top: 50%;
      left: -35%;
      background-color: blue;
      width: 200%;
      height: 200%;
      border-radius: 40%;
      opacity: .5;
      animation: inherit;
      animation-duration: 5s
    }

    .water-wave3 {
      position: absolute;
      top: 60%;
      left: -45%;
      background-color: blue;
      width: 200%;
      height: 200%;
      border-radius: 40%;
      opacity: .3;
      animation: inherit;
      animation-duration: 5s
    }

    @keyframes water-waves {
      0% {
        transform: rotate(0deg)
      }

      100% {
        transform: rotate(360deg)
      }
    }
  }
  // .circle_progress_barleft2 {
  //   animation: circle_progress_keyframes_name_left2 1000ms 200ms cubic-bezier(0.99, 0.01, 0.22, 0.94) forwards;
  // }
  // @keyframes circle_progress_keyframes_name_left2 {
  //     0% {
  //       stroke-dashoffset: 423.90000000000003px
  //     }
  //     100% {
  //       stroke-dashoffset: 105.975px
  //     }
  // }
  .content {
    position: relative;
  }
  .center_text {
    position: absolute;
    top: 43%;
    left: 56px;
    font-size: 20px;
  }
</style>



