<template>
  <div class="wrap">
    <div id="myChart" :style="mapStyle"></div>
  </div>
</template>

<script>
  import 'echarts/map/js/china.js';
  export default {
    data() {
      return {
        mapStyle: {
          width: '90%', 
          height: '800px',
          margin: '0 auto'
        }
      }
    },
    methods: {
      drawMap() {
        let myChart = this.$echarts.init(document.getElementById('myChart'))
        myChart.setOption({
          title: {
            text: '建亿通',
            left: 'center',
            top: '0px',
            textStyle: {
              color: '#fff',
              align: 'right'
            }
          },
          tooltip: {
            trigger: 'item',  //
            showDelay: 0,
            transitionDuration: 0.2,
            formatter: function (params) {
              var value = (params.value + '').split('.');
              value = value[0].replace(/(\d{1,3})(?=(?:\d{3})+(?!\d))/g, '$1,');
              return params.seriesName + '<br/>' + params.name + ': ' + value;
            }
          },
          visualMap: {
            type: 'continuous',
            min: 800,
            max: 50000,
            text:['High','Low'],
            realtime: false,
            calculable : true,
            inRange: {
              color: ['white','yellow','black']
            }
          },
          series: [{
            name: '中国地区数据',
            type: 'map',
            map: 'china',
            selectedMode: 'multiple', //可选'single'表示单选，或者'multiple'表示多选
            roam: false,
            nameMap: {
              'China': '中国'
            },
            label: { //文字快标签
              backgroundColor: "#ff8586",
              normal: {
                show: true
              },
              emphasis: {
                show: true
              }
            },
            itemStyle: { //地图区域图形样式
              areaColor: '#fff',
              // areaColor: { //颜色，支持线性，径向渐变
              //   type: 'radial',
              //   x: 0.5,
              //   y: 0.5,
              //   r: 0.5,
              //   colorStops: [{
              //     offset: 0,
              //     color: 'red' // 0% 处的颜色
              //   }, {
              //     offset: 1,
              //     color: 'blue' // 100% 处的颜色
              //   }],
              //   globalCoord: false // 缺省为 false
              // },
              normal: { // 默认样式
                areaColor: '#fff',
                // borderWidth: 1,
                // borderColor: '#333',
                label: {
                  show: true,
                  
                  textStyle: {
                    color: '#333',
                    // borderWidth: 1,
                    // borderColor: 'red'
                  }
                },
              },
              emphasis: {  // 选中高亮
                areaColor: 'red',
                label: {
                  show: true,
                  textStyle: {
                    color: '#fff'
                  }
                }
              }
            },
            tooltip: {  //提示框样式
              textStyle: {
                color: '#fff'
              },
              extraCssText: 'box-shadow: 0 0 3px rgba(0, 0, 0, 0.7);'
            },
            data: [{
                name: '广东',
                value: '323423423',
                selected: true,
                label: { // selected: 为false时才有效果
                  show: true,
                  color: "red"
                }
              },
              {
                name: '成都',
                value: '323423423',
                selected: true
              },
              {
                name: '北京',
                value: '323423423',
                selected: true
              },
              {
                name: '新疆',
                value: '323423423',
                selected: true
              },
            ]
          }]
        })
      }
    },
    mounted() {
      this.drawMap()
    }

  }
</script>

<style scoped>
  .wrap {
    width: 1200px;
    height: 800px;
    background-color: #1890ff;
    margin: 0 auto
  }
</style>


