<template>
  <div class="input-number">
    <span class="input-number__decrease"><i></i></span>
    <span class="input-number__increase" :class="[isDisabled ? 'is-disabled' : '']"><i></i></span>
    <input type="text" max="10" min="1">
  </div>
</template>

<script>
  export default {
    data() {
      return {
        isDisabled: false
      }
    }
  }

</script>

<style scoped lang="scss">
  .input-number {
    position: relative;
    display: inline-block;
    width: 180px;
    line-height: 38px;
    .input-number__decrease {
      position: absolute;
      z-index: 1;
      top: 1px;
      left: 1px;
      width: 40px;
      height: auto;
      text-align: center;
      background: #f5f7fa;
      color: #606266;
      cursor: pointer;
      font-size: 13px;
      display: flex;
      justify-content: center;
      align-items: center;
      i {
        &::after {
          content: "\e6d8";
        }
      }
    }
    .input-number__increase {
      position: absolute;
      z-index: 1;
      top: 1px;
      right: 1px;
      width: 40px;
      height: auto;
      text-align: center;
      background: #f5f7fa;
      color: #606266;
      cursor: pointer;
      font-size: 13px;
      display: flex;
      justify-content: center;
      align-items: center;
      i {
        &::after {
          content: "\e6d9";
        }
      }
    }
    input {
      -webkit-appearance: none;
      border: 1px solid #dcdfe6;
      padding-left: 50px;
      padding-right: 50px;
      text-align: center;
      transition: border-color .2s cubic-bezier(.645,.045,.355,1);
    }
  }
</style>



