<!-- 
    ed
-->
<template>
    <div class="mask" :class="{show: show}">
        <div class="wrap" :style="style">
            <span>Loading</span>
        </div>
        <span @click="close">关闭</span>
    </div>
</template>

<script>
export default {
    data() {
        return {
            show: false
        }
    },
    props: {
        width: String,
    },
    computed: {
        style() {
            let style = {}
            if(this.width) {
                style.width = this.width
                style.height = this.width
            }
            return style
        }
    },
    methods: {
        close() {
            this.show = false
        },
        mask(data) {
            this.show = data.show
        }
    },
    mounted() {
        console.log(this.width, '宽')
    }
}
</script>

<style scoped lang="scss">
    .mask {
        display: none;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0,0,0, .3);
        color: #fff;
        .wrap {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%); 
            font-size: 28px;
            background-color: #333;
        }
        >span {
            position: absolute;
            right: 10px;
            top: 10px;
            font-size: 20px;
        }
    }
    .show {
        display: block;
    }
</style>



