<template>
  <div>
    <div class="meau">
      <div>
        搜索：<input type="text" v-model="keyWords" @change="search">
      </div>
      <router-link to="/index" tag="p" class="to-home">跳转-后台管理系统模板</router-link>
      <p :to="list.path" v-for="(list, index) of lists" :key="index" @click="current(index, list.path)"
        :class="{'active': index == currentIndex}">{{list.name}}</p>
    </div>
    <main>
      <!-- <router-view /> -->
      <header>
        <h1>苹果家+</h1>
        <!-- <h3>提供企业级开发全流程组件方案，模板</h3> -->
        <h3 style="margin-top: 30px">待处理：官网导航组件，面板组件，item组件</h3>
        <p>
          <a href="">捐赠</a>
          <a href="">赞助</a>
          <a href="">联系</a>
        </p>
      </header>
      <div class="ui-zj" style="display: flex">
        <!-- <ap-checkbox v-model="checked">备选项</ap-checkbox>
        <ap-input placeholder="请输入内容1"><i class="iconfont icon-finish"></i></ap-input> -->

        <!-- <apple-popover title="你确定批量通过该申请吗111？">
          <button>确认</button>
        </apple-popover> -->
        <component :is="currentTab"></component>
        <!-- <ap-button @filterData="filterData" v-if="currentIndex == 2"></ap-button>
        <span @click="add" v-if="currentIndex == 2">获取全局对象</span>
        <ap-select v-if="currentIndex == 3" ref="reashinit"></ap-select>
        <ap-drawer v-if="currentIndex == 9"></ap-drawer>
        <ap-tabs v-if="currentIndex == 10"></ap-tabs>
        <ap-roster :dataObj="dataObj" v-if="currentIndex == 11"></ap-roster>
        <ap-progress1 v-if="currentIndex == 13"></ap-progress1>
        <ap-progress2 v-if="currentIndex == 13"></ap-progress2>
        <ap-table v-if="currentIndex == 14"></ap-table> -->
        <div class="api"> 
          <h2>Api</h2>
          <p>
            <span>cheeckEd</span>
            <span>是否禁用</span>
            <span>禁用</span>
          </p>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
import Vue from 'vue';
import apCol from './col';
import apLoading from './loading'
import apCheckbox from './Checkbox'
import apButton from './button'
import apSelect from './select'
import apInput from './input'
import apSwitch from './switch'
import apSlider from '@xp/components/slider'
import apFrom from './form'
import apUpload from './Upload'
import apTree from '@xp/components/ap_tree/index'
import apRollNum from '@xp/components/roll_num'  // 滚动数字
import apDialog from '@xp/components/appleDialog' // 弹框
import applePopover from '@xp/components/popover' // 气泡框
import apRate from '@xp/components/ap_rate';
import apCrumbs from '@xp/components/ap_crumbs/index'  // 面包屑
import apDrawer from './drawer'
import apTabs from './tabs/index'
import apAddList from '@xp/components/add_lists/index'
import apProgress1 from './progress1'
import apProgress2 from './progress2'
import apTable from './table'
import apDownFile from '@xp/components/ap_downFile'
import apEditor from '@xp/components/ap_editor'
import apPdfLook from '@xp/components/ap_pdf/pdf_look'
import apCutImg from './cut_img'
import apSteps from '@xp/components/appleSteps'
import apShopping from '@xp/components/ap_shopping_cart/index'
  export default {
    data() {
      return {
        checked: true,
        currentIndex: 0,
        currentTab: 'ap-col',
        fl: {},
        dataObj: {
          type: 'pAndC',
          disabledArr: [],
          isDisabledRoster: false,
          isDeleteCurrent: true,
          userNameCopy: '',
          allowMaxNum: 99
        },
        lists: [],
        keyWords: ''
      }
    },
    components: {
      apCol,
      apLoading,
      apCheckbox,
      apButton,
      apSelect,
      apInput,
      apSwitch,
      apSlider,
      apFrom,
      apUpload,
      apTree,
      apRollNum,
      apDialog,
      applePopover,
      apRate,
      apCrumbs,
      apDrawer,
      apTabs,
      apAddList,
      apProgress1,
      apProgress2,
      apTable,
      apDownFile,
      apEditor,
      apPdfLook,
      apCutImg,
      apSteps,
      apShopping
    },
    methods: {
      current(index, path) {
        this.currentIndex = index
        this.currentTab = path
      },
      filterData(obj) {
        let cc = {
          aa: 112
        }
       let tt = Object.assign(this.fl, obj, cc)
      },
      add() {
        console.log(this.fl)
      },
      search() {
        let arr = []
        this.lists.forEach((item, index) => {
          if(item.name.includes(this.keyWords)) {
            // this.currentIndex = index
            arr.push(item)
          }
        })
        this.lists = arr
        if(!this.keyWords) {
          this.lists = JSON.parse(localStorage.getItem('lists'))
        }
      }
    },
    created() {
      console.log(this, '实例对象')
      // console.log(Vue, '构造函数')
      // debugger
      this.lists = [{
          name: '布局',
          path: 'ap-col'
        }, {
          name: 'Loading',
          path: 'ap-loading'
        }, {
          name: 'Checkbox',
          path: 'ap-Checkbox'
        }, {
          name: 'button',
          path: 'ap-button'
        }, {
          name: 'Select',
          path: 'ap-select'
        }, {
          name: 'input',
          path: 'ap-input'
        }, {
          name: 'switch',
          path: 'ap-switch'
        }, {
          name: '滑块',
          path: 'ap-slider'
        }, {
          name: '倒计时',
          path: 'progress'
        }, {
          name: 'from表单',
          path: 'ap-from'
        }, {
          name: '文件上传',
          path: 'ap-Upload'
        }, {
          name: '树',
          path: 'ap-tree'
        }, {
          name: '异步分页',
          path: 'progress'
        }, {
          name: '滚动数字',
          path: 'ap-roll-num'
        }, {
          name: '弹框- 提示',
          path: 'ap-dialog'
        }, {
          name: '气泡框',
          path: 'ap-dialog'
        }, {
          name: '评分',
          path: 'ap-rate'
        }, {
          name: '面包屑',
          path: 'ap-crumbs'
        }, {
          name: '抽屉',
          path: 'ap-drawer'
        }, {
          name: '标签页',
          path: 'ap-tabs'
        }, {
          name: '人员管理', // ap-store库提供
          path: 'ap-roster'
        }, {
          name: '列表添加',
          path: 'ap-add-list'
        }, {
          name: '上拉下拉加载刷新',
          path: 'progress'
        }, {
          name: '轮播图',
          path: 'progress'
        }, {
          name: '瀑布流',
          path: 'progress'
        }, {
          name: '进度条1',
          path: 'ap-progress1'
        }, {
          name: '进度条2',
          path: 'ap-progress2'
        }, {
          name: '表格',
          path: 'ap-table'
        }, {
          name: '菜单组件 - 树',
          path: 'progress'
        }, {
          name: '全屏滚动',
          path: 'progress'
        }, {
          name: '级联组件',
          path: 'progress'
        }, {
          name: '日期选择',
          path: 'progress'
        }, {
          name: '流下载',
          path: 'ap-down-file'
        }, {
          name: '富文本编辑器',
          path: 'ap-editor'
        }, {
          name: '图片查看器',
          path: 'ap-pdf-look'
        }, {
          name: '图片裁剪',
          path: 'ap-cut-img'
        }, {
          name: '流程图',
          path: 'ap-steps'
        }, {
          name: '滚动公告',
          path: 'progress'
        }, {
          name: '购物车',
          path: 'ap-shopping'
        }, {
          name: '粒子动点',
          path: 'progress'
        }, {
          name: '滚动条溢出',
          path: 'progress'
        }]
        localStorage.setItem('lists', JSON.stringify(this.lists))
    }
  }

</script>

<style scoped lang="scss">
  .meau {
    width: 220px;
    height: 100vh;
    background-image: linear-gradient(#0b960b, #15c715);
    color: #fff;
    text-align: center;
    float: left;
    overflow-y: scroll;
    cursor: pointer;
    p {
      padding: 10px;
      &:hover {
        background-color: #409eff
      }
    }
    .active {
      background-color: #409eff;
      color: #fff
    }
  }

  main {
    float: left;
    width: calc(100% - 230px);
    height: 700px;
    header {
      text-align: center;
      background-image: linear-gradient(#63d863, #0b960b);
      padding: 20px 0;
      color: #fff;
      h1 {
        padding: 20px 0
      }
      p {
        margin-top: 20px;
        a {
          color: #fff
        }
      }
    }
    .ui-zj {
      padding: 30px
    }
  }
</style>



