<template>
    <div>
      <canvas id="circle1" width="140px" height="140px"></canvas>
      <div class="jindu">
        <div></div>
      </div>
    </div>
</template>

<script scoped>
  export default {
    data() {
      return {
        num: 0,
        progress: 79,
        baseColor: 'blue',
        barColor: 'red'
      }
    },
    mounted() {
      this.draw()
    },
    methods: {
      init(obj) {
        console.log(obj, 'fds')
        this.progress = obj.progress
        this.baseColor = obj.baseColor
        this.barColor = obj.barColor
        this.draw()
      },
      draw() {
        var circle1 = document.getElementById('circle1');
        var ctx = circle1.getContext('2d');
        ctx.clearRect(0, 0, 140, 140);
        ctx.beginPath();
        ctx.arc(70, 70, 60, Math.PI * 3 / 4, Math.PI * 9 / 4, false);
        ctx.strokeStyle = this.baseColor;
        ctx.lineWidth = 20;
        ctx.stroke();
        // ctx.font = '26px'
        // ctx.fillText('168', 60, 60);
        // ctx.font = '14px'
        // ctx.fillText('已施工(天)', 50, 80);
        this.move(ctx)
      },
      move(ctx) {
        this.num = 0 // 重绘
        let p = this.progress / 100
        let deg = Math.PI * 3 / 2
        let view = deg * p
        let speed = view / 20
        let times = setInterval(() => {
          this.num++
          ctx.beginPath();
          ctx.arc(70, 70, 60, Math.PI * 3 / 4, (Math.PI * 3 / 4) + speed, false);
          ctx.strokeStyle = this.barColor;
          ctx.lineWidth = 20;
          ctx.stroke();
          speed += view / 20
          if (this.num > 19) {
            clearInterval(times)
          }
        }, 20);
      }
    }
  }

</script>

<style lang="scss" scoped>
  #circle1 {
    // position: absolute;
    // top: 0;
    // left: 0;
    z-index: 2;
  }
  .jindu {
    position: relative;
    width: 600px;
    height: 60px;
    border: 2px solid red;
    box-sizing: border-box;
    overflow: hidden;
    div {
      position: absolute;
      left: -100%;
      width: 596px;
      height: 56px;
      background-image: linear-gradient(black, blue);
      animation: identifier 3s linear forwards
    }
  }
  @keyframes identifier {
    from {left: -100%}
    to {left: -30%}
  }
</style>



