<template>
    <ul class="adjustpowerdiv">
    	<li v-for="k,i in data" class="li_padding" :key='k.privilegeId'  :class="defaul(k)">
    		<div class="text_icon">
    			<i class="iconfont icon-sanjiaoleft" :style="{opacity:k.privilegeTrees?'':'0'}"  
    				:class="k.open==true?'open_sanjiaoleft':''"
    				@click="openTree(k)" >
    			</i>
	    		<b class="chenk_box" @click="chenck(k)" :class="[k.ifCheck==1?'pitch':'',defaultChenck(k.privilegeId)?'unableSelect':'']">
	    			<i class="iconfont icon-finish" v-if="k.ifCheck==1||defaultChenck(k.privilegeId)"></i>
					
	    		</b>
	    		<span class="chenck_name">
	    			{{k.privilegeName}}
	    		</span>
    		</div>
    		<tree :data="k.privilegeTrees" v-if="k.open" @permissions="permissions" :upt="upt"></tree>
    	</li>
    </ul>
</template>
<script>
	
export default {
	name:'tree',
  data(){
    return{
    	child:[] 
    	
    }
  },
	
  props:['data','upt'],
 
  methods:{
  	defaultChenck(id){
  		if(id==46||id==47||id==55||id==62||id==63||id==71||id==72||id==73||id==74){
  				return true
  			}
  	},
	defaul(k){
  		if(this.upt.indexOf(k.privilegeId)!=-1){
  			k.ifCheck=true
  		}else{
  			k.ifCheck=false
  		}
  	},
    openTree(k){//是否打开树    
        k.open=!k.open
        this.$forceUpdate()
  	},
  	chenck(k){
  		this.child=[]
  		
  		if(k.privilegeId!=46&&k.privilegeId!=47&&k.privilegeId!=55&&k.privilegeId!=62&&k.privilegeId!=63&&
  			k.privilegeId!=71&&k.privilegeId!=72&&k.privilegeId!=73&&k.privilegeId!=74
  		){	
			
				if(k.ifCheck==1){
					k.ifCheck=-1
				}else{
					k.ifCheck=1
				}
			
			  
	  		if(k.ifCheck==1){
	  			let arr=[];
	  				arr.push(k.privilegeId)
				this.$emit('permissions',arr,'add')
	  		}else{
	  			let arr=[];
			  	arr.push(k.privilegeId)
			  
			  	this.removeArrData(k.privilegeTrees)
					 

	
				this.$emit('permissions',arr,'remove',k.privilegeId,this.child)
	  		}
  		}
	  },
	  removeArrData(arr){
  		if(arr!=null){
			for (var i in arr) {
				this.child.push(arr[i].privilegeId)
				
				this.removeArrData(arr[i].privilegeTrees)
			}
  		}
	
  	},
  	permissions(arr,sta,rId,rArr){
  		
  		for (var i in this.data) {
  			for (var j in this.data[i].privilegeTrees) {
  				if(this.data[i].privilegeTrees[j].privilegeId==arr[arr.length-1]){
  					if(sta=='add'){
  						this.data[i].ifCheck=1
  					}
  					arr.push(this.data[i].privilegeId)
					this.$emit('permissions',arr,sta,rId,rArr)
  				}
  			}
  		}
  	}
  }
}
</script>
<style >
	.adjustpowerdiv .text_icon{
		width: inherit;
		height: 30px;
		line-height: 28px;
	}
	.adjustpowerdiv .li_padding{
 		padding-left: 30px;
 		line-height: 30px;
 	}
	/*向下*/
 	.adjustpowerdiv .icon-sanjiaoleft{
 		font-size: 30px;
 		transform: rotateZ(0deg)translateX(0px);
 		display: inline-block;
 		cursor: pointer;
 		color: #666;
 		float: left;
 		transition: all 300ms;
 		margin-right: 5px;
 	}
 	.adjustpowerdiv .open_sanjiaoleft{
 		transform: rotateZ(90deg)translateY(2px)translateX(-3px);
 	}
 	/*选中*/
 	.chenk_box{
 		border:1px solid #0092DD ;
 		display: inline-block;
 		width: 18px;
 		height: 18px;
 		cursor: pointer;
 		text-align: center;
 		line-height: 18px;
 		transition: all 300ms;
 		float: left;
 		box-sizing: border-box;
 		margin-top: 5px;
 		margin-right: 10px;
 	}
 	.chenk_box i{
 		font-size: 13px!important;
 	}
 	.pitch{
 		background: #0092DD;
 		color: #fff;
 	}
 	/*名字*/
 	.chenck_name{
 		font-size: 16px;
 		font-family: "微软雅黑";
 		float: left;
 	}
 	/*无法勾选*/
 	.unableSelect{
 		background: #999!important;
 		border: 1px solid #666;
 	}
 	.unableSelect i{
 		color: #eee!important;
 	}
</style>



