<!-- ed-->
<template>
    <div :class="[!!apSm?aa:'', !!apMd?bb:'', !!apLg?cc:'', !!apXl?dd:'', !!isShadow?'box-shadow':'']">
        <slot name="title"></slot>
        <slot name="main"></slot>
    </div>
</template>

<script>
export default {
    data() {
        return {
            isShadow: false
        }
    },
    props: {
        apSm: {
            type: Number,
            default: null
        },
        apMd: {
            type: Number,
            default: null
        },
        apLg: {
            type: Number,
            default: null
        },
        apXl: {
            type: Number,
            default: null
        },
        // 效果
        shadow: {
           type: String,
           default: 'never' 
        }
    },
    computed: {
        aa() {
            return `ap-sm-${this.apSm}`
        },
        bb() {
            return `ap-md-${this.apMd}`
        },
        cc() {
            return `ap-lg-${this.apLg}`
        },
        dd() {
            return `ap-xl-${this.apXl}`
        }
    },
    created() {
        if(this.shadow == 'always') {
            this.isShadow = true
        }
    }
}
</script>

<style scoped lang="scss">
@import './../../xp/css/mobileRem';
.box-shadow {
    box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
}
</style>



