<template>
  <ul id="side_ul" :style="{background:num>1?'#000d1a!important':''}">
    <!-- <transition-group name="sideAnimation" > -->
    <li v-for="(n,index) in model" :key="index" class="side_ul_li">
      <div class="side_txt" @click="sidefun(n)" :class="cId==n.id?'side_txt_bg':''"
        :style="{paddingLeft:num*44+'px',color:cloro(n.name)?'#fff':'#A6AAAE'}">
        <!--<i :class="n.icon" class="num_icon" :style="{marginLeft:(Number(num)*44)+'px'}"></i>-->
        <i :class="n.icon" class="num_icon" v-if="n.icon"></i>
        <span class="side_ul_li_span">{{n.name}}</span>
        <i class="iconfont icon-icon-arrow-right2 pullDown" v-if='showChild(n.child)'
          :class="n.hide==11?'pull_down':''">《</i>
        <!-- {{n.hide}} -->
      </div>
      <side :model='n.child' v-if="n.hide==11" :num='num+1' @pitch="pitch" :cId='cId' :openTag="openTag"></side>
    </li>
    <!-- </transition-group> -->
  </ul>
</template>

<script>
  import {
    mapState,
    mapGetters,
    mapMutations
  } from 'vuex' //使用state快捷方式   引入mapstate来操作
  export default {
    name: 'side',
    data() {
      return {
        data1: {},
      }
    },
    props: ['model', 'num', 'cId', 'openTag'],
    created() {
      let aa = [1, 2, 3]
      console.log(this.$user.getData('crumbsList'), aa, 'ss顺风顺水发搜索谁说的')
    },
    methods: {
      pitch(id) { //选中项的id值上浮
        this.$emit('pitch', id)
      },
      sidefun(par) {
        this.$emit('pitch', par) //上浮id

        if (this.$user.getData('crumbsList') != null) {
          let arr = this.$user.getData('crumbsList')
          arr.splice(this.num - 1, 1, par)
          arr.splice(this.num)
          this.$user.saveData('crumbsList', arr)
        } else {
          let arr = []
          arr.push(par)
          this.$user.saveData('crumbsList', arr)
        }

        if (par.id == 'company.book' || par.id == 'F0400') {
          this.$router.push({
            name: par.routerName,
            query: {
              tabId: '1',
              clooseId: '1'
            }
          });
        } else {
          this.$router.push({
            path: par.router
          });
        }
      },
      //是否拥有子集     仅仅展示小三角用途
      showChild(n) { //n为子集
        if (n != undefined) {
          if (n.length > 0) {
            return true
          }
        }
      },


      cloro(name) {

        let arr = this.$user.getData('crumbsList')
        let str = '';
        if (arr[0]) {
          str = arr[0].name
          if (arr[1]) {
            str = str + arr[1].name
            if (arr[2]) {
              str = str + arr[2].name
            }
          }
        }
        if (JSON.stringify(str).indexOf(name) != -1) {
          return true
        }
      },



      /*************验证是否有这个权限*/
      showSide(id) {
        if (this.opentab(id) || this.projetab(id)) {
          return true
        }
      },
      //验证是否有企业及项目等权限
      opentab(id) {
        if (id != 'A0000' && id != 'I0000' && id != 'C0000' && id != 'E0400') {
          if (id != 'project.base.operation' || id != 'project.file' || id != 'project.user' ||
            id != 'project.finance' || id != 'project.quality' || id != 'project.security') {
            if (this.$user.jur(id, 'c')) {
              return true
            }
          }
        }
      },
      //验证是否有项目相关权限
      projetab(id) {

        if (id == 'project.base.operation' || id == 'project.file' || id == 'project.user' ||
          id == 'project.finance' || id == 'project.quality' || id == 'project.security' || id == 'project.statistic' ||
          id == 'E01011E') {
          if (this.$user.jur(id, 'p')) {
            return true
          }
        }
      },


    }
  }

</script>

<style>
  /*列表样式*/
  #side_ul .side_ul_li {
    width: 100%;
    min-height: 40px;
    margin-top: 1px;
    color: #fff;
    /* font-weight: 100; */
    cursor: pointer;
    line-height: 40px;
    overflow: hidden;
    box-sizing: border-box;
    transition: all 300ms;
  }

  #side_ul .side_txt:hover {
    background: #198CFF;
  }

  #side_ul .side_txt_bg {
    background: #198CFF;
  }

  #side_ul .side_ul_li .num_icon {
    width: 20px;
    height: 40px;
    /*margin-left: 40px;*/
    display: inline-block;
    margin-right: 24px;
    font-size: 16px;
    box-sizing: border-box;
    display: block;
    float: left;
  }

  #side_ul .side_ul_li .side_ul_li_span {
    display: block;
    float: left;
    font-size: 15px !important;
  }

  #side_ul .side_ul_li .side_txt {
    height: 40px;
  }

  /*下拉按钮*/
  .pullDown {
    display: inline-block;
    float: right;
    width: 40px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    transition: all 300ms;
  }

  .pull_down {
    transform: rotateZ(90deg) !important;
  }

  /*菜单打开动画*/
  .sideAnimation-enter {
    height: 0px;
    line-height: 0px;
    opacity: 0;
    color: #323d4b;
  }

  .sideAnimation-enter-active {
    transition: all 1s;
  }

  .sideAnimation-enter-to {
    height: 40px;
    line-height: 40px;
    opacity: 1;
  }

  .sideAnimation-leave-active {
    transition: all 1s;
  }

  .sideAnimation-leave-to {
    height: 0px;
    line-height: 0px;
    opacity: 0;
    color: #323d4b;
  }

</style>



