<template>
<!-- 文件树 过渡收缩，点击展开，图标，右键查看 左右联动 逆向递归-->
  <ul v-if="apTreeData != null">
    <li v-for="(k,i) in apTreeData" :key="i" class="tree-menu" :class="rightOpenLeft(k)">
      <div class="item" :style="indent" @click="open(k)">
        <span>{{k.departmentName}}</span>
        <span>({{k.nodes?k.nodes.length:'空'}})</span>
        <i class="iconfont icon-home1" style="color: #1890FF;font-size: 19px"></i>
      </div>
      
      <!-- <ap-tree :apTreeData="k.nodes" :depth="depth + 1" v-show="k.unfold==false" @dataUpt="dataUpt"></ap-tree> -->
      <ap-tree :apTreeData="k.nodes" :depth="depth + 1" v-show="k.unfold==false" @openFather="openFather" :checkdepartid="checkdepartid"></ap-tree>
    </li>
  </ul>
</template>

<script>
  export default {
    props: ['nodes', 'depth', 'apTreeData','currentOpen', 'checkdepartid'],
    name: 'ap-tree',
    data() {
      return {
      }
    },
    computed: {
      indent() {
        //  return { transform: `translate(${this.depth * 30}px)` }
        return {
          padding: '10px',
          'padding-left': `${this.depth * 20}px`
        }
      }
    },
    watch: {
      checkdepartid() {}
    },
    methods: {
      open(k){
        // this.$emit('dataUpt',k);
        k.unfold=!k.unfold; 
        this.$emit('openFather',k)
        //注意     迫使 Vue 实例重新渲染。注意它仅仅影响实例本身和插入插槽内容的子组件，而不是所有子组件。 
        this.$forceUpdate()
      },
      openFather(k) {
        // this.$emit('openFather',k)
        for(var i in this.apTreeData){
          for(var j in this.apTreeData[i].nodes){
            if(this.apTreeData[i].nodes[j].departmentId==k.departmentId){
              console.log(k, '执行。。。')
              this.apTreeData[i].unfold=false;  //遍历找到右侧树需打开的对应的点
              this.$emit('openFather',k)
              // this.$forceUpdate()
            }
          }
        }
      },
      // 点击右侧打开对应左侧
      rightOpenLeft(k) {
        console.log('点击')
        if(k.departmentId==this.checkdepartid){
          console.log(k, '进入。。。')
          this.$emit('openFather', k)
        }
      }

      // 内部消化

      // dataUpt(k){
    	//   this.$emit('dataUpt',k);
  	  // },
    },
    
    created() {
      for (var i in this.apTreeData) {
        // false 展开 true 收缩
        this.apTreeData[i].unfold=false;
      };
    },
    watch: {
      currentOpen(o, n) {
        if(o != n) {
          console.log('展开子集')
          let k = {"departmentId":6,"departmentName":"管理员","nodes":null,"parentDepartmentId":3,"ifJob":true, "unfold": false}
          this.open(k)
        }
      }
    }
  }

</script>

<style scoped>
ul {
  list-style: none;
}
  .tree-menu {
    position: relative;
    width: 500px
  }
	
  .tree-menu   .item {
      display: flex;
      justify-content: space-between;

    }
	.tree-menu   .item:hover {
        background-color: #F2F2F2;
        cursor: pointer;
      }

  .isShow {
    display: block !important;
  }

</style>



