<template>
  <div class="wrap">
    <input type="text" :placeholder="placeholder" v-model="mail" checked="true" @change="vaildent" @focus="focus"
      @blur="blur" :class="{'borderChecked': isFocus}">
    <slot></slot>
    <span>正则匹配</span>
    <span>正则验证</span>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        mail: '11',
        isFocus: false
      }
    },
    props: {
      placeholder: {
        type: String,
        default: '请选择'
      }
    },
    methods: {
      vaildent() {
        console.log(111)
        let r = /^([a-zA-Z]|[0-9])+@+(qq|163|sina)+.com$/;
        if (!r.test(this.mail)) {
          alert('不是邮箱')
        }
      },
      focus() {
        this.isFocus = true
      },
      blur() {
        this.blur = false
      }
    }
  }

</script>

<style scoped lang="scss">
  .wrap {
    position: relative;
    width: 260px;
    height: 26px;

    input {
      width: 100%;
      display: inline-block;
      line-height: 26px;
      border-radius: 4px;
      text-indent: 7px;
      color: blue;
      outline: none;
      border: 1px solid #999
    }

    .borderChecked {
      border: 1px solid #409eff;
      box-shadow: 1px 1px 3px red;
      transition: all .5s
    }

    i {
      position: absolute;
      right: 3px;
      top: 3px
    }
  }

</style>



