<template>
  <div id="my-chart" style="width: 600px;height: 500px"></div>
</template>

<script>
  export default {
    data() {
      return {

      }
    },
    mounted() {
      this.init()
    },
    methods: {
      init() {
        // const res = []
        // const map = list.reduce((res, v) => (res[v.id] = v, res), {})  // 逗号运算符 表示 返回 后面的值 可以改成 res[v.id]=v; return res;
        let myChart = this.$echarts.init(document.getElementById('my-chart'));
        let option = {
          grid: {
            x2: 100,
            y2: 100,
          },
          xAxis: {
            axisTick: {
              show: false
            },
            data: [1, 2, 3, 4, 5, 6, 7],
            axisLabel: {
              interval: 0,
              formatter: function (value) {
                return value.split("").join("\n");
              }
            },
            axisLabel: {
              rotate: 45,
            }
          },
          yAxis: {
            splitLine: {
              show: false
            }
          },
          animationDurationUpdate: 1200,
          series: [{
            name: '直接访问',
            type: 'bar',
            itemStyle: {
              normal: {
                color: 'green'
              }
            },
            silent: true,
            barWidth: 40,
            barGap: '-100%', // 不同系列的柱间距离，为百分比（如 '30%'，表示柱子宽度的 30%）;如果想要两个系列的柱子重叠，可以设置 barGap 为 '-100%'。这在用柱子做背景的时候有用
            z: 1,
            data: [10, 52, 200, 334, 390, 330, 220]
          }, {
            name: '直接访问',
            type: 'bar',
            itemStyle: {
              normal: {
                color: 'red'
              }
            },
            barWidth: 40,
            z: 2, // 柱状图前后顺序
            data: [90, 270, 60, 120, 70, 80, 300]
          }]
        }
        myChart.setOption(option)
      }
    }
  }

</script>

<style>

</style>



