<template>
    <div>
        <div class="menu">
            <h1 class="title">Logo</h1>
            <div class="slide">
                <side-list></side-list>
            </div>
        </div>
        <div class="nav">
            <span @click="route(1)">项目管理</span> / <span @click="route(2)">项目列表</span>
        </div>
        <div class="content">
            <router-view></router-view>
        </div>
        
        <!-- <ap-crumbs></ap-crumbs> -->
    </div>
</template>

<script>
import apCrumbs from './../../xp/components/ap_crumbs/index'
import sideList from './navigation/side'
export default {
  components: {
    apCrumbs,
    sideList
  },
  methods: {
      route(k) {
        if(k == 1) {
            this.$router.push({
                path: "/index/projectsubject/businessAdministration/busCard"
            })
        } else {
            this.$router.push({
                path: "/index/projectsubject/businessAdministration"
            })
        }
      },
  }
}
</script>

<style scoped lang="scss">
    .nav {
        width: 80%;
        margin: 0 auto
    }
    .content {
        width: 80%;
        height: 500px;
        border: 1px solid red;
        margin: 0 auto
    }
</style>



