<template>
  <div>
    <span>表单打印(待完成)</span>
    <p @click="isClose = true" style="color: red">特定权限树</p>
    <td-tree v-if="isClose" @guanbi="closepower"></td-tree>
  </div>
</template>

<script>
  import tdTree from './adjust/index';
  export default {
    data() {
      return {
        isClose: false
      }
    },
    components: {
      tdTree
    },
    methods: {
      closepower() {
        this.isClose = false
      }
    }
  }

</script>

<style>

</style>



