<template>
  <div id="btn">
    <button @click="filterData">默认按钮</button>
    <span class="btn__line--top"></span>
    <span class="btn__line--right"></span>
    <span class="btn__line--bottom"></span>
    <span class="btn__line--left"></span>
  </div>
</template>

<script>
  export default {
    methods: {
      filterData() {
        let aa = {
          name: '测试全局对象丢失',
          age: 25
        }
        if(aa.age == 25) aa.age = 26
        this.$emit('filterData', aa)
      }
    }
  }

</script>

<style scoped lang="scss">
  #btn {
    position: relative;
    margin: 20px;
    overflow: hidden;
    width: 98px;
    height: 40px;
    button {
      outline: 0;
      width: 98px;
      height: 40px;
      line-height: 40px;
      text-align: center;
      color: #fff;
      border: 1px solid green;
      background-color: green;
      box-sizing: border-box;
      cursor: pointer;
    }

    span {
      position: absolute;
      width: 0;
      height: 3px;
      background-color: blue;
      transition: width 0.4s ease;
    }

    .btn__line--top {
      left: 0;
      top: 0px;
    }
    .btn__line--right {
      left: 100%;
      top: 0px;
      transform: rotate(90deg) scaleX(0.6667);
      transform-origin: left top;
    }
    .btn__line--bottom {
      left: 100%;
      top: 100%;
      transform: rotate(180deg);
      transform-origin: left top;
    }
    .btn__line--left {
      left: 0;
      top: 100%;
      transform: rotate(-90deg) scaleX(0.6667);
      transform-origin: left top;
    }

    &:hover span{
      width: 100%;
      transition: width 0.4s ease
    }

  }

</style>



