<!--
action: String  地址；
@fileLists: 回调文件列表
-->
<template>
  <div>
    <div class="wrap">
      <input type="file" @change="fileChange($event)" name="file" multiple  accept="*/*">
      <div>+</div>
    </div>
    <img :src="item"  alt="" width="200px" v-for="(item, k) in imgURL" :key="k">
  </div>
</template>

<script>
  export default {
    data() {
      return {
        imgURL: [],
        filesList: []
      }
    },
    methods: {
      fileChange(e) {
        // this.imgURL = window.URL.createObjectURL(e.target.files[0]);
        this.allFiles(e.target.files)
      },
      allFiles(_files) {
        for(var i=0;i<_files.length;i++){
          this.filesList.push( _files[i])
        }
        for(var i=0;i<this.filesList.length;i++) {
          let url = window.URL.createObjectURL(this.filesList[i])
          this.imgURL.push(url)
        }
      }
    }
  }

</script>

<style scoped lang="scss">
  .wrap {
    position: relative;
    width: 120px;
    height: 120px;
    input {
      position: absolute;
      z-index: 2;
      width: 100%;
      height: 100%;
      outline: 0;
      opacity: 0;
    }
    div {
      position: absolute;
      width: 100%;
      height: 120px;
      line-height: 120px;
      text-align: center;
      font-size: 18px;
      border: 1px dotted green
    }

  }

</style>



