<!--
    自定义表单
-->
<template>
  <div id="wrapId">
    <div class="items" v-if="items">
      <div class="item" v-for="(item, index) of items" :key="index">
        <!-- <div class="custom-title" contenteditable="true" @keyup="entry">
          {{item.projectCustomKey}}
        </div> -->
        <input type="text" class="custom-title" placeholder="自定义标题" v-model="item.projectCustomKey" @keyup="entry" maxlength="15">
        <span>：</span>
        <!-- <div class="custom-content" contenteditable="true" @keyup="entry">
          {{item.projectCustomValue}}
        </div> -->
        <input type="text" class="custom-content" placeholder="自定义内容" v-model="item.projectCustomValue" @keyup="entry">
        <i @click="delect(index, item.projectCustomId)">x</i>
      </div>
    </div>
    <div class="add" @click="add"><span>+</span>新增自定义信息</div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        items: [],
        deleteCustomIds: []
      }
    },
    // watch: {
    //   newValue1() {
    //     this.$emit('addCustomMessage', this.items)
    //   },
    //   newValue2() {
    //     this.$emit('addCustomMessage', this.items)
    //   },
    //   items: {
    //     handler(newVal) {
    //       this.$emit('addCustomMessage', newVal) 
    //     },      
    //     deep: true
    //   }
    // },
    // computed: {
    //   newValue1() {
    //     for(let k in this.items) {
    //       return this.items[k].projectCustomKey
    //     }
    //   },
    //   newValue2() {
    //     for(let k in this.items) {
    //       return this.items[k].projectCustomValue
    //     }
    //   }
    // },
    methods: {
      add() {
        let item = {
          projectCustomKey: '',
          projectCustomValue: '',
          projectCustomId: null
        }
        this.items.push(item)
        this.$emit('addCustomMessage', this.items, this.deleteCustomIds)
      },
      delect(index, id) {
        this.items.splice(Number(index), 1)
        if(id) this.deleteCustomIds.push(id)
        this.$emit('addCustomMessage', this.items, this.deleteCustomIds)
      },
      entry() {
        this.$emit('addCustomMessage', this.items, this.deleteCustomIds)
      },
      init(data) {
        this.items = data
      }
    },
    // destroyed() {
    //   // 检查，删除空数据
    //   for(let k in this.items) {
    //     for(let t in this.items[k]) {
    //       if(!this.items[k][t] == '') {
    //         this.items.splice(k, 1)
    //       }
    //     }
    //   }
    //   this.$emit('addCustomMessage', this.items, this.deleteCustomIds)
    // }
  } 

</script>

<style lang="scss" scoped>
  #wrapId {
    display: block;
  }

  .items {
    .item {
      display: flex;
      align-items: center;
      margin-top: 32px;

      .custom-title {
        width: 285px;
        height: 32px;
        line-height: 32px;
        border: 1px solid rgba(0, 0, 0, 0.15);
        border-radius: 4px;
        padding: 0 15px;
        box-sizing: border-box;
        outline: none;
        font-size: 14px;
        color: #606266
      }

      .custom-content {
        width: 515px;
        height: 32px;
        line-height: 32px;
        border: 1px solid rgba(0, 0, 0, 0.15);
        border-radius: 4px;
        outline: none;
        font-size: 14px;
        color: #606266;
        padding: 0 15px;
        box-sizing: border-box;
      }

      i {
        display: inline-block;
        width: 18px;
        height: 18px;
        background: #ddd;
        border-radius: 50%;
        margin-left: 7px;
        color: #fff;
        text-align: center;
        line-height: 18px;
        cursor: pointer
      }
    }
  }

  .add {
    width: 154px;
    height: 34px;
    background: #FFFFFF;
    border: 1px dotted #E8E8E8;
    border-radius: 4px;
    margin-top: 32px;
    margin-left: 300px;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.65);
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;

    span {
      display: inline-block;
      margin-right: 10px;

    }
  }

</style>



