<template>
  <!-- 折叠面板 -->
  <div class="item is-active" :class="{'toShow': isVisible}">
    <div class="item-table">
      <p @click="isShow">{{title1}}</p>
      <p @click="isShow">{{title2}}</p>
      <p @click="isShow">{{title3}}</p>
      <p @click="isShow">{{title4}}</p>
    </div>
    <div class="item-details">
      <slot></slot>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        isVisible: false
      }
    },
    props: {
      title1: {
        type: String,
        default: '邀请加入企业'
      },
      title2: {
        type: String,
        default: '邀请加入企业'
      },
      title3: {
        type: String,
        default: '邀请加入企业'
      },
      title4: {
        type: String,
        default: '邀请加入企业'
      }
    },
    methods: {
      isShow() {
        this.isVisible = !this.isVisible
      }
    }
  }

</script>

<style scoped lang="scss">
  .item {
    height: 31px;
    overflow: hidden;
    transition: all 1s;
    .item-table {
      display: flex;
      position: relative;
      z-index: 999;
    border-bottom: 1px solid #333;
      p {
        width: 300px;
        height: 30px;
        line-height: 30px;
        background-color: red;
        color: #fff
      }
    }
    .item-details {
        div {
            width: 300px;
            height: 60px;
            padding: 20px;
            color: #fff;
            background-color: blue;
        }
    }
  }
    .toShow {
        height: 90px;
        transition: all 1s;
    }

</style>



