<template>
  <div>
    <p>人员{{num1}}：</p>
    <div class="list1" v-for="item in table1" :key="item.id">
      <span>姓名1: {{item.typeVos}}{{item.id}}</span>
      <span>年龄1：{{item.id}}</span>
    </div>
    <button @click="add(1)">添加1</button>
    <p style="margin-top: 30px">企业{{num2}}：</p>
    <div class="list2" v-for="item in table2" :key="item.id">
      <span>姓名2: {{item.typeVos}}{{item.id}}</span>
      <span>年龄2：{{item.id}}</span>
    </div>
    <button @click="add(2)">添加2</button>
    <ap-add-list ref="listAdd" @updataList="updataList"></ap-add-list>
  </div>
</template>

<script>
  import {
    mapGetters
  } from 'vuex'
  import apAddList from './ap_add_list';
  export default {
    data() {
      return {
        num1: null,
        num2: null,
        table1: [],
        table2: []
      }
    },
    components: {
      apAddList
    },
    computed: {
      ...mapGetters({
        'borrowDetails': 'borrowDetails'
      })
    },
    methods: {
      add(flag) {
        let obj = {
          key: flag
        }
        this.$refs.listAdd.init(obj)
      },
      updataList(delArr, noDel, key, allowedData) {
        if (key == 1) {
          this.num1 = '第一类'
          this.table1 = allowedData
        } else if (key == 2) {
          this.num2 = '第二类'
          this.table2 = allowedData
        }
      },
    },
  }

</script>

<style>

</style>



