<template>
  <div class="wrap-roster">
      <span @click="toChoose" class="to-choose" :class="{chooseEd: isChoosed, 'disAbled-current': isDisabledCurrent}">
        {{userName}}
        <i class="iconfont icon-icon-arrow-right2"></i>
      </span>
      <roster ref="initRoster" @checkedVal="checkedVal"></roster>
  </div>
</template>

<script>
import roster from './roster'
export default {
  data() {
    return {
      userName: '请选择',
      userId: [],
      isChoosed: false,
      isDisabledCurrent: false
    }
  },
  components: {
    roster
  },
  props: {
    dataObj: {
      type: Object,
      default() {
        return {
          type: 'company',
          allowMaxNum: 9999999,
          disabledArr: [],
          typeChoose: 'checkBox',
          isDisabledRoster: false,
          isDeleteCurrent: true,
          userNameCopy: ''
        }
      }
    },
    apProjectId: {
      type: Number,
      default: null
    },
    userNameCopy: {
      type: String
    }
  },
  watch: {
    userName(n, o) {
      this.isChoosed = true
    },
    dataObj(n, o) {
      this.userName = n.userNameCopy
    },
    userNameCopy(n, o) {
      this.userName = n
    }
  },
  methods: {
    toChoose() {
      if(!this.dataObj.isDisabledRoster) {
        let data = {
          type: this.dataObj.type,
          allowMaxNum: this.dataObj.allowMaxNum,
          disabledArr: this.dataObj.disabledArr,
          typeChoose: this.dataObj.typeChoose,
          isDeleteCurrent: this.dataObj.isDeleteCurrent,
          morenUserId: this.userId,
          apProjectId: this.apProjectId
        }
        this.$refs.initRoster.apRoster(data)
      }
    },
    checkedVal(res, val) {
      this.userName = res.join(',')
      // 回显默认数据
      this.userId = val
      this.isRosShow = false
      this.$emit('cbCheckedVal',res,  val)
    },
  },
  created() {
    let that = this
    setTimeout(function() {
      if(that.dataObj.isDisabledRoster) {
        that.isDisabledCurrent = true
      } 
    }, 500)
    // setTimeout(function() {
    //   that.userName = that.userNameCopy
    // }, 1000)
  }
}
</script>

<style scoped>
  .to-choose {
  display: block;
  width: 197px;
  height: 40px;
  line-height: 40px;
  text-align: left;
  padding-left: 15px;
  padding-right: 20px;
  border-radius: 4px;
  color: #dcdfe6;
  font-size: 14px;
  border: 1px solid #dcdfe6;
}
.to-choose i {
  float: right;
  color: #dcdfe6!important;
}
.chooseEd {
  color: #606266;
}
.disAbled-current {
  background-color: #f5f7fa;
  color: #999
}
</style>



