<template>
<!-- 文件树 过渡收缩，点击展开，图标，查看 左右联动-->
  <ul v-if="department != null">
    <li v-for="(item,i) in department" :key="i" class="tree-menu">
      <div class="item" :style="indent" @click="getdepart(item)">
        <span>{{item.projectDepartment.projectDepartmentName}}</span>
					<span>({{item.departmentUsers}})人</span>
        <i class="iconfont icon-home1" style="color: #1890FF;font-size: 19px"></i>
      </div>
      
      <!-- <ap-tree :apTreeData="k.nodes" :depth="depth + 1" v-show="k.unfold==false" @dataUpt="dataUpt"></ap-tree> -->
      <ap-tree :department="item.departments" :depth="depth + 1" v-show="item.unfold==true" @getmsg="getmsg"></ap-tree>
    </li>
  </ul>
</template>

<script>
  export default {
    props: ['depth', 'department'],
    name: 'ap-tree',
    data() {
      return {
      }
    },
    computed: {
      indent() {
        //  return { transform: `translate(${this.depth * 30}px)` }
        return {
          padding: '10px',
          'padding-left': `${this.depth * 20}px`
        }
      }
    },
    methods: {
        getdepart(i){		
          i.departments=[{
            departmentUsers: 0,
            projectDepartment: {
              companyId: 5,
              projectDepartmentName: "安全一部122"
            }
          }]
          i.unfold=!i.unfold; 
          this.$forceUpdate();

          // 传出数据				
          this.$emit('getmsg',i);
      },
      // openmyself(i){			
      //   this.getdepart(i);     
      // },
      getmsg(data){
        console.log(res, '传出的数据')
        this.$emit('getmsg',data);
      },
      // opendad(){
      //   if(this.steps!=1){
      //     i.unfold=!i.unfold;
      //     this.$emit('opendad')
      //   }
      // }
    },
    
    created() {
      for (var i in this.department) {
			  this.department[i].unfold=false;
    	}
    }
  }

</script>

<style scoped>
ul {
  list-style: none;
}
  .tree-menu {
    position: relative;
    width: 500px
  }
	
  .tree-menu   .item {
      display: flex;
      justify-content: space-between;

    }
	.tree-menu   .item:hover {
        background-color: #F2F2F2;
        cursor: pointer;
      }

  .isShow {
    display: block !important;
  }

</style>



