<template>
    <div>
      <ap-pdf :menuPdfData='pdf.pdfObj' v-if="pdf.pdfShow" @openpdf="hidePdf"></ap-pdf>
      <p @click="openpdf(obj)">打印查看pdf图片文件</p>
    </div>
</template>

<script>
import apPdf from './index'
export default {
   data() {
     return {
        pdf:{//查看文件
          pdfShow:false,
          pdfObj:null,
          obj: null
        },
     }
   },
   components: {
     apPdf
   },
   methods: {
     //****************打开文件********
			openpdf(obj){//打开文件
				this.pdf.pdfObj=obj[0]
				this.pdf.pdfShow=true
			},
			hidePdf(){//关闭文件
				this.pdf.pdfShow=false
				this.pdf.pdfObj=null
				
			},
   },
   created() {
     this.obj = [
        {
          fileSuffix: "docx",
          // fileSuffix: "png",
          fileId: 11511,
          fileName: "预览.docx"
        }
     ]
   } 
}
</script>

<style>

</style>



