<template>
  <div>
    <!-- <dot></dot> -->
    <span @click="message">消息提示</span>
    <span @click="open">遮罩层</span>
    <fn-bian-c></fn-bian-c>
    <bt></bt>
    <!-- <css-input></css-input> -->
    <ap-mask ref="apMask" width="30%"></ap-mask>
    <table1></table1>
    <ap-rate color="red" v-model="rateVal"></ap-rate>
    <bag></bag>
    <!-- <ex1></ex1>
    <ex2></ex2>
    <ex3></ex3> -->
    <switchc></switchc>
    <div class="demo ap-mobile ap-xs-12 ap-md-12 ap-lg-12"></div>
    <div class="roww">
      <card class="ap-mobile-w690" :ap-sm="4" :ap-md="2" :shadow="'always'">
        <h1 slot="title">一级标题</h1>
        <div slot="main">
            <p>内容内容内容内容内容</p>
            <span>了解详情</span>
        </div>
      </card>
      <card class="ap-mobile-w690" :ap-sm="6" :ap-md="8" :shadow="'always'">
        <h1 slot="title">一级标题</h1>
        <div slot="main">
            <p>内容内容内容内容内容</p>
            <span>了解详情</span>
        </div>>
      </card>
    </div>
    
    <progress2 :percent="90" :time="3"></progress2>
  </div>
</template>

<script>
import dot from '@xp/components/effect/canvas/dot';
import fnBianC from '@xp/testFiles/functionBiancheng';
import apMask from '@xp/pc_components/mask';
import bt from '@xp/components/effect/ap_button';
import cssInput from '@xp/components/effect/css_input';
import table1 from '@xp/pc_components/table';
import apRate from '@xp/components/ap_rate';
import bag from '@xp/components/effect/bag';
import ex1 from '@xp/components/effect/ex1';
import ex2 from '@xp/components/effect/ex2';
import ex3 from '@xp/components/effect/ex3';
import switchc from '@xp/testFiles/switch';
import card from '@xp/pc_components/card';
import progress2 from '@xp/components/ap_progress/index';
  export default {
    data() {
      return {
        rateVal: 0
      }
    },
    components: {
      dot,
      fnBianC,
      bt,
      cssInput,
      apMask,
      table1,
      apRate,
      bag,
      ex1,
      ex2,
      ex3,
      switchc,
      card,
      progress2
    },
    methods: {
      message() {
        this.$apMessage({
          content: '成功',
          type: 'success',
          time: 3000
        })
      },
      open() {
        let data = {
          show: true
        }
        this.$refs.apMask.mask(data)
      }
    }
  }

</script>

<style>

</style>



