<template>
  <div class="mask-wrap" v-if="isShow">
    <div class="mask">
      <h1>{{title}}<i class="el-icon-close" @click="close"></i></h1>
      <p>{{des}}</p>
      <div class="pd30" v-if="state === 1">
        <input v-model="applyFundNum" placeholder="请输入内容">
      </div>
      <div class="bt">
        <button class="tp1" @click="close">{{btnName[0]}}</button>
        <button class="tp2" @click="sure">{{btnName[1]}}</button>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        applyFundNum: 2,
        isShow: true
      }
    },
    props: {
      title: {
        type: String,
        default: '确认发起工程款申请'
      },
      des: {
        type: String,
        default: '您即将发起工程款拨付申请，请确认您已上传相关的申请材料。'
      },
      btnName: {
        type: Array,
        default() {
          return ['继续上传', '发起申请']
        }
      },
      state: {
        type: Number,
        default: 1
      }
    },
    methods: {
      close() {
        this.$emit('close')
        this.isShow = false
      },
      sure() {
        this.$emit('sure', this.applyFundNum)
      },
      apply() {
        let projectId = JSON.parse(localStorage.getItem("pro_id")),
          useData = JSON.parse(localStorage.getItem("useData")),
          token = useData.token
        let params = new URLSearchParams();
        let files = []
        params.append('projectId', projectId)
        params.append('token', token)
        params.append('inventoryId', 11)
        params.append('files', this.filesFormData)
        // this.axios({
        //     method: "POST",
        //     url: '/api/project/projectFund/uploadFilePaymentByPaymentId',
        //     data: params
        //   })
        //   .then(res => {
        //     console.log(res.data)
        //     if (res.data.code == 1) {
        //       console.log(res.data)
        //     }
        //     if (res.data.code == 0) {
        //       alert(res.data.errorMessage)
        //     }
        //   })

        // this.$router.push({
        //   name: 'applyDatum'
        // })
      },
      toSure() {

      }
    }
  }

</script>

<style scoped lang="scss">
  .mask-wrap {
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9999;
    transform: scale(1);
    transition: all 1s;

    .mask {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background: #FFFFFF;
      box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.15);
      border-radius: 4px;

      h1 {
        font-size: 16px;
        color: #1890FF;
        line-height: 22px;
        border-bottom: 1px solid #E8E8E8;
        padding: 15px 30px;
        display: flex;
        justify-content: space-between;

        i {
          color: #000;
          cursor: pointer;
        }
      }

      p {
        padding: 15px 30px;
        color: rgba(0,0,0,0.65);
      }

      .pd30 {
        padding: 0 30px;
        width: 290px
      }

      .bt {
        padding: 30px;
        display: flex;
        justify-content: flex-end;

        button {
          outline: none;
          width: 86px;
          height: 30px;
          background: #FFFFFF;
          border: 1px solid #D9D9D9;
          border-radius: 4px;
          cursor: pointer;
          color: rgba(0, 0, 0, 0.65);
          font-size: 14px;

          &.tp2 {
            background-color: #1890FF;
            color: #fff;
            margin-left: 10px
          }
        }
      }
    }
  }

</style>



